.page {

  &--basic-page {

    main.main-content {
      padding-top: 0;

      @include break($sm-break) {
        padding-top: 0;
      }
    }

    .basic-page__sidebar {

      @include break($sm-md-break) {
        padding-right: 2rem;
        padding-left: 2rem;
        flex-grow: 0;
        flex-shrink: 0;
        flex-basis: 25%;
        max-width: 25%;
      }
    }
  }

  &--basic-with-image-header {

    main.main-content {
      padding-top: 0;
    }
  }
}

.basic-page__sidebar {
  order: 2;

  @include break($sm-md-break) {
    order: 1;
  }
}

.basic-page__content {
  order: 1;

  @include break($sm-md-break) {
    order: 2;
  }
}