.breadcrumb {
  background-color: $gray;
  color: $red;
  font-size: 1.3rem;

  &__list {
    width: auto;
    overflow: scroll;
    padding-top: 20px;
    padding-bottom: 20px;
    white-space:nowrap;
    -ms-overflow-style: none;


    @include break($sm-break) {
      width: 100%;
      overflow: hidden;
    }

    li {
      display: inline-block;
    }
  }

  a {
    display: inline-block;
    color: $black;
    // transition: color 0.4s ease;

    &:hover {
      // color: $red;
      text-decoration: underline;
    }

    &.active {
      text-decoration: underline;
    }
  }

  li + li:before {
    content: '/';
    padding: 0 10px;
  }

  &._red {
    background-color: $red;
    color: $white;

    a {
      color: $white;
    }
  }
}