.its-quick-help {
  position: relative;
  z-index: 2;
  background-color: $white;

  &__contact {
    text-align: left;
    font-size: 1.4rem;
    justify-content:space-around;
    > .row > div {
      padding-bottom: 2rem;
      &:last-of-type {
        padding-bottom: 0;
      }
      @include break($sm-break) {
        padding-bottom: 0;
      }
    }
  }
  .search-results__form {
    margin-bottom: 3rem;
    &-input {
      font-size: 1.6rem;
      margin-bottom: 0.2rem;
      @include break($sm-break) {
        font-size: 2.6rem;
      }
      @include break($md-break) {
        font-size: 3.6rem;
        margin-bottom: 1.2rem;
      }
    }
    button[type=submit] {
      margin-bottom: 0.2rem;
      @include break($md-break) {
        margin-bottom: 1.2rem;
      }
    }
  }
  .form-item {
    margin: 0;
  }
  .its-self-help__list {
    margin: 0;
    padding: 0 2rem;
    &-item {
      padding-bottom: .25rem;
    }
  }
}
.site-home-search {
  .block-swat-hooks {
    // @extend .col-sm-12;
  }
  .its-quick-help {
    padding: 3.5rem 2.5rem;

    @include break($xs-break) {
      padding: 4.5rem 5rem;
    }
    @include break($sm-md-break) {
      margin-bottom: -5rem;
      padding: 5rem 9rem 0rem;
    }
  }
}
.standard-page-search {
  .block-swat-hooks {
    @extend .col-sm-10;
    @extend .col-sm-md-12;
    @extend .col-md-10;
    @extend .col-sm-offset-1;
    @extend .col-sm-md-offset-0;
    @extend .col-md-offset-1;
  }
  .its-quick-help {
    padding-top: 3.5rem;
    padding-bottom: 3.5rem;

    @include break($xs-break) {
      padding-top: 4rem;
      padding-bottom: 4rem;
    }
    @include break($sm-md-break) {
      // margin-bottom: 2rem;
      padding-top: 4rem;
      padding-bottom: 2rem;
    }
    &__contact,
    .search-results__form {
      // @extend .col-sm-12;
    }
  }
}
