.c-stat-lockup {
  $sb: &;

  &__inner,
  &__text {
    will-change: transform;
  }

  &__inner {
    padding: 3.5rem 2.5rem 3.0rem;

    @include break($sm-break) {
      padding: 3rem 4rem;
    }

    @include break($md-break) {
      padding: 4rem 5rem;
    }
  }

  &--red {

    #{$sb}__inner {
      background-color: $red;
      color: $white;
    }

    .u-tiny-label {
      color: $white;
    }
  }

  &--gray {

    #{$sb}__inner {
      background-color: $gray;
    }

    .u-tiny-label {
      color: $black;
    }

    #{$sb}__stat {
      color: $red;
    }

    #{$sb}__line-inner {
      background-color: $black;
    }
  }

  &--quote {

    p {
      font-weight: 600;
      line-height: 1.26;
      position: relative;
      // text-indent: -0.8rem;

      @include break($sm-break) {
        font-size: 2.4rem;
        // text-indent: -1.1rem;
      }

      @include break($md-break) {
        font-size: 3rem;
        // text-indent: -1.5rem;
      }

      // &:before {
      //   content: open-quote;
      //   position: absolute;
      //   top: 0;
      //   left: -0.8rem;

      //   @include break($sm-break) {
      //     left: -1.1rem;
      //   }

      //   @include break($md-break) {
      //     left: -1.5rem;
      //   }
      // }

      // &:after {
      //   content: close-quote;
      // }
    }
  }

  &--small {
    max-width: 25rem;

    @include break($sm-break) {
      max-width: 33rem;
    }

    p {
      line-height: 1.4;

      @include break($sm-break) {
        padding-right: 15%;
      }
    }
  }

  &--large {
    max-width: 55rem;

    .c-stat-lockup__inner {
      padding-top: 4rem;
    }

    @include break($xs-sm-break) {
      padding: 0;
    }
  }

  &__line {
    height: 0.2rem;
    display: block;
    width: 4.6rem;
    margin: 1.5rem 0 2rem;

    @include break($sm-break) {
      height: 0.3rem;
      margin: 1.5rem 0 3rem;
    }

    &-inner {
      display: block;
      background-color: $white;
      width: 100%;
      height: inherit;
    }
  }

  &__stat {
    display: block;
    font-size: 3.6rem;
    font-weight: 600;
    margin-bottom: 0.5rem;

    @include break($sm-break) {
      margin-bottom: 3rem;
      font-size: 4rem;
    }
    // font-size: calc(24px + (40 - 24) * (100vw - 750px) / (800 - 300));
  }
}
