.c-social-icons-list {
  display: flex;
  width: 275px;

  &__icon {
    display: inline-block;
    flex: 1 0 0%;

    svg {
      display: block;
    }

    &--20 {

      & + & {
        margin-left: 2.4rem;
      }

      svg {
        height: 2rem;
        width: auto;
      }
    }
  }

  &--white {

    .c-social-icons-list__icon {
      fill: $white;
    }
  }
}

.c-social-icons-block {

  &__title {
    @extend .u-tag-label;

    margin-bottom: 0.5rem;

    @include break($md-break) {
      margin-bottom: 0.8rem;
    }
  }

  &__list {
    display: flex;
    align-items: center;
    @include list-reset;
  }

  &__icon {
    display: inline-block;
    fill: $red;

    a {
      display: block;
    }

    svg {
      height: 1.3rem;
      // width: auto;
      transition: fill 0.4s ease;
    }

    & + & {
      margin-left: 2.0rem;
    }
  }

  &--red {

    svg {
      fill: $red;
      transition: fill 0.4s ease;
    }

    a:hover svg,
    a:focus svg {
      fill: $black;
    }
  }
}
