.double-callout-group {
  margin: 3rem 0;

  @include break($sm-break) {
    margin: 4rem 0 5rem;
  }

  &__col {
    margin: 0 0 3rem;
    width: 100%;

    &:last-child {
      margin: 0;
    }

    @include break($sm-break) {
      margin: 0 0 4rem;

      &:last-child {
        margin: 0;
      }

      &:nth-child(even) {

        .c-double-callout__image {
          animation-delay: 0.4s !important
        }

        // .c-double-callout__line-inner {
        //   animation-delay: 1.15s !important;
        // }

        // .u-stagger-delay {
        //   @for $i from 1 through 16 {
        //     &:nth-of-type(#{$i}) { 
        //       animation-delay: 0.6s + (0.15s * $i);
        //     }
        //   }
        // }
      }
    }
  }
}