.site-header__main-nav {

  &-inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  a {
    padding: 0.8rem 0;
  }

  a,
  button {
    display: inline-block;
    color: $black;
    fill: $black;
    transition: all 0.5s ease;

    &:hover,
    &:focus {
      cursor: pointer;
      outline: none;
    }
  }

  &-items {
    display: none;

    @include break($sm-md-break) {
      display: flex;
      align-items: center;
      justify-content: flex-end;
    }

    > li {
      position: relative;
    }

    & > li > a {
      border-bottom: 3px solid transparent;
      font-weight: 500;

      &:hover,
      &:focus,
      &.active-parent,
      &.active-trail {
        border-color: $black;
        outline: none;
      }

      &:focus {
        border-color: $red;
      }
    }

    & > li + li {

      @include break($sm-break) {
        margin-left: 1.5rem;
      }

      @include break($md-break) {
        margin-left: 3rem;
      }
    }
  }

  &-toggle-items {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }


  .submenu {
    position: absolute;
    top: 100%;
    left: 0;
    //borrow the width from the container-fluid
    width: $container-max-width;
    text-align: left;
    transform: translate3d(0, -80%, 0);
    transition: transform 0.5s ease;
    border-top: 1px solid $gray;
    padding: 2.5rem 0;
    display: none;
    z-index: -1;

    > .container-fluid {
      padding: 0;
      margin: 0;
    }
    &.show-menu {
      display: block !important;
    }

    &.animates-menu-in {
      transform: translate3d(0, 0, 0);
    }

    &__inner {
      margin: 0 auto;
      padding-left: 0;
      // overflow: hidden;
      position: relative;
    }

    &__links {
      @include list-reset;
      line-height: 1.3125;
      float: left;
      padding-right: 3rem;
      position: relative;

      a {
        font-size: 1.6rem;
        padding: 0 0 0.5rem;
        transition: all 0.9s ease, transform 0s ease, opacity 0s ease;
        // Setting the transition-duration solved a choppy animation in Safari
        transition-duration: 0.9s, 0s, 0s;
        position: relative;
        // overflow: hidden;
        color: $black;

        .js & {
          opacity: 0;
          transform: translate3d(2rem, 0, 0);

          &.animate-in {
            opacity: 1;
            transform: translate3d(0%, 0, 0);
            transition: all 0.9s ease, transform 0.5s ease, opacity 0.9s ease;
            // Setting the transition-duration solved a choppy animation in Safari
            transition-duration: 0.9s, 0.5s, 0.9s;
          }
        }

        span {
          display: inline-block;
          transition: transform 1s ease;
        }

        &.current,
        &.active,
        &:hover,
        &:focus {
          color: $red;
          outline: none;

          span {
            transform: translate3d(3.8rem, 0, 0);
          }

          &:before {
            transform: translate3d(0, 0, 0);
          }
        }

        &:focus {
          text-decoration: underline;
        }

        &:before {
          width: 2.8rem;
          height: 0.2rem;
          content: '';
          background: $red;
          display: block;
          margin-right: 0;
          transition: transform 1s ease;
          position: absolute;
          left: 0;
          top: 1.1rem;
          transform: translateX(-100%) translateX(-1.2rem);
          z-index: 1;
        }

        &:after {
          width: 4rem;
          height: 0.4rem;
          content: '';
          background: $white;
          display: block;
          margin-right: 0;
          position: absolute;
          left: 0;
          top: 1rem;
          transform: translate3d(-100%, 0, 0);
          z-index: 2;
        }
      }

      li {
        display: block !important;

        @include break($sm-md-break) {
          // padding-left: 4rem;
        }
      }

      &--two-column {
        @include break($sm-md-break) {
          columns: 2;
          column-gap: 3rem;
        }

        li {
          @include break($sm-md-break) {
            width: 180px;
          }

          @include break($md-break) {
            width: 250px;
          }
        }
      }
    }
  }

  .site-header__toggle {
    padding: 0.8rem;
    fill: $black;
    stroke: $black;
    width: 3.6rem;
    height: 3.6rem;

    @include break($sm-md-break) {
      width: 4.4rem;
      height: 4.1rem;
    }

    @include break($md-break) {
      padding: 0;
    }

    svg {

      @include break($sm-md-break) {
        margin-left: auto;
        margin-right: auto;
      }
    }

    g {
      stroke: inherit;
    }

    &:hover {

      svg {
        fill: $red;
        stroke: $red;
      }

      .site-header__toggle-outer:before,
      .site-header__toggle-outer:after,
      .site-header__toggle-inner {
        background-color: $red;
      }

      @include break($sm-md-break) {
        .site-header__toggle-inner {
          background-color: $white;
        }
      }
    }

    &.more {
      transition: all 0.5s ease;
      color: $black;
      position: relative;

      &:hover * {
        color: $red;
      }

      span {
        display: block;
        width: 1.8rem;
        transition: inherit;
        margin-left: auto;
        margin-right: auto;

        @include break($sm-md-break) {
          width: 2.8rem;
        }
      }

      &.modal--open-modal {
        @include break($sm-md-break-max) {
          .site-header__toggle-outer:before {
            transform: rotate(45deg);
            top: 6px;
          }
          .site-header__toggle-outer:after {
            transform: rotate(-45deg);
            top: 6px;
          }
          .site-header__toggle-inner {
            opacity: 0;
          }
        }
      }
    }

    &-outer {
      height: 14px;
      width: 100%;
      position: relative;

      @include break($sm-md-break) {
        height: 25px;
      }

      &:before,
      &:after {
        content: '';
        display: block;
        width: 100%;
        height: 2px;
        background-color: $black;
        position: absolute;
        left: 0;
        border-radius: 1px;
        transition: inherit;

        @include break($sm-md-break-max) {
          transition: all 0.5s ease;
        }

        @include break($sm-md-break) {
          height: 3px;
          border-radius: 1.5px;
        }
      }

      &:before {
        top: 0;
      }

      &:after {
        bottom: 0;
      }
    }

    &-inner {
      position: absolute;
      top: 50%;
      left: 50%;
      width: 100%;
      height: 2px;
      text-indent: 9999rem;
      background-color: $black;
      margin-top: -0.1rem;
      border-radius: 1px;
      transition: all 0.5s ease;
      transform: translate(-50%, 0);
      overflow: hidden;

      @include break($sm-md-break) {
        border-radius: 0;
        margin-top: 0.5px;
        height: auto;
        background-color: transparent;
        transform: translate(-50%, -50%);
        text-transform: uppercase;
        text-indent: 0;
        font-size: 0.9rem;
        font-weight: 600;
        letter-spacing: 0.05rem;
        text-align: center;
      }
    }

    &.search {

      span {
        display: block;
      }

      svg {
        width: 2rem;
        height: 2rem;
        display: block;

        @include break($sm-md-break) {
          width: 2.5rem;
          height: 2.6rem;
        }
      }
    }

    &.search,
    &.options {

      &.modal--open-modal,
      &.modal--options-open {

        @include break($sm-md-break-max) {

          svg {
            opacity: 0;
          }

          span {
            position: relative;
            height: 14px;
            transition: 0.3s ease-in-out;
            &:before, &:after {
              content: '';
              display: block;
              height: 2px;
              // background-color: $white;
              background-color: $black;
              position: absolute;
              left: 0;
              border-radius: 1px;
              width: 100%;
              top: 6px;
              transition: all 0.5s .4s ease;

            }

            &:before {
              transform: rotate(45deg);
            }

            &:after {
              transform: rotate(-45deg);
            }

            &:hover {
              &:before, &:after {
                background-color: $red;
              }
            }
          }
        }

      }
    }

    &.options {

      &.modal--options-open {
        @include break($sm-md-break-max) {
          span:before, span:after {
            background-color: $black;
          }
        }
      }

      svg {
        height: 2rem;
        width: 2rem;
        stroke: none;
        transition: fill 0.5s ease;
        margin-left: auto;
        margin-right: auto;

        @include break($sm-md-break) {
          height: 2.3rem;
          width: 2.3rem;
        }

        @include break($md-break) {
          height: 2.5rem;
          width: 2.5rem;
        }
      }

      span {
        display: flex;
        align-items: center;

        @include break($sm-md-break) {
          display: block;
        }
      }
    }
  }
}

li.submenu__content {
  position: relative;
  margin-top: 2rem;
  padding-right: 2rem;

  @include break($sm-md-break) {
    padding-right: 0;
    margin-top: 0;
    position: absolute;
    left: 0;
    top: 0;
    transform: translate(-115%, 0);
    width: auto !important;
  }

  &.guide-block {
    a {
      display: block;
      overflow: hidden;
      padding: 0 !important;
      position: relative;

      img {
        transition: transform 1.5s ease;
        display: block;
        backface-visibility: unset;
        width: 100%;

        @include break($sm-md-break) {
          width: auto;
          max-width: 21rem;
        }

        @include break($md-break) {
          max-width: 247px;
        }
      }

      &:before {
        content: none !important;
      }

      &.guide-block__link {
        &:after {
          top: 0 !important;
          right: 0 !important;
          bottom: 0 !important;
          background: linear-gradient(to bottom, rgba(0,0,0,0) 0%, rgba(0,0,0,0) 37%, rgba(0,0,0,0.54) 73%, rgba(0,0,0,0.54) 100%) !important;
          z-index: 1 !important;
          width: 100% !important;
          height: 100% !important;
          transform: none !important;
        }
      }

      &:hover {

        span {
          transform: none !important;
        }

        @include break($sm-md-break) {
          // padding-left: 0 !important;
          transform: translate3d(0, 0, 0);

          img {
            transform: scale(1.1);
          }
        }
      }
    }
  }

  span {
    position: absolute;
    bottom: 2rem;
    left: 4rem;
    color: $white;
    font-weight: 600;
    z-index: 2;
    display: block;

    @include break($sm-md-break) {
      left: 2.5rem;
    }
  }
}


.site-header__instructions {
  position: absolute;
  top: 5.6rem;
  left: 0;
  max-width: 320px;
  background-color: $white;
  color: $black;
  padding: 2rem;
  border-right: 2px solid $red;
  border-bottom: 2px solid $red;
  border-top: 2px solid $red;

  @include break($sm-md-break) {
    top: 6.3rem;
  }

  @include break($md-break) {
    top: 7.6rem;
  }
}
