.double-callout-image {
  ul {
    padding: 0;
    list-style: none;
  }

  background-color: $gray;
  position: relative;
  padding-bottom: 3.5rem;
  margin-top: 3rem;
  margin-bottom: 5rem;

  @include break($sm-break) {
    margin-top: 4rem;
    margin-bottom: 6rem;
  }

  &:before {
    content: '';
    display: block;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    height: 12.0rem;
    background-color: $white;
  }

  .container-fluid {
    position: relative;
    z-index: 2;
  }

  &__link {
    position: relative;
    display: flex;
    flex-direction: column;
    overflow: hidden;

    @include break($xs-break) {
      display: block;
    }

    img {
      width: 100%;
    }

    &:hover {

      .double-callout-image__content {
        // transform: translate3d(1.5rem, 0, 0);
        left: 3.5rem;
      }
      .double-callout-image__meta {
        @include break($xs-break) {
          left: 3.5rem;
        }
      }

      .double-callout-image__content {
        top: 3.5rem;

        @include break($md-break) {
          top: 6rem;
        }

      }

      .double-callout-image__shader {
        opacity: 0.6;
        transition: all $trans-hover-time-img-slow ease;
      }
    }
  }

  &__shader,
  img {
    transition: all $trans-hover-time-img-slow-out ease;
  }

  &__shader {
    background: $img-bkg-gradient;
  }

  &__content {
    position: absolute;
    color: $white;
  }
  &__meta {
    order: 2;
    color: $black;
    font-size: 1.2rem;
    padding: 1rem 3rem 0;

    @include break($xs-break) {
      position: absolute;
      color: $white;
      padding: 0;
    }
  }
  &__content,
  &__meta {
    z-index: 2;
    left: 3.0rem;
    transition: $trans-clip-hover;
  }

  &__content {
    // top: 2.4rem;
    top: 3rem;
    max-width: 29.0rem;
    display: flex;
    flex-direction: column-reverse;
    animation-delay: 0.4s !important;

    @include break($md-break) {
      top: 6.0rem;
    }

    .u-tag-label {
      margin-bottom: 1.0rem;

      @include break($md-break) {
        margin-bottom: 1.7rem;
      }
    }
  }

  &__meta {
    width: calc(100% - 7rem);
    bottom: 3.0rem;
    animation-delay: 0.8s !important;

    p {
      font-size: 1.4rem;
      line-height: 1.4;

      @include break($sm-break) {
        font-size: 1.2rem;
      }

      @include break($md-break) {
        font-size: 1.7rem;
      }
    }
  }

  &__image-wrapper {
    position: relative;
  }

  &__col--right {
    margin-top: 3.0rem;

    @include break($sm-break) {
      animation-delay: 0.4s !important;
    }

    .double-callout-image__content {

      @include break($sm-break) {
        animation-delay: 1.2s !important;
      }
    }

    .double-callout-image__meta {

      @include break($sm-break) {
        animation-delay: 1.6s !important;
      }
    }
  }

}
