$themeMap: (
  green: ($green-light, $green-dark),
  blue: ($blue-light, $blue-dark),
  orange: ($orange-light, $orange-dark),
  red: ($red-light, $red),
  blueberry: ($blueberry-light, $blueberry-dark),
  turquoise: ($turquoise-light, $turquoise-dark),
  pink: ($pink-light, $pink-dark),
  charcoal: ($charcoal-light, $charcoal-dark),
  garnet: ($white, $red)
);

// Defaults
.u-color-theme {

  // BACKGROUNDS
  &--bkg {

    &-dark {
      background-color: $gray-dark;
    }

    &-light {
      background-color: $gray;
    }

    &-default {
      background-color: $red;
    }

    &-white {
      background-color: $gray;
    }
  }

  // TEXT
  &--text {

    &-white {
      color: $white;

      a {
        color: $white;
      }
    }

    &-dark {
      color: $red;
    }
  }

  // LINES
  &--line-after {

    &-dark:after {
      background-color: $red;
    }
  }

  &--fill {

    &-dark {
      fill: $red;
    }

    &-default {
      fill: $gray-dark;
    }
  }

  &--cta {

    &-dark {
      fill: $red;
    }

    svg,
    svg g {
      fill: $red;
    }
  }

}

/* For each key in the map, created an own class */
@each $class, $color in $themeMap {
  $color-light: nth($color, 1);
  $color-dark: nth($color, 2);

  .u-color-theme--#{$class} {
    background-color: $color-light;

    .u-color-theme {

      // BACKGROUNDS
      &--bkg {

        &-dark {
          background-color: $color-dark;
        }

        &-light {
          background-color: $color-light;
        }

        &-white {
          background-color: $white;
        }
      }

      // TEXT
      &--text {

        &-white {
          color: $white;

          a {
            color: $white;
          }
        }

        &-dark {
          color: $color-dark;
        }
      }

      // LINES
      &--line-after {

        &-dark:after {
          background-color: $color-dark;
        }
      }

      // FILL
      &--fill {

        &-dark {
          fill: $color-dark;
        }

        &-default {
          fill: $color-dark;
        }
      }

      &--cta {
        color: $color-dark;

        svg,
        svg g {
          fill: $color-dark;
        }
      }
    }

    // Sub Nav
    .c-sub-nav {
      a:hover {
        color: $color-dark;
      }

      &__header,
      &__button {
        border-top-color: $color-dark;

        .c-sub-nav__social a:hover svg {
          fill: $color-dark;
        }
      }

      &__header.c-sub-nav__active {

        a {
          color: $color-dark;
        }
      }

      &__active > a {
        color: $color-dark;

        &:before {
          background-color: $color-dark;
        }
      }

      &__list-item--subitem {

        a:hover {
          &:before {
            background-color: $color-dark;
          }
        }
      }

      &__social a:hover svg {
        fill: $color-dark;
      }
    }

    .c-dept-info__social svg {
      fill: $color-dark
    }

    // Entry List
    .entry-list__entry-title a:hover,
    .entry-list--news .entry-list__entry-title:hover,
    .c-news-archive-list__entry-title:hover
     {
      color: $color-dark;
    }

    // Basic Page
    &.page--basic-page {
      background-color: $white;
    }

    .basic-content {

      p a {
        color: $color-dark;
      }
      .u-btn,
      p a.u-btn {
        background-color: $color-dark;
        color: $white;
      }

      ul li:before,
      ol li:before,
      p.pullquote {
        color: $color-dark;
      }

      .c-stat-block__chart-part {
        stroke: $color-dark;
      }

      .c-stat-block__line-inner {
        background-color: $color-dark;
      }
    }

    .c-download-button {
      color: $color-dark;

      svg { fill: $color-dark; }

      &:hover {
        background-color: $color-dark;
        color: $white;

        svg { fill: $white; }
      }
    }

    .u-cta,
    .u-color-theme--cta {
      color: $color-dark;

      svg g {
        fill: $color-dark;
      }
    }

    table thead {
      border-color: $color-dark;
    }

    table.table--zebra thead {
      background-color: $color-dark;
    }
    
    // Ambient Video 
    .video-header__small-cta {
      background-color: $color-dark;

      @include break($xs-break) { 
        background: transparent;
      }
    }

    // Accordions and Audio Players
    .c-accordion__item--open .c-accordion__button,
    .c-accordion__button:hover,
    .c-audio-player__transcript-link {
      color: $color-dark;
      fill: $color-dark;
    }

    .c-audio-player--large .c-audio-player__button svg g,
    .c-audio-player--large .c-audio-player__controls .c-audio-player__keyboard-controls svg g {
      stroke: $color-dark !important;
    }

    .c-audio-player--large .c-audio-player__button {
      color: $color-dark;
    }

    // Instagram
    .ig-lockup__social-links a {

      svg { fill: $color-dark; }

      &:hover svg { fill: $black; }
    }

    .breadcrumb--default li+li:before {
      // sets color for breadcrumb separators
      color: $color-light;
    }

    .c-audio-player__transcript-link {
      color: $color-dark;
    }

    .c-pagination__button:hover,
    .c-pagination__number:hover,
    .c-pagination__active {
      color: $color-dark;
    }

  }
}

// Necessary because of the way the basic-page-header, dept-header-, and  u-color-theme--garnet was implemented.
// TODO: refactor the theme classes so that this practice isn't necessary
.u-color-theme--garnet {
    .basic-page-header,
    .dept-header-single,
    .dept-header-double,
    .u-color-theme--bkg-white
    {
      @include break($sm-break) {
        background-color: $gray;
      }
    }
    // TODO: consider refactoring any --red style to --dark so that things like this override aren't necessary.
    .image-callout__block--red .u-cta {
      color: $white;

      svg g {
        fill: $white;
      }
    }
}

// Garnet, Green, and Black theme used on Celebrating Black Excellence site
.u-color-theme--ggb {
  @extend .u-color-theme--green;

  // Overrides to allow third theme color
  .u-color-theme--text-dark,
  .u-color-theme--cta svg, 
  .c-double-callout__line-inner,
  .basic-content p a,
  .u-color-theme--cta,
  .u-cta,
  .c-dept-info a
  {
    color:#990033;
  }
  .panel-pane:not(.dept-overview__sidebar):not(.basic-page__sidebar):not(.quick-links-h):not(.full-width-callout):not(.video-header):not(.ig-lockup):not(.entry-list--events):not(.full-width-events) {
    a {
      color:#990033;
    }
  }
  .image-callout__block--red,
  .quick-links-h__line,
  .quick-links-h__link .c-arrow-circle,
  .quick-links-h__link:hover,
  .c-title-cta__line,
  .c-arrow-circle,
  .feature-stories__title-line-inner
  {
    background-color:#990033;
  }
  // Site Footer
  .site-footer {
    background-color: #000000;
    color: #ffffff;
    a {
      color:#ffffff;
      &:hover {
        color: $green-light;
      }
    }
    &__social-links a svg{
      fill: #ffffff;
      &:hover {
        fill: $green-light;
      }
    }
    // Fill for footer logo
    .fill-red-swat {
      fill: #990033;
    }
    .alert {
      display:none;
    }
  }
  // Black Excellence Logo
  #black-excellence-logo{
    margin-bottom: 75px;
    &.full-width-callout:before,
    &-wide.full-width-callout:before {
      background: none;
    }
  }
  // Sidebar Nav
  .c-sub-nav {
    &__header > a {
      color: $green-dark;
    }
    &__list-item > a {
      color: $black;
    }
  }
  // Quicklinks (horizontal)
  .quick-links-h {
    &__link {
      &:hover {
        color:#ffffff;
        .c-arrow-circle {
          background-color: #ffffff;
          .stroke-white {
            stroke: #990033;
          }
        }
      }
    }
    &__list-item {
      @include break($md-break) {
        border-right: none;
      }
    }
  }

  // Ambient Video
  .video-header__small-cta {
    background-color:#990033;
    
    @include break($xs-break) { 
      background: transparent;
    }
  }

  // Instagram
  .ig-lockup__social-links {
    .c-social-icons-block__title {
      color: $white;
    }
    a {
      svg { fill: $white; }
    }
  }
  // Three column callout
  .three-col-callout__title {
    color: $white;
  }
  // Calendar Feeds (entry list)
  .entry-list__entry-content {
    a { text-decoration: none; }
  }
  // Image Gallery
  .c-image-slider__meta-text {
    color: $black;
  }
  // Utils
  .u-tiny-label{
    color: $white;
    font-size: 1.6rem;
    text-transform: none;
    letter-spacing: 0rem;
  }
  .u-color-theme--bkg-white {
      background-color: #3d7637;
  }
  .u-color-theme--cta,
  .u-cta {
     svg g {
      fill:#990033 !important;
    }
  }
}
