html.keyboard-user {

  a:focus,
  button:focus,
  input[type="submit"]:focus {
    background-color: $red-light;
    outline: 2px dashed $red;
    border: 0;
    color: $red;
    transition: none;
    transition-delay: 0;
    clip-path: none;
  }

  // Academics: Design Your Own Major Button
  .academic-depts__other a:focus.academic-depts__other-button {
    svg g {
      fill: $red;
    }
    &:hover {
      color: $red;
    }
  }

  // Modals
  .flyout .c-social-icons-list a:focus {
    outline-color: $white;
    padding-top: 4px;
    padding-bottom: 4px;
    margin-top: 2px;
    background-color: transparent;

    svg {
      margin: 0 auto;
    }
  }

  .flyout--more__nav-pages a:focus {
    outline-color: $white;
    background-color: transparent;
    padding-left: 4px;
    padding-right: 4px;
  }

  .flyout--more__button:focus,
  .guide-carosel__guide a:focus {
    outline-color: $white;
    color: $red;
  }
  .flyout--more__button:focus {
    margin-top: 2px;
  }

  .flyout .flyout__close:focus,
  .flyout--search-button:focus {
    background-color: $white;
    outline-color: $white;

    @include break($sm-md-break-max) {
      right: 0;
    }

    @include break($sm-md-break) {
      background-color: transparent;
    }
  }

  .guide-carosel__button:focus {
    svg g {
      stroke: $red;
    }
  }
  #js--site-options-close-first,
  .site-options__close:focus {
    left: .2rem;
  }

  .flyout__main-nav-trigger:focus {
    fill: $red;
  }

  // Double Image Callout
  .double-callout-image__shader {
    transition: none;
  }

  .c-title-cta__title {

    &:focus {
      color: $red;

      span {
        transition: none;
      }

      .stroke-white {
        stroke: $red;
      }
    }
  }

  // Surface Control
  .u-player-control:focus {
    padding: 1rem 1rem 0.5rem;
    background-color: $white;

    .u-player-control__circle-outline {
      stroke: $red;
    }
    .u-player-control__circle {
      fill: $red;
      opacity: 1;
    }
    .u-player-control__pause {
      fill: $white;
    }
  }

  // fixed items
  .double-callout-image__link:focus .double-callout-image__shader
  .c-video-embed__play:focus .c-video-embed__poster {
    top: 1rem;
    left: 1rem;
    bottom: 1rem;
    right: 1rem;
  }

  .double-callout-image__link:focus {
    padding: 1rem;
  }

  .c-video-embed__play:focus {

    .c-play-icon__circle {
      fill: $white;
    }

    .c-play-icon__triangle {
      fill: $red;
    }
  }

  .ig-lockup__entry-link:focus {
    outline: none;
    border: 2px dashed $red;

    @include break($sm-break) {
      color: $red;
    }

    @include break($sm-md-break) {
      color: $white;
    }
  }

  // Ambient Video Header
  .video-header.animates-cta .c-title-cta__btn:focus {
    @extend button;
  }

  // Primary Nav > Submenu > Guide Blocks
  .submenu__content.guide-block a.guide-block__link:focus {
    @extend button;
  }

}
