p {

  //
  // TK - make decision here....full line height margin or valued amount
  //
  & + & {
    // margin-top: 2.4rem;

    &:before {
      content: '\A';
      white-space:pre;
    }
  }
}

strong, b {
  font-weight: 600;
}

i, em {
  font-style: italic;
}

a {
  color: $red;
  text-decoration: none;
  transition: all 0.4s ease;
  // &:visited {
  //   color: $red;
  // }
  p & {
    text-decoration: underline;
  }
}

button, input[type="submit"] {
  background: transparent;
  border-radius: 0;
  border: 0;
  display: inline-block;
  font-family: $sans;
  font-size: inherit;
  outline: 0;
  margin: 0;
  -webkit-appearance: none;

  &:hover {
    cursor: pointer;
  }
}

a,
button {

  html:not(.keyboard-user) &:focus {
    outline: none;
  }
}
