.site-options {
  position: fixed;
  top: 5.6rem;
  left: 0;
  width: 100%;
  background-color: $white;
  z-index: -1;
  border-top: 1px solid $gray;
  display: none;
  transform: translate3d(0,-100%,0);
  transition: 0.4s ease;
  padding: 2rem 0;
  opacity: 1;

  @include break($sm-md-break) {
    padding: 2.4rem 0;
    top: 6.3rem;
  }

  @include break($md-break) {
    top: 7.6rem;
  }

  &.show-options {
    display: block;
  }

  &.animates-options {
    opacity: 1;
    transform: translate3d(0,0,0);
  }

  @include break($sm-break) {
    padding: 2.4rem 0;
  }


  &__content {
    padding-right: 1rem;

    @include break($sm-md-break) {
      padding-right: 0.8rem;
    }
  }

  &__label,
  &__choices {
    display: inline;
  }

  &__option {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    transform: translate3d(2rem, 0, 0);
    transition: 0.4s ease-in-out 0.1s;
    opacity: 0;

    @for $i from 2 through 5 {
      &:nth-of-type(#{$i}) {
        transition-delay: (0.15s * $i);
      }
    }

    .animates-options & {
      opacity: 1;
      transform: translate3d(0,0,0);
    }
  }

  &__option-label {
    font-size: 1.2rem;
    font-weight: 600;
    padding-right: 1.5rem;
    display: inline-block;
    margin-right: 2rem;

    @include break($sm-md-break) {
      font-size: 1.5rem;
      padding-right: 0;
      margin-right: 2.5rem;
    }
  }

  &__toggle {
    font-weight: 600;
    text-transform: uppercase;
    height: 3rem;
    width: 3rem;
    display: inline-block;
    padding: 0;
    font-size: 1.2rem;

    @include break($sm-md-break) {
      font-size: 1.6rem;
    }

    &-inner {
      display: block;
      position: relative;
      width: 2.1rem;
      height: 3rem;
      margin: 0 auto;
      overflow: hidden;

      @include break($sm-md-break) {
        width: 2.8rem;
      }
    }

    &-on,
    &-off {
      display: inline-block;
      position: absolute;
      top: 50%;
      left: 0;
      transition: 0.3s ease-in-out;

      &:after {
        content: '';
        display: block;
        height: 2px;
        background-color: $black;
        width: 100%;
      }
    }

    &-on {
      color: $red;
      transform: translate3d(-100%, -50%, 0);

      &:after {
        background-color: $red;
      }

      .is-on & {
        transform: translate3d(0,-50%,0);
      }
    }

    &-off {
      transform: translate3d(100%, -50%, 0);

      .is-off & {
        transform: translate3d(0,-50%,0);
      }
    }

  }

