  &__container {
    position: relative;
  }

  .modal--close-options {

    @include break($sm-md-break) {
      background-color: $white;
      display: block;
      position: absolute;
      top: -6.5em;
      right: 5.6rem;
      z-index: 9999;
    }

    @include break($md-break) {
      top: -7.2em;
      right: 5.6rem;
    }

    &:before,
    &:after {
      background-color: $black;
    }
  }
}

button.site-options__close {
  position: absolute;
  top: -2.4rem;
  left: -9999px;
  background-color: $red;
  color: $white;
  padding: 1rem 1rem 1.2rem;
  transition: none;
}
