.site-footer {
  background-color: $gray;
  padding: 6.0rem 0;
  position: relative;

  &__info {
    @include break($sm-break) {
      padding-right: 1rem;
    }
    @include break($md-break) {
      padding-right: 2rem;
    }
  }

  &__desktop-info {
    display: none;

    @include break($sm-break) {
      display: block;
    }
  }

  &__mobile-info {
    margin-top: 1.6rem;

    @include break($sm-break) {
      display: none;
    }
  }

  &__logo {
    display: block;
    max-width: 21.0rem;
    margin-bottom: 1.6rem;

    @include break($sm-break) {
      margin-bottom: 3.8rem;
    }
  }

  &__address {
    line-height: 1.5;
    margin-bottom: 1.6rem;

    @include break($sm-break) {
      margin-bottom: 2.0rem;
    }

    a {
      color: $black;
      text-decoration: none;

      &:hover,
      &:focus {
        color: $red;
      }
    }
  }

  &__social-links {
    margin-bottom: 1rem;

    @include break($sm-break) {
      margin-bottom: 1.6rem;
    }
    ul {
      @include list-reset;
    }

    li {
      display: inline-block;

      @include break($md-break) {
        margin-right: 0.5rem;
      }

      &:first-child a {
        padding-left: 0;
      }
    }

    @include break($sm-break) {
      margin-bottom: 2.5rem;
    }

    a {
      color: $black;
      display: inline-block;
      padding: 0.8rem;

      &:hover,
      &:focus {
        color: $red;

        svg {
          fill: $red;
        }
      }
    }

    svg {
      // width: auto;
      height: 12px;
      transition: fill 0.4s ease;

      @include break($md-break) {
        height: 16px;
      }
    }
  }

  &__helpful-links {
    @include list-reset;
  }

  &__accessibility,
  &__helpful-links,
  &__copyright {
    font-size: 1.1rem;
    line-height: 1.54;
  }

  &__accessibility {
    margin-bottom: 1.6rem;
  }

  &__helpful-links {

    @include break($sm-break) {
      margin-bottom: 3rem;
    }

    a {
      color: $black;

      &:hover {
        color: $red;
        transform: translate3d(5px,0,0);

        .fill-black {
          fill: $red;
        }

        span {
          transform: translate3d(5px,0,0);
        }
      }
    }

    span {
      padding-left: 0.3rem;
    }

    svg {
      height: 0.7rem;
      // width: auto;
    }

    a, span {
      transition: 0.4s ease;
      display: inline-block;
    }

  }

  &__links {
    ul {
      @include list-reset;
    }

    @include break($sm-break) {
      margin-top: 3.0rem;
    }

    @include break($md-break) {
      padding-top: 0.9rem;
    }

    li {
      border-top: 1px solid $gray-dark;

      &:last-child {
        border-bottom: 1px solid $gray-dark;
      }
    }

    a {
      padding: 1.0rem 0;
      display: block;
      font-size: 1.0rem;
      color: $black;

      @include break($sm-break) {
        font-size: 1.4rem;
        padding: 1.5rem 0;
      }

      &:hover,
      &:focus {
        color: $red;
        transform: translate3d(5px, 0, 0);
      }
    }

    &--left {
      @include break($sm-break-max) {
        padding-left: 0;
      }
    }

    &--right {
      @include break($sm-break-max) {
        padding-right: 0;
      }
    }
  }

  &__copyright {
    margin-top: 1.6rem;

    @include break($sm-break) {
      margin-top: 0;
      padding-left: 2rem;
    }
  }
}
