.dept-header-double {
  margin-top: 2.8rem;
  margin-bottom: 2.8rem;

  // TODO: Refactor Garnet theme so we can get rid of this.
  .u-color-theme--garnet & {
    margin-top: 0;
    margin-bottom: 0;
    padding-top: 2.8rem;
    padding-bottom: 2.8rem;
  }

  @include break($sm-break) {
    margin-top: 4rem;
    margin-bottom: 4rem;
    position: relative;
    z-index: 1;

    // TODO: Refactor Garnet theme so we can get rid of this.
    .u-color-theme--garnet & {
      margin-top: 0;
      margin-bottom: 0;
      padding-top: 4rem;
      padding-bottom: 4rem;
    }

  }

  @include break($md-break) {
    margin-bottom: -5rem;

    // TODO: Refactor Garnet theme so we can get rid of this.
    .u-color-theme--garnet & {
      padding-bottom: 0;
    }
  }


  &.dept-header-double--left .row {
    flex-direction: column-reverse;

    @include break($sm-break) {
      flex-direction: row-reverse;
    }
  }
  &.dept-header-double--right .row {
    flex-direction: column-reverse;

    @include break($sm-break) {
      flex-direction: row;
    }
  }

  &__content {
    position: relative;
    padding-left: 5rem;

    @include break($sm-break) {
      padding-left: 2.5rem;
    }

    @include break($md-break) {
      padding-left: 5rem;
      padding-right: 2.5rem;
    }
  }

  &__line {
    height: 0.2rem;
    width: 4rem;
    position: absolute;
    left: 0;
    top: 1.6rem;
    overflow: hidden;
    z-index: 1;

    @include break($sm-break) {
      width: 16.5rem;
      left: -16.5rem;
      top: 1.8rem;
    }

    @include break($md-break) {
      width: 21.5rem;
      top: 2.4rem;
      left: -18rem;
    }

    &-inner {
      display: block;
      background-color: $red;
      height: inherit;
      width: 100%;

      @include break($sm-break) {
        float: right;
      }
    }
  }

  &__images {
    overflow: hidden;
    display: flex;
    align-items: flex-start;
    margin-bottom: 1.6rem;

    @include break($sm-break) {
      margin-bottom: 0;
    }

    @include break($md-break) {
      // TODO: Refactor Garnet theme so we can get rid of this.
      .u-color-theme--garnet & {
        margin-bottom: -3rem;
      }
    }

    img {

      &:first-child {
        width: 70%;

        @include break($sm-break) {
          width: 50%;
        }
      }

      &:last-child {
        margin-left: -40%;
        margin-top: 6.5rem;
        width: 70%;

        @include break($sm-break) {
          margin-left: -10%;
          width: 60%;
          margin-top: 3rem;
        }

        @include break($md-break) {
          margin-top: 6rem;
        }
      }
    }
  }
}
