.guide {
  position: relative;

  p {
    line-height: 1.6;
  }
  .row {
    margin-bottom: 2.5rem;

    @include break($sm-md-break) {
      margin-bottom: 5rem;
    }
  }

  &:before,
  &:after {

    @include break($sm-break) {
      content: '';
      position: fixed;
      width: 100%;
      left: 0;
      background-color: $white;
      height: 2.5rem;
      z-index: 3;
    }

    @include break($sm-md-break) {
      height: 5rem;
    }
  }

  &:before {
    @include break($sm-break) {
      top: 0;
    }
  }

  &:after {
    @include break($sm-break) {
      bottom: 0;
    }
  }

  &__bkg {
    background-color: $guide-gray;
    width: 100%;

    @include break($sm-md-break) {
      margin: 0 2.5rem;
    }
  }

  &__content-wrapper {
    padding: 0 2.5rem;

    @include break($sm-break) {
      padding: 0 5rem;
    }

    @include break($sm-md-break) {
      padding: 0;
      // width: 58%;
      // width: calc(62.5% - 8rem);
      margin-left: 37.5%;
      // width: 100%;
      max-width: 100%;
    }
  }

  &-header {
    z-index: 2;
    display: flex;
    padding: 5rem 0;
    min-height: 80vh;
    flex-direction: column;
    justify-content: center;

    @include break($sm-md-break) {
      min-height: 80vh;
      padding: 4.5rem 0 5.5rem;
    }

    &__inner {
      position: relative;
      z-index: 2;
      width: 100%;
    }

    &__title {
      padding-top: 2rem;
      font-size: 5rem;
      line-height: 0.86;
      transform: scale(1.0);
      transform-origin: bottom left;
      margin-bottom: 5rem;
      max-width: 60rem;

      @include break($xs-break) {
        font-size: 7rem;
      }

      @include break($sm-break) {
        font-size: 9rem;
      }

      @include break($md-break) {
        font-size: 11rem;
      }

      @include break($lg-break) {
        font-size: 13rem;
      }
    }

    &__lead {
      max-width: 90%;
      margin-top: 5rem;

      .js & {
        opacity: 0;
        transition: all 1.1s cubic-bezier(.3, .75, .53, 1);
        transform: translate3d(0,-1.2rem,0);
      }

      .animates-in & {
        opacity: 1;
        transform: translate3d(0,0,0);
      }
    }

    &__scroll-hint {
      display: none;

      @include break($sm-break) {
        display: inline-block;
        margin-top: 4rem;

        .js & {
          opacity: 0;
          transition: all 1.1s cubic-bezier(.3, .75, .53, 1) 0.25s;
          transform: translate3d(0,-100%,0);
        }

        .animates-in & {
          opacity: 1;
          transform: translate3d(0,0,0);
        }
      }

      &-inner {
        display: inline-block;
        transform: rotate(90deg);
        width: 30px;
        height: 30px;
        border: 2px solid $red;
        border-radius: 50%;
      }

      svg {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }

    &__aside {
      display: none;
      z-index: 2;

      @include break($sm-md-break) {
        display: block;
        position: fixed;
        top: 10rem;
        left: 7.5rem;
        max-width: 25rem;
        font-size: 1.8rem;
        font-weight: 600;
        transition: all 0.8s cubic-bezier(.5,0,.5,1);
      }

      .site-header__hide & {

        @include break($sm-break) {
          transform: translate3d(0,-2rem,0)
        }

        @include break($sm-md-break) {
          // top: 7rem;
          transform: translate3d(0,-3rem,0)
        }
      }

      &.is-locked {
        top: -10rem;
        transition: 0.4s ease;
      }

      &-title {

        .js & {
          opacity: 0;
          transition: all 0.3s cubic-bezier(.3, .75, .53, 1);
        }

        .animates-in.title-out & {
          opacity: 1;
          transition: all 0.8s cubic-bezier(.3, .75, .53, 1);
        }
      }
    }

    &__share {
      margin-top: 2rem;
      position: relative;
      z-index: 2;
      width: 100%;

      @include break($sm-md-break) {
        bottom: 7rem;
        position: fixed;
        left: 7.5rem;
        width: auto;
      }

      &.is-locked {

        @include break($sm-md-break) {
          position: absolute;
          bottom: 2rem;
        }
      }

      &-inner {
        font-size: 1.6rem;
        display: flex;
        align-items: center;
        justify-content: flex-start;

        .js & {
          opacity: 0;
          transform: translate3d(0, 50px, 0);
          transition: all 1.1s cubic-bezier(.3, .75, .53, 1) 0.5s;
        }

        .animates-in & {
          opacity: 1;
          transform: translate3d(0, 0, 0);
        }
      }

      &-list {
        @include list-reset;
        display: flex;
        align-items: center;
      }

      &-title {
        padding-right: 2rem;
        display: inline-block;
      }


      li {
        display: inline-block;
        margin-right: 1.5rem;

        @include break($sm-break) {
          margin-right: 3rem;
        }
      }

      a {
        display: block;

        svg {
          display: block;
          transition: fill 0.5s ease;
          fill: $red;
          height: 1.6rem;
        }

        &:hover {

          svg {
            fill: $black;
          }
        }
      }
    }

    &__list-count {
      position: fixed;
      top: 50%;
      left: 50%;
      font-size: 300px;
      color: $white;
      font-weight: 600;
      line-height: 1;
      z-index: 0;
      transform: translate(-50%, -50%);

      @include break($sm-md-break) {
        font-size: 400px;
        margin-top: -200px;
        left: 7.5rem;
        transform: none;
      }

      &.is-locked {

        @include break($sm-md-break) {
          position: absolute;
          top: auto;
          // bottom: 16.5rem;
          margin-top: 0;
        }
      }

      .js & {
        opacity: 0;
      }

      &.animates-in {
        opacity: 1;
        transition: opacity 1.1s cubic-bezier(.3, .75, .53, 1) 0.25s;
      }

      &.animates-out {
        opacity: 0;
        transition: opacity 1.1s cubic-bezier(.3, .75, .53, 1) 0.25s;
      }

      &-dot {
        display: none;

        @include break($sm-md-break) {
          display: inline;
        }
      }
    }
  }



  &__content {
    margin-top: 20px;

    &-container {
      max-width: 100%;
      width: 100%;
      @include break($sm-md-break) {
        max-width: 90%;
      }
    }

    .js & {
      opacity: 0;
      transform: translate3d(0, 50px, 0);
      transition: all 1.0s cubic-bezier(.3, .75, .53, 1) 1s;

      &.animates-in {
        opacity: 1;
        transform: translate3d(0, 0, 0);
      }
    }
  }

  &__section {
    padding: 1.6rem 0 4rem;

    &--border {
      border-top: 0.3rem solid $black;
    }
    // faux border-top animation for numbered guide
    .scroll-list__item-content {

      &:before {
        content: '';
        display: block;
        width: 100%;
        height: 2px;
        background-color: $black;

        .js & {
          width: 0;
          transition: 0.5s ease;
        }
      }

      .js &.content-animates-in:before {
        width: 100%;
      }
    }

    @include break($md-break) {
      animation-delay: 0.5s;
    }

    p + p {
      padding-top: 3rem;
      &::before {
        content: '';
        white-space: normal;
      }
    }

    @include break($xs-break) {
      margin: 0;
    }

    @include break($sm-break) {
      margin: 0 5rem;
    }

    @include break($sm-md-break) {
      margin: 0;
    }

    @include break($md-break) {
      margin-left: 5rem;
    }

    @include break($lg-break) {
      margin-left: 20%;
    }

    &:first-of-type {
      margin-top: 4rem;
    }

    &:last-of-type {
      padding-bottom: 8rem;

      @include break($md-break) {
        padding-bottom: 15rem;
      }
    }

    p {
      line-height: 1.6;

      &.guide__section-lead, &.basic-content__lead {
        font-size: 2.8rem;
        // font-family: $serif;
        line-height: 1.4;
      }


    }

    h2 {
      font-size: 1.2rem;
      text-transform: uppercase;
      letter-spacing: 1px;
      margin-bottom: 1.5rem;
      margin-top: 3.3rem;

      &:first-child, &.guide__section-title {
        margin-top: 1.7rem;
      }
    }

    ul, ol {
      list-style: disc;
      padding: 2rem 1.6rem 2rem 2.4rem;
      margin-left: 1.2rem;
      font-family: $sans;

      &:last-of-type {
        padding-bottom: 0;
      }

      li {
        padding: 0.8rem 0;

        &:last-of-type {
          padding-bottom: 0;
        }
      }
    }

    ul {
      list-style-type: disc;
    }

    ol {
      list-style-type: decimal;
    }
  }

  &__lead {

    @include break($md-break) {
      margin-left: -10rem;
    }

    img {
      width: 100%;
    }

    &-abstract {
      margin-top: 3rem;
      margin-bottom: 3rem;
      @include break($md-break) {
        margin-top: 6rem;
        margin-bottom: 6rem;
      }
      p {
        line-height: 1.6;
        &.basic-content__lead {
          font-size: 3.6rem;
          line-height: 1.26;
          // font-family: $serif;
          margin-left: 0;
        }
        & + p {
          padding-top: 3rem;
          &::before {
            content: '';
            white-space: normal;
          }
        }
      }

    }

  }
}

.guide--numbered {
  .guide__lead {
    margin-left: 0;
  }
  .guide__lead .guide__lead-abstract p.basic-content__lead,
   p.basic-content__lead {
    font-family: $sans;
  }
}

.scroll-list {

  &__item {
    margin-bottom: 10rem;
    transform: translate3d(0, 0, 0);
    transition: transform 50ms cubic-bezier(0.785, 0.135, 0.15, 0.86);

    @include break($xs-sm-break) {
      margin-bottom: 15rem;
    }

    &:last-child {
      @include break($xs-sm-break) {
        margin-bottom: 20rem;
      }
    }

    &-number {
      font-size: 1.6rem;
      display: block;
      color: $red;
      font-weight: 600;
      letter-spacing: 0.5px;
      margin: 1.5rem 0 1rem;

      .js & {
        opacity: 0;
        transform: translate3d(2.5rem, 0, 0);
        transition: 0.6s $cubic-ease;
      }

      .js .content-animates-in & {
        opacity: 1;
        transform: translate3d(0, 0, 0);
      }
    }

    &-graphic {
      margin-bottom: 3rem;
      overflow: hidden;

      @include break($sm-break) {
        margin-left: -2.5rem;
        margin-right: -2.5rem;
      }

      @include break($sm-md-break) {
        margin-left: 0;
        margin-right: 0;
      }
    }

    &-content {

      .js & .u-title-line {
        opacity: 0;
        will-change: opacity, transform;
      }

      .js & .u-title-line__inner {
        opacity: 1;
        will-change: opacity, transform;
      }

      &--small {
        margin-left: 0;
        font-size: 2.0rem;
        line-height: 1.27;

        @include break($sm-break) {
          font-size: 2.6rem;
          margin-left: 25%;
        }

        @include break($sm-md-break) {
          margin-left: 40%;
          margin-right: 5%;
        }

        @include break($md-break) {
          margin-right: 0;
          margin-left: 50%;
        }

        .js & p {
          opacity: 0;
          transform: translate3d(2.5rem, 0, 0);
          transition: 0.6s $cubic-ease;
        }

        .js &.content-animates-in p {
          opacity: 1;
          transform: translate3d(0, 0, 0);
        }
      }

      &--large {
        margin-left: 0;
        font-size: 2.8rem;
        line-height: 1.26;

        @include break($sm-break) {
          margin-left: 10%;
          font-size: 3.2rem;
        }

        @include break($sm-md-break) {
          font-size: 3.6rem;
          margin-left: 0;
        }

        @include break($md-break) {
          margin-left: 20%;
        }

        &:before {
          content: '';
          display: block;
          width: 100%;
          height: 2px;
          background-color: $black;

          .js & {
            width: 0;
            transition: 0.5s ease;
          }
        }

        .js &.content-animates-in:before {
          width: 100%;
        }
      }
      &-text {
        .js & {
          opacity: 0;
          transform: translate3d(2.5rem, 0, 0);
          transition: 0.6s $cubic-ease;
        }

        .js .content-animates-in & {
          opacity: 1;
          transform: translate3d(0, 0, 0);
        }
      }
    }

    // Variations
    &--text {
      @include break($md-break) {
        margin-bottom: 20rem;
      }
    }

    &--image {
      display: flex;
      flex-direction: column-reverse;

      .js & .scroll-list__item-graphic {
        opacity: .1;
        transform: translate3d(0, 0, 0) scale(0.9);
        transition: transform 3.0s cubic-bezier(.3, .75, .53, 1),
                    opacity 1.0s cubic-bezier(.3, .75, .53, 1);

        img {
          transition: transform 2.0s cubic-bezier(.3, .75, .53, 1);
          transform: scale(1.5);
        }
      }

      .js &.animates-opacity .scroll-list__item-graphic {
        opacity: 1;
        transform: translate3d(0, 0, 0)  scale(1.0);

        img {
          transform: scale(1);
        }
      }
    }

    &--video-native,
    &--video-embed {
      .js & .scroll-list__item-graphic {
        opacity: .1;
        transform: scale(0.9);
        transition: transform 2.0s cubic-bezier(.3, .75, .53, 1),
                    opacity 1.0s cubic-bezier(.3, .75, .53, 1);

        video,
        .c-video-embed__wrapper {
          transform: scale(2.0);
          transition: transform 2.0s cubic-bezier(.3, .75, .53, 1),
                      clip-path 0.3s cubic-bezier(0.3, 0.75, 0.53, 0.95);
        }
      }

      .js &.animates-opacity .scroll-list__item-graphic {
        opacity: 1;
        transform: scale(1.0);

        video,
        .c-video-embed__wrapper {
          transform: scale(1.0);
        }
      }
    }
  }
}
