.feature-stories {
  margin-top: 3.0rem;
  position: relative;
  padding-bottom: 2.5rem;

  @include break($sm-break) {
    padding-bottom: 5.0rem;
    margin-top: 4.0rem;
  }

  @include break($md-break) {
    padding-bottom: 6rem;
  }

  .wrapper {
    position: relative;
    z-index: 0;
  }

  &__section-title {
    display: block;
    text-align: center;
    margin-bottom: 3rem;
  }

  &__header {
    position: relative;
    order: 1;
    min-height: 1px;
  }

  &__label {
    margin-bottom: 1.2rem;

    @include break($sm-break) {
      margin-bottom: 0  ;
      position: absolute;
      transform: rotate(-90deg);
      display: inline-block;
    }
  }

  &__list {
    padding: 0;
    list-style: none;
    position: relative;
  }

  &__column {
    margin-bottom: 3rem;

    &-inner {
      display: flex;
      flex-direction: column;

      &:hover {

        .feature-stories__title-line-outer {
          animation: drawsWidthAgain 0.8s $quickSnap forwards;
        }
      }
      &-link {
        order:2;
      }
    }
    @include break($sm-break) {
      margin-bottom: 0;
    }

    &--left {

      .feature-stories__label {

        @include break($sm-break) {
          top: 0;
          transform-origin: top left;
          transform: rotate(-90deg) translate(-100%, -2.4rem);
        }
      }
    }

    &--right {

      .feature-stories__label {

        @include break($sm-break) {
          transform-origin: right bottom;
          bottom: 2.4rem;
          right: 0;
          transform: rotate(-90deg) translate(100%, 2.4rem);
        }
      }

      .feature-stories__image {

        @include break($sm-break) {
          animation-delay: 0.4s !important;
        }
      }

      .feature-stories__title-line-inner {

        @include break($sm-break) {
          animation-delay: 0.6s !important;
        }
      }
    }
  }

  &__title {
    margin-bottom: 2.0rem;
    margin-right: 16.66667%;
    // @include line-after(3.8rem, 1.0rem);
    order: 2;
    color: $black;

    @include break($sm-break-max) {
      font-size: 2.4rem !important;
      line-height: 1.16 !important;
    }

    @include break($sm-break) {
      // @include line-after(5.8rem);
      margin-right: 0;
    }

    @include break($md-break) {
      margin-bottom: 2.0rem;
    }

    &-line {
      width: 3.8rem;
      height: 0.2rem;
      display: inline-block;
      position: relative;
      bottom: 0.8rem;
      margin-left: 1.5rem;
      overflow: hidden;

      @include break($sm-break) {
        width: 5.8rem;
        height: 0.3rem;
        bottom: 1rem;
      }

      @include break($md-break) {
        width: 5.8rem;
        bottom: 1.6rem;
      }

      &-outer,
      &-inner {
        display: block;
        width: 100%;
        height: inherit;
      }

      &-inner {
        background-color: $red;
      }
    }
  }

  &__content,
  &__cta {
    margin-left: 3rem;

    @include break($xs-break) {
      margin-left: 5rem;
    }
  }

  &__content {
    // font-family: $serif;
    padding: 0;
    line-height: 1.35;
    order: 3;
    color: $black;

    @include break($md-break) {
      font-size: 2.0rem;
    }
  }

  &__image {
    display: block;
    overflow: hidden;
    margin-bottom: 2.4rem;
    margin-top: 0;

    // img {
    //   transition: transform $trans-hover-time-img-slow-out ease;
    //   width: 100%;
    // }

    // &:hover {

    //   img {
    //     transform: scale(1.08);
    //     transition: transform $trans-hover-time-img-slow ease;
    //   }
    // }
  }

  &__color-block {

    @include break($sm-break) {
      background-color: $gray;
      position: absolute;
      bottom: 0;
      left: 0;
      top: 11.0rem;
      width: 100%;
      z-index: -1;
    }

    @include break($md-break) {
      top: 21.0rem;
    }
  }

  &__cta {
    padding: 0;
    margin-top: 1.4rem;
    order: 4;
    text-align: center;

    .u-cta {
      transition: transform 0.8s ease;
    }

    &:hover {

      .u-cta {
        transform: translate3d(5px, 0, 0);
      }
    }

    @include break($sm-break) {
      margin-top: 1.6rem;
    }
  }

  &__footer {

    .u-cta-regular {
      text-align: center;
      display: block;
      margin-top: 1rem;

      @include break($sm-break) {
        margin-top: 3rem;
      }

      @include break($md-break) {
        margin-top: 5rem;
      }
    }
  }
}
