$easeInOutQuad: cubic-bezier(0.77, 0, 0.175, 1);
$quickSnap: cubic-bezier(0.2, 1, 0.2, 1);

@mixin u-fades-in--from-right($time: 0.875s, $delay: 0.5s) {
  animation: fadesInFromRight $time $easeInOutQuad $delay forwards;
}

@mixin u-fades-in--from-left($time: 0.875s, $delay: 0.5s) {
  animation: fadesInFromLeft $time $easeInOutQuad $delay forwards;
}

@mixin u-fades-in--from-top($time: 0.875s, $delay: 0.5s) {
  animation: fadesInFromTop $time $easeInOutQuad $delay forwards;
}

@mixin u-fades-in--from-below($time: 0.875s, $delay: 0.2s) {
  animation: fadesInFromBelow $time $quickSnap $delay forwards;
}

@mixin u-fades-in--from-below--big($time: 2s, $delay: 0.5s) {
  animation: fadesInFromBelowBig $time $quickSnap $delay forwards;
}

@mixin u-fades-in--in-place($time: 0.5s, $delay: 0s) {
  animation: fadesInInPlace $time $easeInOutQuad $delay forwards;
}

@mixin u-animation-stagger($maxNum: 10, $delay: 0.2s) {

  @for $i from 1 through $maxNum {
    &:nth-of-type(#{$i}) {
      animation-delay: ($delay * $i);
    }
  }
}

@mixin u-draws-width($time: 1s, $delay: 0.2s) {
  animation: drawsWidth $time $easeInOutQuad $delay forwards;
}

@mixin u-slide-in--from-left($time: 0.875s, $delay: 0.5s) {
  animation: slideInFromLeft $time $cubic-ease $delay forwards;
}

@mixin u-slide-in--from-below($time: 0.5s) {
  animation: slideInFromBelow
    $time
    cubic-bezier(.165,.84,.44,1)
    forwards;
}
