.c-accordion {
  list-style: none;
  margin: .5rem 0 3rem!important;
  padding: 0 !important;

  &__item {
    border-top: 1px solid $gray;
    padding: 0 !important;

    &:last-child {
      border-bottom: 1px solid $gray;
    }

    &:before {
      content: none !important;
    }

    &--open {

      .c-accordion__button {
        color: $red;
        fill: $red;
      }

      .c-accordion__panel {
        position: unset;
      }
    }
  }

  &__item + &__item {
    margin-top: 0 !important;
  }

  &__header {
    margin: 0 !important;
  }

  &__button {
    display: block;
    font-size: inherit;
    font-family: $sans;
    font-weight: 600;
    padding: 1.5rem 6rem 1.5rem 0;
    width: 100%;
    text-align: left;
    min-height: 6rem;
    transition: color 0.4s ease-in-out;
    position: relative;

    @include break($sm-md-break) {
      padding: 0 6rem 0 0;
      min-height: 8rem;
    }

    span.c-accordion__button-svg {
      display: block;
      width: 5rem;
      height: 5rem;
      position: absolute;
      top: 50%;
      right: 0;
      margin-top: -2.5rem;
      display: flex;
      align-items: center;
      transition: all 0.2s ease;

      .c-accordion__item--open & {
        transform: rotate(90deg) translateX(-2px);
      }
    }

    svg {
      margin: 0 auto;
      display: block;
      transition: all 0.3s ease;
      height: 1.4rem;
      width: 7.5rem;

      @include break($sm-md-break) {
        width: 1.1rem;
        height: 2.1rem;
      }
    }

    &:hover {
      color: $red;
      fill: $red;
    }
  }

  &__panel {
    position: absolute;
    left: -9999px;

    @include p_list-style();

    p:last-of-type {
      margin-bottom: 3rem;
    }
  }
  &-toggle {
    color: $red;
    font-size: 1.4rem;
    line-height: 1.625;
    margin-top:3rem;
    padding-left: 0;
    padding-right: 0;
    &:hover {
      text-decoration: underline;
    }
    &:after {
      content: "\0020-";
      // padding-left: .5rem;
    }
  }
  &-expand {
    &:after {
      content: "\0020+";
    }
  }
}
