.c-audio-player {
  .additional-content &, .shared-site-content & {
    @extend .container-fluid;
    max-width: 80rem;
  }
  max-width: 80rem;

  &__wrapper {
    background-color: $gray;
    min-height: 9.0rem;
    position: relative;
    padding: 3rem;

    @include break($xs-break) {
      // padding: 3rem 3rem;
    }

    // .c-audio-player--with-image & {
    //   padding: 3rem;
    // }
  }

  &__content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    @include break($xs-break) {
      flex-direction: row;
      justify-content: space-between;
    }
  }

  &__title {
    display: flex;
    flex-direction: column-reverse;
    margin-bottom: 1rem;
    flex: 2;
    max-width: 500px;
    order: 2;
    width: 100%;

    @include break($xs-break) {
      order: 1;
    }

    .c-audio-player--with-image & {
      padding-right: 3rem;
    }

    &-text {
      line-height: 1.3;
      font-size: 1.8rem;
      margin: 0 !important;
      font-weight: 600;

      @include break($sm-break) {
        font-size: 2.0rem;
      }

      @include break($md-break) {
        font-size: 2.4rem;
      }
    }

    .u-tiny-label {
      margin-bottom: 0.4rem;

      @include break($sm-break) {
        margin-bottom: 1.5rem;
      }

      .c-audio-player--with-image & {

        @include break($sm-break) {
          margin-bottom: 0.6rem;
        }
      }
    }
  }
  &__subtitle {

    &-text {
      order: -1;
    }

  }

  // Controls
  &__controls {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    order: 3;
    flex: 1 0 100%;
    width: 100%;
    margin-top: 1.5rem;
    // font-size: 1.6rem;

    button {
      padding-bottom: 0;
    }
  }

  // Play/Pause Button
  &__toggle-state {
    order: 2;
  }

  // Play/Pause Spans
  &__play,
  &__pause {
    font-size: inherit;
    color: $red;
    text-align: left;

    &.active-state {
      display: none;
    }

    svg {
      display: inline-block;
      width: 3rem;
      height: 3rem;
    }
  }

  &__rw {
    order: 1;
    padding-left: 0;
  }

  &__ff {
    order: 3;
  }

  &__v-slider {
    order: 4;
    width: 100%;
    display: flex;
    align-items: center;
    margin-top: 0.4rem;

    @include break($xs-break) {
      margin-left: 1.5rem;
      width: auto;
      margin-top: 0;
    }

    .ui-slider {
      height: 0.4rem;
    }

    .ui-slider-handle {
      top: -0.4rem;
      width: 1.2rem;
      height: 1.2rem;
    }
  }

  &__v-track {
    width: 6rem;
    position: relative;
    margin: 0 0.8rem;
  }

  &__v-down,
  &__v-up {
    display: flex;
    align-items: center;
  }

  &__v-down {
    padding-right: 0.3rem;
  }

  &__v-up {
    padding-left: 0.3rem;
  }

  &__v-down,
  &__v-up {

    &:active {

      svg {
        fill: $black;
      }
    }

    &.is-disabled {
      cursor: default;
      opacity: 0.5;
      pointer-events: none;
    }
  }

  &__time {
    color: $gray-dark;
    font-size: 1.6rem;
    margin-right: 1rem;
  }

  &__controls-aside {
    order: 6;
    color: $gray-dark;
    margin-left: auto;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    width: 100%;
    margin-top: 0.5rem;

    @include break(600px) {
      width: auto;
      margin-top: 0;
    }
  }


  // GRAPHIC
  &__graphic {
    flex: 1;
    max-width: 14rem;
    order: 1;
    margin-bottom: 2rem;

    @include break($xs-break) {
      order: 2;
      margin-bottom: 0;
    }

    img {
      border-radius: 50%;
      width: 100%;
    }
  }


  // TIMELINE
  &__timeline {
    width: 100%;
    position: absolute;
    bottom: -0.2rem;
    left: 0;
  }

  &__seek {
    padding: 0;
    margin: 0;
    height: auto;
    position: absolute;
    bottom: -2px;
    left: 0;
  }

  &__footer {
    font-size: 1.5rem;
    color: $gray-dark;
    font-weight: 400;
    padding: 2.1rem 0;
  }

  &__transcript {
    padding: 3.0rem;
    border: 1px solid $gray-dark;
    display: none;

    &-link {
      font-size: 1.6rem;
      color: $red;
      fill: $red;
      padding: 0.1rem;
      display: inline-block;
      text-decoration: none;
      padding: 0;
      text-align: left;
      padding-left: 0;
      margin-left: 1.2rem;

      span {
        padding-right: 0.3rem;
      }
    }

    a + &-text {
      @extend .u-margin--sm-top;
    }
  }

  // SVG Circle
  &__control-circle {
    fill: none;
    stroke: $red;
    stroke-width: 0.2rem;
  }

  input[type=range] {
    height: 1.4rem;
  }

  // &__seek-slider {
  //   position: absolute;
  //   bottom: 0;
  //   left: 0;
  // }
}

.ui-slider {
  height: 1rem;
  background-color: darken($gray, 10%);

  &-range {
    height: inherit;
    background-color: $red;
    position: absolute;
    left: 0;
    top: 0;
    border-right: 2px solid $red;
    box-sizing: content-box;
  }

  &-handle {
    display: block;
    position: absolute;
    top: -0.3rem;
    height: 1.6rem;
    width: 1.6rem;
    background-color: $gray;
    border: 1px solid $gray-dark;
    border-radius: 50%;
    z-index: 2;
    text-indent: -9999px;
    transition: none;
  }
}


.ui-slider-handle {
  transform: translate3d(-50%,0,0);
  padding: 0;
}
