.c-people-list{
  &__title {
    &.u-h2 {
      margin: 3rem 0 2rem;
    }
  }
}
.c-person-detail {
  display: flex;
  flex-direction: column-reverse;

  @include break($xs-break) {
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-between;
  }

  .basic-content & {
    ol {
      list-style: none;
      margin: 0;
      padding: 0;

      li {
        padding-left: 0;
        line-height: 1.6;

        &:before {
          display: none;
        }
      }

      li + li {
        margin-top: 0;
      }
    }
  }

  & + & {
    margin-top: 3rem;

    @include break($sm-md-break) {
      margin-top: 5rem;
    }
  }

  p + p {
    padding-top: 0 !important;

    &:before {
      content: '';
    }
  }

  &--faculty {

    @include break($xs-break) {
      flex-direction: row-reverse;
      justify-content: flex-end;
    }
  }

  p {
    font-family: $sans !important;
  }

  a {
    text-decoration: none;
  }

  &__content {

    @include break($xs-break) {
      // flex: 1 0 auto;
    }

    @include break($sm-break) {
        padding-right: 4rem;
    }

    .c-person-detail--faculty & {

      @include break($xs-break) {
        padding-left: 3rem;
      }

      @include break($sm-break) {
        padding-right: 0;
      }
    }
  }

  &__title {
    margin: 0 0 0.6rem !important;
  }

  &__role {
    font-size: 1rem;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 0.1rem;
    margin: 0 0 0.6rem;

    @include break($sm-break) {
      font-size: 1.2rem;
    }
  }

  &__subtitle {
    margin-bottom: 1rem;

    & + & {
      margin-top: -1rem;
    }
  }

  &__meta,
  &__links {
    // font-size: 1.3rem;
    @include list-reset;

    li {
      padding-left: 0;
      line-height: 1.6;
    }

    @include break($sm-md-break) {
      font-size: 1.7rem;
    }
  }

  &__meta {

    a {
      color: $black !important;
      text-decoration: none;
      display: inline-block;

      &:hover {
        transform: translate3d(5px, 0, 0);
      }
    }

    span {
      display: inline-block;
      width: 2.4rem;

      svg {
        max-height: 1rem;
        width: 1rem;
        height: auto;

        @include break($sm-break) {
          max-height: 1.3rem;
          width: 1.3rem;
        }
      }
    }
  }

  &__links,
  // .basic-content__person & &__links,
  .basic-content & &__links {
    margin-top: 1rem;

    .u-cta-regular {
      display: inline-block;
    }
  }

  &__image {

    margin-bottom: 2.4rem;

    img {
        width: 100%;
    }

    @include break($xs-break) {
      min-width: 14rem;

      img {
          width: 14rem;
      }

    }

    @include break($sm-break) {
      margin-bottom: 0;
      min-width: 20rem;

      img {
          width: 20rem;
      }

    }

    .c-person-detail--faculty & {

      @include break($sm-break) {
        margin-right: 3rem;
      }
    }

    .basic-content__person & {

      @include break($xs-break) {
        margin-left: 3rem;
      }
    }

    img {
      // margin: 0;

      // @include break($sm-break) {
      //   margin: 0;
      // }
    }
  }
}
