.c-sub-nav {
  position: absolute;
  top: -8rem;
  left: 2.5rem;
  right: 2.5rem;
  width: auto;

  @include break($xs-break) {
    left: 5rem;
    right: 5rem;
  }

  @include break($sm-break) {
    max-width: 20.5rem;
    margin-top: 0;
    position: relative;
    top: auto;
    right: auto;
    left: auto;
    width: auto;
  }

  &__container {
    margin-top: 8rem;
    position: relative;
  }

  &__content {
    display: none;
    padding: 1.6rem;
    background-color: $white;
    position: relative;
    border: 1px solid $black;
    border-top: 0;
    z-index: 3;

    @include break($sm-break) {
      padding: 0;
      border: 0;
      background-color: transparent;
      display: block;
    }
  }

  &__list {
    @include list-reset;
    &--sublist {
      margin: 1rem 0 0;

      @include break($sm-break) {
        margin: 1.5rem 0 1rem;
      }

      > li > a {
        font-weight: 400;
      }
    }

    .c-sub-nav__active {

      > a {
        font-weight: 600;
      }
    }



    &-item {
      padding: 0.6rem 0;

      &--subitem {
        @include list-reset;
        font-size: 1.6rem;
        padding: 0.3rem 0;
        margin-top: 0.5rem;

        a {
          padding-left: 2.8rem;
          position: relative;

          &:hover {

            &:before {
              background-color: $red;
            }
          }

          &:before {
            content: '';
            display: block;
            width: 2rem;
            height: 0.2rem;
            position: absolute;
            top: 0.6rem;
            left: 0;
            background-color: $black;
            transition: 0.5s ease;

            @include break($sm-break) {
              top: 0.9rem;
            }
          }
        }
        & & & {
          margin-top: 0.25rem;

          a {
            padding-left: 1.8rem;
            margin-left: 2rem;
            &:before {
              width: 1rem;
            }
          }
        }
      }
    }
  }

  &__button {
    display: block;
    font-size: 1.2rem;
    text-align: left;
    position: relative;
    font-weight: 600;
    border: 1px solid $black;
    border-top: 0.3rem solid $red;
    padding: 1.6rem;
    background-color: $white;
    width: 100%;

    @include break($sm-break) {
      display: none;
    }

    span {
      display: block;
      position: absolute;
      top: 1.6rem;
      right: 2rem;
      transform: rotate(90deg);
      transform-origin: center;

      svg {
        height: 0.9rem;
        // width: auto;
      }
    }

    &.menu-open span {
      right: 1.9rem;
      transform: rotate(270deg);
    }
  }

  a {
    color: $black;
    display: block;
    font-size: 1.4rem;
    line-height: 1.3;

    @include break($sm-break) {
      font-size: inherit;
    }

    &:hover {
      transform: translate3d(5px, 0, 0);
      transition-duration: 0.5s;
      color: $red;
    }
  }

  &__social {
    margin-top: 2rem;

    svg {
      fill: $black;
    }

    a:hover {
      transform: none;

      svg {
        fill: $red;
      }
    }
  }

  &__header {
    font-weight: 600;
    padding: 0.6rem 2rem 0.6rem 0;
    line-height: 1.4;
    font-size: 1.6rem;

    @include break($sm-break) {
      border-top: 0.3rem solid $red;
      border-bottom: 0.1rem solid $gray;
      padding: 1.4rem 2rem 1.3rem 0;
      margin-bottom: 1.6rem;
      font-size: 1.6rem;
    }

    a {

      @include break($sm-md-break) {
        font-size: 1.8rem;
      }
    }

    a:hover {
      transform: none;
    }
  }

  &__active {

    > a {
      color: $red;
    }
  }

  &__active-child {

    > a {
      font-weight: 600;
    }
  }

}

.page--dept {
  .c-sub-nav {
    &__content {
      @include break($sm-break) {
        display: block !important;
      }
    }
  }
}

.page--basic-page {
    .c-sub-nav {
      top: 2rem;

      @include break($sm-break) {
        position: absolute;
        left: 11rem;
        right: 11rem;
        width: auto;
        max-width: 100%;
       }

      @include break($sm-md-break) {
        max-width: 20.5rem;
        margin-top: 0;
        position: relative;
        top: auto;
        right: auto;
        left: auto;
        width: auto;
      }

      &__button {
        @include break($sm-break) {
          font-size: inherit;
          display: block;
        }
        @include break($sm-md-break) {
          display: none;
        }
      }


      &__header {
        @include break($sm-break) {
          border: none;
          padding-right: 20px;
          margin-bottom: 0;
        }
        @include break($sm-md-break) {
          border-top: 0.3rem solid $black;
          border-bottom: 0.1rem solid $gray;
          padding: 1.4rem 2rem 1.3rem 0;
          margin-bottom: 1.6rem;
        }

      }

      &__content {
        @include break($sm-break) {
          display: none;
          padding: 1.6rem;
          background-color: $white;
          position: relative;
          border: 1px solid $black;
          border-top: 0;
          z-index: 3;
        }
        @include break($sm-md-break) {
          width: 100%;
          padding: 0;
          border: 0;
          background-color: transparent;
          display: block !important;
        }
      }

      &__social {
        @include break($sm-md-break-max) {
          display: none;
        }
        @include break($sm-md-break) {
          display: block;
        }
      }
    }
  }
