.c-download-button {
  display: inline-block;
  background-color: $gray;
  font-family: $sans;
  font-weight: 600;
  font-size: 1.6rem;
  padding: 2rem 3.5rem 2rem 5.5rem;
  position: relative;
  transition: 0.5s ease;

  @include break($sm-break) {
    font-size: 2rem;
    padding: 2rem 3.5rem 2rem 7.5rem;
  }

  &:hover {
    color: $white;
    background-color: $red;

    svg {
      fill: $white;
    }
  }

  &__icon {
    display: block;
    position: absolute;
    left: 2rem;
    top: 50%;
    transform: translate(0,-50%);

    @include break($sm-break) {
      left: 3rem;
    }

    svg {
      display: block;
      transition: 0.5s ease;
      width: 1.6rem;
      height: 2.1rem;

      @include break($sm-break) {
        width: 2.2rem;
        height: 2.9rem;
      }
    }
  }
}