.search-results {
  width: 100%;

  &__form {
    margin-bottom: 3rem;

    @include break($sm-md-break) {
      margin-bottom: 6.2rem;
    }

    &-form {
      border-bottom: 0.2rem solid $gray;
    }

    &-inner {
      position: relative;
    }

    &-input,
    .st-default-search-input,
    .st-ui-search-input {
      font-size: 3.6rem;
      color: $black;
      font-weight: 600;
      border: 0;
      line-height: 1;
      background-image: none;
      width: 100%;
      padding: 0 5rem 0 0;
      box-sizing: border-box;
      font-family: $sans;
    }

    button[type=submit],
    input[type=submit],
    &-input,
    .st-default-search-input,
    .st-ui-search-input {
      height: 5rem;
      display: block;
      margin-bottom: 1.2rem;
    }

    button[type=submit], input[type=submit] {
      background-color: transparent;
      width: 50px;
      position: absolute;
      top: 0;
      right: 0;
      display: flex;
      align-items: center;
      padding: 0;

      span {
        margin: 0 auto;
      }

      svg {
        display: block;
        width: 3rem;
        height: 3rem;
        fill: $red;
      }
    }
  }
}
