.o-media-object {
  display: block;
  overflow: hidden;
  margin-bottom: 5rem;

  &__image {
    margin-bottom: 2rem;

    @include break($xs-sm-break) {
      margin-bottom: 0;
      width: 50%;
      max-width: 34rem;
      float: left;
      padding-right: 4rem;
    }
  }

  &__content {
    overflow: hidden;

    h1, h2, h3, h4, h5, h6, p {
      margin-top: 0 !important;
    }

    h3 {

      @include break($sm-break) {
        font-size: 2.4rem !important;
      }
    }

    p {
      font-size: 1.6rem;
    }

    .u-cta {
      margin-top: 2rem;
    }
  }
}