.two-column-layout {
  margin: 2.5rem 0;

  @include break($sm-break) {
    margin: 5.0rem 0 5rem;
  }

  @include break($md-break) {
    padding: 0;
    margin: 7.5rem 0 6rem;
  }

  &__right {

    .u-watch {

      @include break($sm-break) {
        animation-delay: 0.4s !important;
      }
    }
  }
}