.c-double-callout {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  max-width: 80rem;

  &__header-link {
    display: flex;
    flex-direction: column-reverse;
    order: 1;
    width: 100%;
    min-height: 1px;

    &:hover + .c-double-callout__content .c-double-callout__line-outer {
      animation: drawsWidthAgain 0.8s $quickSnap forwards;
      animation-delay: 0s !important;
    }
  }

  &__title {
    margin-bottom: 2.4rem;
    position: relative;
    padding-left: 6rem;
    color: $black;
    // hyphens: auto;

    @include break($sm-break) {
      margin-bottom: 4rem;
      padding-left: 0;
    }
  }

  &__content {
    order: 3;
    position: relative;
    width: 100%;

    @include break($sm-break) {
      padding-left: 8rem;
    }

    @include break($md-break) {
      padding-left: 28%;
    }
  }

  &__text {
    line-height: 1.55;
    margin-bottom: 1.4rem;
  }

  &__image {
    margin-bottom: 2rem;

    @include break($sm-break) {
      margin-bottom: 3rem;
    }
  }

  &__link-list {
    @include list-reset;
    font-size: 1.4rem;
    line-height: 1.625;

    @include break($md-break) {
      font-size: 1.6rem;
    }

    a {
      display: inline-block;
      transition: 0.4s ease;
      padding-left: 1.5rem;
      text-indent: -1.5rem;

      span {
        display: inline-block;
        padding-left: 1.8rem;
        transition: 0.4s ease;

        svg .fill-red {
          transition: 0.4s ease;
        }
      }

      &:hover {
        color: $black;
        transform: translate3d(5px, 0, 0);

        span {
          transform: translate3d(5px, 0, 0);

          svg .fill-red {
            fill: $black;
          }
        }
      }
    }
  }

  &__line {
    height: 0.2rem;
    width: 4rem;
    display: block;
    position: absolute;
    top: 1.3rem;
    left: 0;
    overflow: hidden;

    @include break($sm-break) {
      width: 5.6rem;
      top: 1rem;
    }

    @include break($md-break) {
      top: 1.1rem;
      left: calc(33% - 12rem);
      height: 0.3rem;
      width: 6.6rem;
    }

    &-outer {
      display: block;
      width: 100%;
      height: inherit;
    }

    &-inner {
      display: block;
      width: 100%;
      background-color: $red;
      height: inherit;
    }

    &--title {

      @include break($sm-break) {
        display: none;
      }
    }

    &--content {
      display: none;

      @include break($sm-break) {
        display: block;
      }
    }
  }
}
