.dept-overview {
    margin-bottom: 6rem;

  @include break($sm-break) {
    margin-bottom: 8rem;
  }

  &__wrapper {
    margin-left: -2.5rem;
    margin-right: -2.5rem;
    width: calc(100% + 5rem);

    @include break($xs-break) {
      margin-left: -5rem;
      margin-right: -5rem;
      width: calc(100% + 10rem);
    }

    @include break($sm-break-max) {
      padding-left: 0;
      padding-right: 0;
    }
  }

  &__inner {
    background-color: $white;
    display: flex;
    flex-direction: column;
    text-align: left;
    width: 100%;
    padding: 3.5rem 2.5rem;

    @include break($xs-break) {
      padding: 4.5rem 5rem;
    }

    @include break($sm-break) {
      display: flex;
      flex-direction: row;
      margin: 0;
      width: 100%;
      align-items: flex-start;
      justify-content: space-between;
    }

    @include break($sm-md-break) {
      padding: 10rem 9rem 9rem;
    }
  }

  &__content {
    margin-bottom: 3rem;

    @include break($sm-break) {
      order: 2;
      flex: 2;
      padding-left: 4rem;
      margin-top: 1.6rem;
    }

    @include break($sm-md-break) {
      padding-left: 6.5%;
      margin-top: 1.4rem;
    }

    @include break($lg-break) {
      // padding-left: 11%;
      padding-right: 4.4rem;
    }

    &-inner {
      max-width: 63rem;
    }

    &-title {
      font-weight: 600 !important;
      margin-bottom: 2rem;
    }

    p {
      // font-family: $serif;
      font-size: 1.6rem;
      line-height: 1.6;

      @include break($sm-break) {
        font-size: 1.8rem;
      }

      @include break($md-break) {
        font-size: 2rem;
      }

      // TODO: Need to refactor some of these content/utility classes. See utils.paragraph.scss
      &.basic-content__lead {
          font-size: 2rem;

          @include break($sm-md-break) {
            font-size: 2.4rem;
          }
      }
    }

    &-cta {
      margin-top: 1.8rem;
    }
  }

  &__sidebar {

    @include break($sm-break) {
      order: 1;
      flex: 1;
      max-width: 25rem;
    }

    @include break($sm-md-break) {
      order: 1;
    }
  }

  &__nav {

    &-social {
      display: none;

      @include break($sm-break) {
        display: block;
      }
    }
  }

  &__info {

    &--legacy {
      @extend .u-list-inline;
    }

  }

  // Deal with dept homepages that have opted to hide the Nav
  &.dept-overview--no-nav {
    .dept-overview__content {
      display: flex;
      justify-content: center;

      @include break($sm-break) {
        padding-left: 0;
        margin-top: 0;
      }

      @include break($sm-md-break) {
        padding-left: 0;
        margin-top: 0;
      }

      @include break($lg-break) {
        padding-right:0;
        margin-top: 0;
      }
      .dept-overview__content-inner {
        // Matches current max width of standard subpages that do not have a nav
        max-width: 84.4rem;
        flex: 0 1 84.4rem;
      }
    }
  }

}
