.video-header {
  position: relative;
  overflow: hidden;
  margin-bottom: 4.0rem;

  &__inner {
    position: relative;
    padding-bottom: 50%;
    height: 0;
    overflow: hidden;
    max-width: 100%;

    &:after {
      @include break($xs-break) {
        content: '';
        display: block;
        position: absolute;
        left: 0;
        bottom: 0;
        height: 30%;
        width: 100%;
        background: linear-gradient(to bottom, rgba(0,0,0,0) 0%,rgba(0,0,0,0.6) 80%);
        transform: translate3d(0,100%,0);
        transition: transform 1s ease-in-out;
      }
    }
  }

  &__overlay {
    @include break($xs-break) {
      background-color: transparentize($black, .6);
      z-index: 2;
      opacity: 0;
      transition: all 2s ease-in-out;
    }
  }

  video {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
    object-fit: cover;

    .js & {
      transition: opacity 1s ease;
    }

  }



  .video-vimeo-ambient, .video-youtube-ambient{
    z-index: 0;
    &.first-video{
      z-index: 1;
      iframe {
        z-index: 1;
      }
    }
    position: absolute;	
    top: 0;	
    left: 0;	
    height: 100%;	
    width: 100%;	

    iframe {

      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;

      .js & {
        opacity: 0;
        transition: opacity 1s ease;
      }
    }
  }
  
  &__inner {
    .container-fluid {
      display: flex;
      position: absolute;
      bottom: 0;
    }
  }

  &__content {
    position: relative;
    z-index: 10;
    align-self: flex-end;
    padding-bottom: 1rem;
    visibility: hidden;

    @include break($xs-break) {
      visibility: visible;
      padding-bottom: 2rem;
      transition: padding-bottom 0.5s ease-in-out 0.5s;
    }

    @include break($sm-break) {
      padding-bottom: 6rem;
    }

    .js & {

      .u-title-line {
        margin-bottom: 0.3rem;
      }
    }
  }

  &__title {
    font-size: 1.8rem;
    color: $white;
    letter-spacing: 0.05rem;
    margin-bottom: 2.0rem;
    max-width: 65.0rem;
    line-height: 1.18;
    font-weight: 600;
    display: block;

    @include break($sm-break) {
      margin-bottom: 5.0rem;
      font-size: 2.8rem;
    }

    @include break($md-break) {
      margin-bottom: 7.5rem;
      font-size: 3.4rem;
    }

    .js & {
      opacity: 0;
    }
  }

  &--quote {

    .video-header__title {

      @include break($sm-break) {
        @include style-h1;
        margin-left: 5rem;
      }
      @include break($md-break) {
        margin-left: 0;
      }

      .u-title-line {
        position: relative;

        &:first-child {
          margin-left: -0.7rem;
          @include break($sm-break) {
            margin-left: -1.5rem;
          }

          @include break($md-break) {
            margin-left: -2.1rem;
          }
        }
      }

      span {
        position: absolute;
        top: 0;
        left: -1.5rem;

        @include break($sm-break) {
          left: -1.8rem;
        }

        @include break($md-break) {
          left: -2.5rem;
        }
      }
    }
    .video-header__footer {
      @include break($sm-break) {
        margin-left: 5rem;
      }
      @include break($md-break) {
        margin-left: 0;
      }
    }
    .video-header__scroll-cta {
      @include break($sm-break) {
        margin-left: 5rem;
      }
      @include break($md-break) {
        margin-left: 0;
      }
    }
  }

  &__footer {
    position: relative;
    overflow: hidden;
    display: inline-block;
    padding: .2rem 3rem .2rem .2rem;

    a {
      position: relative;
      z-index: 2;
    }

    .js & {
      opacity: 0;

      .c-title-cta__arrow {
        opacity: 0;
      }
    }

  }

  &__scroll-cta {
    position: relative;
    color: $white;
    font-weight: 600;
    font-size: 1.4rem;
    visibility: hidden;
    opacity: 0;

    @include break($xs-break) {
      transition:opacity 0.875s ease-in-out;
      transition-delay: 0.5s !important;
      padding-bottom: 1rem;
    }

    a {
      color: $white;
    }
    &-text,
    &-arrow {
      display: inline-block;
    }

    &-arrow {
      display: inline-block;
      margin: 1.4rem 0 0;
      position: relative;
      transform: rotate(90deg);
      width: 2.4rem;
      height: 2.4rem;
      margin-left: 1rem;
      top: 0.6rem;
    }
  }

  &__player-control {
    position: absolute;
    bottom: 1rem;
    right: 3rem;
    transition: bottom 0.5s ease-in-out 0.5s;
    z-index: 3;
    opacity: 0;
    &--no-sound {
      right: 7rem;
      padding: 2px 6px;
    }

    @include break($sm-break) {
      bottom: 3rem;
      right: 3rem;
    }

  }

  &__poster-fallback {

    .js & {
      opacity: 0;
      transition: opacity 0.4s ease-in-out, transform 10s ease-in-out;
    }

    .js &.animates-in {
      opacity: 1;
      transform: scale(1.1);
    }
  }

  &.animates-overlay .video-header__overlay {
    opacity: 1;
  }

  &.animates-cta {

    .video-header__inner:after {
      transform: translate3d(0,0,0);
    }

    .video-header__content {
      @include break($xs-break) {
        padding-bottom: 1rem;
      }
      @include break($sm-break) {
        padding-bottom: 3.0rem;
      }
    }

    .video-header__footer {
      transition: none;
      visibility: hidden;

      @include break($xs-break) {
        visibility: visible;
        transition: all 0.35s ease-in 0.5s;

        &:before {
          transform: translate3d(0,0,0);
        }
      }

      .c-title-cta__title,
      .c-title-cta__text {
        transition: none;
        transition-delay: none;
        animation-delay: 0s;
        padding-left: 2rem;
        opacity: 0;
        @include break($xs-break) {
          opacity: 1;
          transition: 0.875s ease-in-out;
          transition-delay: 0.5s !important;
        }
      }
    }

    .c-title-cta__btn {
      padding: 1.6rem 2rem 1.9rem;
      background-color: $red;
    }

    &.video-header--quote {

      .video-header__footer {

        &.video-header__attribution {
          transition: all 0.35s ease-in;
          transform: translateY(100%) !important;
          opacity: 0 !important;
          background-color: transparent;
        }

        .c-title-cta__text {
          padding-left: 0;
        }

        &.c-title-cta {
          visibility: hidden;

          @include break($xs-break) {
            visibility: visible;
          }
        }
      }
    }
    .video-header__scroll-cta {
      @include break($xs-break) {
        visibility: visible;
        opacity: 1;
      }
      .c-title-cta__btn {
        display: inline-block;
      }
    }
  }
  &__no-audio {
    text-align:right;
    z-index:10;
    padding-top:.6rem;
    color: $white;
    p {
      font-size:1.1rem;
    }
    @include break($xs-break) {
      padding-right: 3.6rem;
      color: $black;
    }
  }

  &__small-cta {
    background-color: $red;

    @include break($xs-break) { 
      background: transparent;
    }

    &-content {      
      padding: 2rem 0 3rem;

      @include break($xs-break) {
        display: none;
      }

      p {
        font-size: 2.3rem;
        font-weight: 600;
        color: $white;
      }
      .c-title-cta-btn {
        font-size: 1.4rem;
        font-weight: 600;
        color: $white;
        margin-top: 2rem;

        @include break($xs-break) {
        display: inline-block;
        transition: transform 0.8s ease;
        transition: transform 0.8s ease,-webkit-transform 0.8s ease;
        }

        &:hover {
          transform: translate3d(5px,0,0);
          transition: transform 0.8s ease;
          transition: transform 0.8s ease,-webkit-transform 0.8s ease;
        }
        &:focus {
          .c-title-cta__arrow .stroke-white {
            stroke: $red;
          }
        }
      }
    }
  }
  &.vimeo-error {
    @include no-video-animations;
    iframe {
      display: none;
    }
    .u-title-line,
    .u-title-line--inner {
      opacity: 1 !important;
      transition: none !important;
      transform: none !important;
    }
    .video-header__no-audio {
      display: none;
    }
    .video-header__small-cta-content {
      padding-top: 3rem;
    }
  }
}

.object-fit-polyfill video {
  transform: none;
}
