.news-entry {
  margin-top: 3rem;
  margin-bottom: 3rem;

  @include list-basic;

  @include break($sm-break) {
    margin-top: 5rem;
    margin-bottom: 5rem;
  }

  @include break($md-break) {
    margin-top: 8rem;
  }

  &__wrapper {
    margin-bottom: 5rem;
    width: 100%;
    // order: 2;

    @include break($sm-break) {
      margin-bottom: 7rem;
    }
  }

  &__header {
    display: flex;
    flex-direction: column;
    margin-bottom: 2.4rem;

    @include break($sm-break) {
      margin-bottom: 4rem;
    }
    h1 {
      margin-bottom: 0;
    }
  }

  &__meta {
    display: block;
    font-size: 1.2rem;
    line-height: 1.4;

    @include break($sm-break) {
      font-size: 1.5rem;
    }

    &--date {
      order: 1;
      font-weight: 500;
      margin-bottom: 1.5rem;

      @include break($sm-break) {
        margin-bottom: 2.4rem;
      }
    }

    &--pub {
      order: 3;
      margin-top: 1.5rem;

      @include break($sm-break) {
        margin-top: 2.4rem;
      }

      &-line {
        display: inline-block;
        width: 1.8rem;
        height: 0.2rem;
        margin-right: 1rem;
        position: relative;
        top: -0.4rem;
        background-color: $red;
      }
    }
  }

  &__title {
    order: 2;
  }

  &__subtitle {
    order: 3;
    font-weight: 600;
    margin-top: 0.8rem;

    @include break($sm-break) {
      margin-top: 1.5rem;
    }
  }

  &__footer {
    margin-top: 3rem;
  }
}

.news-entry-ftd {
  margin-top: 0;

  &__header {
    margin-bottom: 5rem;

    @include break($sm-break) {
      margin-bottom: 8rem;
    }

    &-wrapper {
      display: flex;
      align-items: flex-end;
      height: 55rem;
      margin-top: auto;
      margin-bottom: 5rem;
    }

    &-inner {
      color: $white;
      display: flex;
      flex-direction: column;
    }
  }

  &__date {
    order: 1;
    margin-bottom: 0.8rem;
    line-height: 1.5;
    font-size: 1.2rem;
    font-weight: 600;
    letter-spacing: 0.1rem;
    text-transform: uppercase;

    @include break($sm-break) {
      margin-bottom: 1.5rem;
    }
  }

  &__title {
    order: 2;
  }

  &__subtitle {
    order: 3;
    font-weight: 600;
    margin-top: 0.8rem;

    @include break($sm-break) {
      margin-top: 1.5rem;
    }
  }

  &__aside {
    font-size: 1.2rem;
    line-height: 1.26;
    margin-bottom: 3rem;

    @include break($md-break) {
      font-size: 1.5rem;
      margin-bottom: 5rem;
      // margin-bottom: 0;
    }

    @include break($md-break) {
      order: 2;
    }

    br {
      display: none;

      @include break($md-break) {
        display: block;
      }
    }

    p + p {
      margin-top: 0.6rem;


      &:before {
        content: '';
      }
    }
  }

  &__wrapper {

    @include break($md-break) {
      order: 1;
    }
  }

  &__image-callout {

    @include break($lg-break) {
      margin-right: -16.66666%;
    }

    @include break($xl-break) {
      margin-left: -16%;
      margin-right: -53%;
    }
  }

  .basic-content__image-caption {

    @include break($lg-break) {
      width: 22rem;
    }
  }
}
