.guide-carosel {

  overflow: hidden;
  padding-left: 2rem;
  padding-right: 2rem;
  position: relative;

  @include break($sm-break) {
    margin-top: 4rem;
  }

  .slick-list {
    padding-bottom: 3rem;

    @include break($sm-break) {
      padding-top: 3rem;
    }
  }

  .slick-slide {

    @include break($sm-break) {
      width: 35vw;
      max-width: 45rem;
    }
  }

  &__guide {
    margin-bottom: 2rem;

    @include break($sm-break) {
      padding-right: 4.5rem;
      padding-bottom: 0;
    }

    a.guide-carosel__guide-link {
      display: block;
      position: relative;
      overflow: hidden;

      @include break($sm-break) {
        display: inline-block;
      }

      &:before {
        content: '';
        background: linear-gradient(180deg, rgba(0, 0, 0, 0) 30%, rgba(0, 0, 0, 0.7) 90%);
        @include cover;
        z-index: 2;
      }
    }

    img {
      position: relative;
      z-index: 1;
      width: 100%;
    }

    &-title {
      position: absolute;
      bottom: 2.0rem;
      left: 2.0rem;
      z-index: 3;
      color: white;

      @include break($sm-break) {
        bottom: 3.0rem;
        left: 3.0rem;
      }
    }
  }

  &__button {
    display: none;
    @include break($sm-break) {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      z-index: 10;
      padding: 0;
      transition: opacity 0.5s ease;

      &:hover {
        cursor: pointer;
      }

      &.slick-disabled {
        opacity: 0;
      }

      svg g {
        stroke: $white;
      }

      &--prev {
        left: 4rem;
        transform: translateY(-50%) rotate(180deg);
      }

      &--next {
        right: 4rem;
      }
    }
  }
}
