// A-Z Index specific overrides
.swat-link-listing{
  form {
    @extend .u-margin--sm-top;
  }
  .form-item {
    margin-bottom: 0;
  }

  .az-alpha {
    @extend .u-margin--sm-top;
  }
  .az-listing {
    @include list-reset;
  }
  .item-list, .dropdown-menu {
    @extend .u-link-list;

    a {
      display: inline-block;

      &.link-listing-link-edit-link {
        margin-left: 1.2rem;
        font-size: 1.2rem;
        color: $gray-dark;
      }
    }
  }
}
#swat-search-form {
  .control-label {
    @extend .sr-only;
  }
  .form-item {
    margin: 0 0 1.2rem;
  }
  #search-results__form-input { background:none;}

  .search-results--search-label {
    @extend .flyout--search-label;
    color: $gray-dark;
    z-index: -1;
   }
}
#swat-campus-directory-search-form,
#swat-link-listing-search-form,
#swat-search-form {
  > div > .form-item {
    .control-label {
      @extend .sr-only;
    }
  }

  button.input-clear.search-results__form-button,
  button.search-results__form-button, {
    background-color: transparent;
    width: 50px;
    position: absolute;
    top: 0;
    right: 0;
    display: flex;
    align-items: center;
    padding: 0;
    height: 5rem;
    margin-bottom: 1.2rem;
  }

  button.input-clear.search-results__form-button {
    &:before,&:after {
      content: '';
      display: block;
      width: 30px;
      height: 2px;
      background-color: $red;
      position: absolute;
      top: 50%;
      left: 50%;
    }
    &:before {
      transform: translate(-50%,-50%) rotate(45deg);
    }
    &:after {
      transform: translate(-50%,-50%) rotate(-45deg);
    }
  }
}

// Campus Directory specific overrides
#swat-campus-directory-search-form {
  .form-item input + {
    .help-block {
      margin-top: 2rem;
    }
  }
  .search-results__form {
    &-inner{
      border-bottom: 0.2rem solid $gray;
    }
  }
}
