.c-stat-block {

  .additional-content &, .shared-site-content & {
    @extend .container-fluid;
    @extend .col-start-fw;
    @extend .col-sm-10;
    @extend .col-sm-offset-1;
    @extend .u-margin--sm-bottom;
  }
  .basic-content &, .dept-overview__content & {
    @extend .u-margin--sm;
    p {
      font-family: $sans;
    }
  }

  font-family: $sans;
  background-color: $gray;
  padding: 5rem 6rem;
  overflow: hidden;



  &__chart {
    width: 20rem;
    min-width: 20rem;
    height: 20rem;
    position: relative;
    margin: 0 auto 3rem;

    &-whole,
    &-part,
    &-content {
      display: block;
      border-radius: 50% 50%;
      position: absolute;
      top: 0;
      left: 0;
    }

    &-whole,
    &-part {
      height: 100%;
      width: 100%;
      fill: none;
      stroke-width: 80;
    }

    &-whole {
      stroke: $black;
    }

    &-part {
      stroke: $red;
      will-change: stroke-dasharray;
      transform: scaleX(-1) rotate(90deg);
    }

    &-content {
      background-color: $gray;
      width: 70%;
      height: 70%;
      display: flex;
      align-items: center;
      justify-content: center;
      text-align: center;
      padding: 18px;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);

      span {
        display: block;
      }
    }
  }

  &__content {
    overflow-wrap: break-word;
    text-align: center;

    p {
      font-family: $sans !important;
      line-height: 1.5;
    }
  }

  &__title {
    margin-bottom: 2.2rem;
    margin-top: 0 !important;
  }

  &__line {
    display: block;
    width: 4.2rem;
    height: 0.3rem;
    margin: 0 auto 1.6rem;

    // @include break($md-break) {
    //   margin: 0 0 1.6rem;
    // }

    &-inner {
      display: block;
      width: 100%;
      background-color: $red;
      height: inherit;
    }
  }

  svg path {
    will-change: stroke-dashoffset;
  }


  //
  // Breakpoints
  //

  // General use breakpoints
  &__chart {
    @include break($md-break) {
      margin: 0;
      margin-right: 5rem;
    }
  }
  &__content {
    @include break($md-break) {
      text-align: left;
      flex-shrink: 1;
    }
  }
  &__line {
    @include break($md-break) {
      margin: 0 0 1.6rem;
    }
  }
  @include break($md-break) {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  // Unset md-break for these contexts
  .basic-content &,
  .advanced-page-content & {
    &__chart {
      @include break($md-break) {
        margin: 0 auto 3rem;
      }
      @include break($lg-break) {
        margin: 0 5rem 0 0;
      }
    }
    &__content {
      @include break($md-break) {
        text-align: center;
        flex-shrink: 1;
      }
      @include break($lg-break) {
        text-align: left;
        flex-shrink: 1;
      }
    }
    &__line {
      @include break($md-break) {
        margin: 0 auto 1.6rem;
      }
      @include break($lg-break) {
        margin: 0 0 1.6rem;
      }
    }
    @include break($md-break) {
      display: block;
      flex-direction: initial;
      justify-content: initial;
    }
    @include break($lg-break) {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
    }
  }


}
