.academic-depts {
  background-color: $gray;
  padding-bottom: 3rem;

  @include break($sm-break) {
    padding-bottom: 5rem;
  }

  &__list-inner {
    background-color: $white;
    overflow: hidden;
    padding: 3rem;
    margin-top: 3rem;

    @include break($sm-break) {
      padding: 4rem 6rem;
      margin-top: 4.4rem;
    }

    @include break($md-break) {
      padding: 4rem 15%;
    }
  }

  &__title {
    margin-bottom: 2.4rem;

    @include break($sm-break) {
      margin-bottom: 3.4rem;
    }
  }


  &__list-col {

    @include break($xs-break) {
      width: 50%;
      float: left;
      padding-right: 4rem;
    }
  }

  &__list,
  &__subtitle {
    @include list-reset;
    font-size: 1.4rem;
    line-height: 1.125;
    color: $black;

    @include break($md-break) {
      font-size: 1.6rem;
    }
  }

  &__list + &__subtitle {

    &:before {
      margin-top: 3rem;
    }
  }

  &__subtitle {
    display: inline;
    border-bottom: 2px solid $black;
    padding-bottom: 0;
    line-height: 1.5;

    &:before {
      content: '';
      display: block;
    }

    &:after {
      content: '';
      display: block;
      margin-bottom: 0.5rem;
    }
  }

  &__list {

    @include break($xs-sm-break) {
      column-count: 2;
      column-gap: 4rem;
    }

    @include break($xs-sm-break) {
      column-count: 3;
    }

    @include break($sm-md-break) {
      column-count: 2;
    }


    a {
      color: $black;
      display: inline-block;
      padding: 0.5rem 0;

      &:hover {
        color: $red;
        transform: translate3d(5px,0,0);
      }
    }
  }

  &__other {
    margin-top: 3rem;
    // position: relative;

    &-text {
      display: block;
      color: $red;
      font-weight: 600;
      margin-bottom: 1.8rem;
    }

    &-button {

      @include break($sm-break) {
        display: block;
        width: 22rem;
      }

      @include break($md-break) {
        width: 24rem;
      }

      .academic-depts__list & {
        background-color: $red;
        color: $white;
        display: inline-block;
        font-size: 1.4rem;
        font-weight: 600;
        padding: 2rem 2.2rem;
        overflow: hidden;

        @include break($md-break) {
          font-size: 1.6rem;
          padding: 2.6rem;
        }

        span {
          transition: 0.3s ease-in-out;
          display: inline-block;
        }

        &-text {

          span {
            margin-left: 5px;
          }
        }

        &:hover {
          color: $white;
          transform: translate3d(0,0,0);

          .academic-depts__other-button-text {

            &,
            & span {
              transform: translate3d(5px,0,0);
            }
          }
        }

        svg g {
          fill: $white;
        }
      }
    }
  }


  &__aside {
    display: flex;
    flex-direction: column;
    position: relative;
    align-items: flex-start;
    margin: 3rem auto 0;

    @include break($sm-break) {
      margin-top: 5rem;
    }
    @include break($sm-md-break-max) {
      max-width: 50rem;
    }

    @include break($sm-md-break) {
      margin: 10rem 0 0 -9%;
    }

    @include break($xl-break) {
      max-width: 50rem;
    }

    &:hover {

      .academic-depts__aside-line-outer {
        animation: drawsWidthAgain 0.8s $quickSnap forwards;
      }
    }

    &-header {
      position: relative;
      width: 100%;
      flex-shrink: 0;
    }

    &-label {
      position: absolute;
      display: inline-block;
      transform-origin: right bottom;
      bottom: 0;
      right: 0;
      transform: rotate(-90deg) translate(100%, 2.4rem);
    }

    &-image {
      order: 1;
      width: 100%;
    }

    &-content {
      order: 2;
      width: 100%;

      @include break($sm-md-break) {
        padding-left: 20%;
      }
    }

    &-title {
      color: $black;
      margin-top: 2.5rem;
      position: relative;
    }

    &-line {
      display: inline-block;
      position: relative;
      width: 4rem;
      height: 0.2rem;
      overflow: hidden;
      bottom: 1rem;
      margin-left: 1rem;

      br {
        display: none;
      }

      @include break($sm-break) {
        width: 5rem;
        height: 0.3rem;
      }

      @include break($sm-md-break) {
        left: auto;
        top: auto;
        bottom: 1.2rem;

        br {
          display: block;
        }
      }

      @include break($md-break) {
        bottom: 1.6rem;
      }

      &-outer,
      &-inner {
        width: 100%;
        height: inherit;
        display: block;
      }

      &-inner {
        background-color: $red;
      }
    }

    &-excerpt {
      color: $black;
      margin-top: 1.6rem;
    }

    &-cta {
      margin-top: 2rem;
    }

    &-excerpt,
    &-cta {
      margin-left: 5rem;
    }
  }
}
