/**
 * Breakpoint Sizes
 * for use in flexgrid and break() mixin
 * 
 */
$xs-break:    (480 / 16)  * 1em;
$xs-sm-break: (620 / 16)  * 1em;
$sm-break:    (750 / 16)  * 1em;
$sm-md-break: (970 / 16)  * 1em;
$md-break:    (1100 / 16) * 1em;
$lg-break:    (1280 / 16) * 1em;
$xl-break:    (1400 / 16) * 1em;

// Max Width
$xs-break-max: (max-width: $xs-break);
$sm-break-max: (max-width: $sm-break);
$sm-md-break-max: (max-width: $sm-md-break);
$md-break-max: (max-width: $md-break);
$lg-break-max: (max-width: $lg-break);
$xl-break-max: (max-width: $xl-break);

/**
 * Breakpoint Container Sizes
 * The size of the .container class at the matching breakpoint
 * 
 */
$xs-break-container: ($xs-break - 2.0em);
$sm-break-container: ($sm-break - 2.0em);
$sm-md-break-container: ($sm-md-break - 2.0em);
$md-break-container: ($md-break - 2.0em);
$lg-break-container: ($lg-break - 2.0em);
$xl-break-container: ($xl-break - 2.0em);

/**
 * Max width for .wrapper
 * 
 */
$site-max-width: 175.0rem;


/**
 * Max width for inner containers (.container and .container-fluid)
 * 
 */
$container-max-width: 144.0rem;

/**
 * Flexbox Grid Variable
 * 
 */
$flexboxgrid-grid-columns: 12;
$flexboxgrid-gutter-width: 4rem;


$flexboxgrid-outer-margin: 5rem;

$flexboxgrid-max-width: $site-max-width;

/**
 * Breakpoint variable for Flexbox Grid.
 * Syntax: `name | breakpoint | .container width` when `name` coordinates
 * with the class naming structure `sm` = `.col-sm-6`, etc.
 * 
 */
$flexboxgrid-breakpoints:
  xs $xs-break $xs-break-container,
  sm $sm-break $sm-break-container,
  sm-md $sm-md-break $sm-md-break-container,
  md $md-break $md-break-container,
  lg $lg-break $lg-break-container,
  xl $xl-break $xl-break-container;

/**
 * Flexbox Grid calculation variables - DO NOT EDIT
 * 
 */
$gutter-compensation: $flexboxgrid-gutter-width * .5 * -1;
$half-gutter-width: $flexboxgrid-gutter-width * .5;