.site-header {
  font-family: $sans;
  z-index: 9998;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  transition: all 0.8s cubic-bezier(.5,0,.5,1);
  background-color: $white;

  @include break($sm-break) {
    font-size: 1.4rem;
  }

  @include break($sm-md-break) {
    z-index: 9998 ;
  }

  @include break($md-break) {
    font-size: 1.5rem;
  }

  &__hide &, &__hide &__drawer {
    transform: translate3d(0, -80px, 0);
    opacity: 0;
  }

  &__hide.no-animation &, &__hide.no-animation &__drawer{
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }

  &__drawer {
    display: none;
    .site-header__hide & {
      transition: all 0.8s cubic-bezier(.5,0,.5,1);
    }

    @include break($sm-md-break) {
      display: block !important;
      width: 100%;
      position: fixed;
      background-color: $white;
      top: 6.3rem;
      left: 0;
      z-index: 9997;
    }

    @include break($md-break) {
      top: 7.5rem;
    }
  }

  &__mask {
    position: relative;
    z-index: 2;
  }

  &__container {
    background-color: $white;
    padding-top: 1rem;
    padding-bottom: 1rem;

    @include break($sm-md-break) {
      padding-top: 1.1rem;
      padding-bottom: 1.1rem;
    }

    @include break($md-break) {
      padding-top: 1.5rem;
      padding-bottom: 1.6rem;
    }
  }

  &__row {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__logo {
    font-weight: 600;
    text-transform: uppercase;
    flex-grow: 2;
    padding-right: 2rem;
    text-align: left;
    display: flex;
    align-items: center;

    svg {
      display: block;
    }

    a {
      display: inline-block;
      color: transparentize($black, 0.5);
    }

    svg {
      width: 14.5rem;
      height: 1.6rem;

      @include break($xs-break) {
        width: 18.0rem;
        height: 1.9rem;
      }

      @include break($sm-md-break) {
        width: 21.0rem;
        height: 2.2rem;
      }
    }
  }

  &__main-nav {

    @include break($sm-md-break) {
      flex-grow: 3;
    }
  }
}

.skip-nav {
  transition: none;
}

.skip-nav:focus {
  border: 1px solid $gray;
  clip: auto;
  clip-path: none;
  height: auto;
  margin: auto;
  overflow: hidden;
  padding: 1rem 1rem 1.2rem;
  position: fixed !important;
  width: auto;
  word-wrap: normal !important;
  background-color: $red !important;
  font-size: 1.6rem;
  top: 0;
  left: 0;
  z-index: 9999;
  outline: none;
  color: $white !important;
  font-weight: 600;
  outline: none !important;
}

