.js {

  .u-fades-in {
    opacity: 0;

    &--from-right.u-in-view {
      @include u-fades-in--from-right;
    }

    &--from-left.u-in-view {
      @include u-fades-in--from-left;
    }

    &--from-top.u-in-view {
      @include u-fades-in--from-top;
    }

    &--from-below.u-in-view {
      @include u-fades-in--from-below;
    }

    &--from-below--big.u-in-view {
      @include u-fades-in--from-below--big;
    }

    &--in-place.u-in-view {
      @include u-fades-in--in-place;
    }

    &.u-delay {
      animation-delay: 0.75s;
    }
  }

  .u-fade-stagger {
    @include u-animation-stagger;
  }

  .u-draws-width {
    width: 0;

    &.u-in-view {
      animation: drawsWidth 1s ease 0.2s forwards;

      &.u-delay {
        animation-delay: 0.75s;
      }
    }
  }

  .u-slides-in {

    &--from-left {
      transform: translate3d(-100%,0,0);

      &.u-in-view {
        @include u-slide-in--from-left;
      }
    }

    &--from-below {
      // transform: translate3d(0,1,0);

      &.u-in-view {
        @include u-slide-in--from-below(1s);
      }
    }
  }

  .u-stagger-delay {
    @for $i from 1 through 16 {
      &:nth-of-type(#{$i}) {
        animation-delay: 0.2s + (0.15s * $i);
      }
    }
  }
}

