// TODO: Need to refactor some of these content/utility classes and how they interact with .dept-overview/.basic-content.

.u-serif {
  font-family: $serif;
}

.u-paragraph-default {
  @include style-paragraph;
}

.u-p-lead {
  // font-family: $serif;
  font-size: 2.0rem;
  line-height: 1.6;
}

.u-section-label {
  @include style-cta;
  letter-spacing: 0.5px;
  color: $black;
  text-decoration: none;

  svg {
    margin-left: 1rem;
  }
}

.u-tag-label,
.dept-overview__content p.u-tag-label,
.basic-content p.u-tag-label {
  font-family: $sans;
  font-size: 1.2rem;
  line-height: 1.5;
  font-weight: 600;
  letter-spacing: 1.3px;
  text-transform: uppercase;
  color: $black;
  text-decoration: none;

  a {
    color: $black;
    display: block;
    margin-top: 0;
  }
}

.u-tiny-label,
.dept-overview__content p.u-tiny-label,
.basic-content p.u-tiny-label {
  font-family: $sans;
  font-size: 1rem;
  letter-spacing: 0.1rem;
  text-transform: uppercase;
  color: $red;
  text-decoration: none;
  font-weight: 600;

  @include break($sm-break) {
    font-size: 1.2rem;
  }

  a {
    color: $red;
    text-decoration: none;
  }
}

.u-subtitle,
.dept-overview__content p.u-subtitle,
.basic-content p.u-subtitle {
  font-family: $sans;
  line-height: 1.4;
  font-weight: 500;

  @include break($sm-break) {
    font-size: 1.9rem;
  }

  @include break($md-break) {
    font-size: 2.4rem;
  }
}

.u-subtitle-normal,
.dept-overview__content p.u-subtitle-normal,
.basic-content p.u-subtitle-normal {
  @extend .u-subtitle;
  font-weight: 400;
}

.u-subtitle-sm,
.dept-overview__content p.u-subtitle-sm,
.basic-content p.u-subtitle-sm {
  font-family: $sans;
  line-height: 1.4;
  font-weight: 500;
  font-size: 1.4rem;

  @include break($sm-break) {
    font-size: 1.6rem;
  }

  @include break($md-break) {
    font-size: 2.0rem;
  }
}

.u-caption,
.dept-overview__content p.u-caption,
.basic-content p.u-caption {
  font-size: 1.2rem;
  color: $gray-dark;
  font-family: $sans;
  line-height: 1.3;

  @include break($sm-break) {
    font-size: 1.4rem;
  }
}

.js {

  .u-title-line {
    position: relative;
    //overflow: hidden;
    line-height: 98%;
    will-change: opacity, transform;

    &--inner {
      opacity: 0;
      will-change: opacity, transform;
    }
  }
}

.u-hr {
  display: block;
  background-color: $gray;
  height: 0.3rem;
  margin: 3rem 0;
  border: 0;
}

// pullquotes
.u-pullquote {
  margin: 5rem 0 4.5rem;
  padding-top: 0;
  font-family: $sans;
  font-size: 2.4rem;
  font-weight: 600;
  line-height: 1.3;
  color: $red;
  position: relative;

  @include break($sm-break) {
    font-size: 3rem;
  }

  @include break($md-break) {
    font-size: 3.6rem;
  }
}
.basic-content .image-callout,
.advanced-page-content .image-callout {
  .u-pullquote {
    margin: 0;
    font-size: 1.8rem;
    color: $red;
    position: relative;
    @include break($sm-break) {
      font-size: 2rem;
    }
    @include break($md-break) {
      font-size: 2.4rem;
    }
  }
}
.full-width-callout,
.basic-content .full-width-callout,
.advanced-page-content .full-width-callout {
  .u-pullquote {
    color: $white;
    font-size: 2.0rem;
    @include break($sm-break) {
      font-size: 2.4rem;
    }
    @include break($md-break) {
      font-size: 2.8rem;
    }
  }
}
