#block-swat-hooks-critical-announcement.block {
	margin: 0;
	padding: 0;
  .critical-announcement-wrapper{
		margin: 0;
		padding: 0;
		display: none;
    .critical-announcement{
      border: 1px solid $red;
    	background-color: #F6E8E8;
			.critical-announcement-inner{
				position: relative;
	      padding: 12px 0 12px 36px;
				min-height: 32px;
	      h3 {
	        color: $red;
	      }
	      .form-error__alert-icon {
					position: absolute;
	        top: 18px;
					left: 6px;
					@include break($xs-sm-break) {
	        	left: -4px;
					}
	      }
			}
    }
  }
}
