.visually-hidden,
.element-invisible,
.element-focusable,
.breadcrumb__title{
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden;
  clip: rect(1px, 1px, 1px, 1px);
  word-wrap: normal;
}

.image-align{
  &-left {
    float:left;
    margin-right: 12px;
    margin-bottom: 6px;
    margin-top: 4px;
  }
  &-right {
    float:right;
    margin-left: 12px;
    margin-bottom: 6px;
    margin-top: 4px;
  }
}

// back-support for old image alignment classes
.mainimage {
  background: #eeeeee;
  border: 1px solid #eeeeee;
  padding: 6px;
  max-width: 100%;
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 6px;
  font-size: small;
  letter-spacing: .0375em;
  img {
    float: none !important;
    margin: 0 auto !important;
    padding: 0 !important;
    max-width: 100% !important;
  }
  &.Floating_Left,
  &.Floating_Right {
    max-width: 100% !important;
    float: none;
    @include break($sm-break) {
      max-width: 40% !important;
    }
  }

  &.Floating_Left {
    @include break($sm-break) {
      float: left;
      margin-right: 12px;
    }
  }

  &.Floating_Right {
    @include break($sm-break) {
      float: right;
      margin-left: 12px;
    }
  }

  p + & {
    margin-top: 3rem;
  }
  p + & + p {
    padding-top: 3rem;
  }
}
