.basic-page-header {
  padding: 2.5rem 0;
  margin-bottom: 0;

  @include break($sm-break) {
    // TODO: Remove the bg color if the current garnet-theme solution stands
    // background-color: $gray;
    margin-bottom: 7rem;
    padding: 0;
  }

  @include break($sm-md-break) {
    margin-bottom: 3rem;
  }

  &__wrapper.basic-page-header--left ,
  &__wrapper.basic-page-header--right {
    flex-direction: column-reverse;
  }
  &__wrapper.basic-page-header--left {
    @include break($sm-break) {
      flex-direction: row-reverse;
    }
  }
  &__wrapper.basic-page-header--right {
    @include break($sm-break) {
      flex-direction: row;
    }
  }

  .basic-page-header__image {
    position: relative;
    margin-bottom: 1.4rem;
    min-height: 1px;

    @include break($sm-break) {
      margin: -1rem 0 -3rem;
    }

    // &:after {
    //   content: '';
    //   @include cover;
    //   background: $img-bkg-gradient;

    //   @include break($sm-break) {
    //     display: none;
    //   }
    // }

    img {
      @include break($md-break) {
      }
    }
  }

  &--left {
    .basic-page-header__content {
      position: relative;
      z-index: 2;

      @include break($sm-break) {
        color: $black;
      }
    }

    .basic-page-header__title {
      position: relative;
      padding-left: 6.0rem;

      // @include break($xs-break-max) {
      //   font-size: 2.0rem;
      // }

      @include break($sm-break) {
        padding-right: 2rem;
        padding-left: 0;
        animation-delay: 0.8s !important;
      }
    }

    .basic-page-header__line {
      display: block;
      height: 0.2rem;
      width: 3.8rem;
      position: absolute;
      left: 0;
      top: 1.6rem;

      @include break($sm-break) {
        width: 15.0rem;
        top: 1.8rem;
        left: -17rem;
        bottom: auto;
      }

      @include break($md-break) {
        width: 25.0rem;
        left: -27.5rem;
        height: 0.3rem;
        top: 2.4rem;
      }

      .basic-page-header__line-inner {
        display: block;
        background-color: $red;
        width: 100%;
        height: inherit;

        @include break($sm-break) {
          float: right;
          animation-delay: 1.4s !important;
        }

        .js & {
          width: 0;
        }

        .js .no-animation .basic-page-header__line-inner {
          width: 100%;
        }
      }
    }
  }
  &--right {
    .basic-page-header__content {
      position: relative;
      z-index: 1;

      p {
        padding-left: 5rem;
        @include break($sm-md-break) {
          padding-left: 9rem;
        }
        @include break($md-break) {
          padding-left: 16%;
        }
        @include break($xl-break) {
          padding-left: 10%;
        }
      }
    }

    .basic-page-header__title {
      position: relative;
      display: inline-block;
      margin-bottom: 1.8rem;
      padding-left: 5rem;

      @include break($md-break) {
        padding-left: 0;
      }
    }

    .basic-page-header__line {
      display: block;
      width: 4rem;
      height: 0.2rem;
      position: absolute;
      top: 1.4rem;
      left: 0;

      @include break($sm-break) {
        width: 20.0rem;
        right: ((20.0rem + 2rem) * -1);
        left: auto;
        top: 2rem;
      }

      @include break($md-break) {
        display: block;
        width: 21.4rem;
        height: 0.3rem;
        top: 2.4rem;
        right: ((21.4rem + 2rem) * -1);
      }

      .basic-page-header__line-inner {
        display: block;
        height: inherit;
        width: 100%;
        background-color: $red;

        .js & {
          width: 0;
        }

        .js .no-animation .basic-page-header__line-inner {
          width: 100%;
        }

        &.u-in-view {
          @include u-draws-width(1s, 0.6s);
        }
      }
    }

    // .basic-page-header__image {
    //   margin-bottom: 3rem;
    //   min-height: 1px;
    //
    //   @include break($sm-md-break) {
    //     margin: -1rem 0 0 0;
    //     width: inherit;
    //   }
    //
    //   @include break($lg-break) {
    //     margin: -2rem 0 -3rem;
    //   }
    // }
  }
}
