.u-padding {
  &--sm {
    padding-top: 3rem;
    padding-bottom: 3rem;

    @include break($sm-break) {
      padding-top: 4rem;
      padding-bottom: 4rem;
    }

    &-top {
      padding-top: 3rem;

      @include break($sm-break) {
        padding-top: 4rem;
      }
    }

    &-bottom {
      padding-bottom: 3rem;

      @include break($sm-break) {
        padding-bottom: 4rem;
      }
    }
  }

  &--md {
    padding-top: 3rem;
    padding-bottom: 3rem;

    @include break($sm-break) {
      padding-top: 6rem;
      padding-top: 6rem;
    }

    &-top {
      padding-top: 3rem;

      @include break($sm-break) {
        padding-top: 6rem;
      }
    }

    &-bottom {
      padding-bottom: 3rem;

      @include break($sm-break) {
        padding-bottom: 6rem;
      }
    }
  }

  &--lg {
    padding-top: 4rem;
    padding-bottom: 4rem;

    @include break($sm-break) {
      padding-top: 8rem;
      padding-top: 8rem;
    }

    &-top {
      padding-top: 4rem;

      @include break($sm-break) {
        padding-top: 8rem;
      }
    }

    &-bottom {
      padding-bottom: 4rem;

      @include break($sm-break) {
        padding-bottom: 8rem;
      }
    }
  }


  &--xl {
    padding-top: 4rem;
    padding-bottom: 4rem;

    @include break($sm-break) {
      padding-top: 8rem;
      padding-top: 8rem;
    }

    @include break($sm-md-break) {
      padding-top: 10rem;
      padding-top: 10rem;
    }

    &-top {
      padding-top: 4rem;

      @include break($sm-break) {
        padding-top: 8rem;
      }

      @include break($sm-md-break) {
        padding-top: 10rem;
      }
    }

    &-bottom {
      padding-bottom: 4rem;

      @include break($sm-break) {
        padding-bottom: 8rem;
      }

      @include break($sm-md-break) {
        padding-bottom: 10rem;
      }
    }
  }
  &--none {
    padding-top: 0rem;
    padding-bottom: 0rem;

    @include break($sm-break) {
      padding-top: 0rem;
      padding-bottom: 0rem;
    }

    &-top {
      padding-top: 0rem;

      @include break($sm-break) {
        padding-top: 0rem;
      }
    }

    &-bottom {
      padding-bottom: 0rem;

      @include break($sm-break) {
        padding-bottom: 0rem;
      }
    }
  }
}
