.u-clip-hover {
  transition: $trans-clip-hover;
  clip-path: inset(0);

  &:hover {
    clip-path: $clip-path-hover;
  }
}

.u-clip-hover--child-img {

  img {
    clip-path: inset(0);
    transition: 0.4s ease-in-out;
  }

  &:hover img {
    clip-path: $clip-path-hover
  }


}

.u-clip-hover--scale-child-img {
  transition: clip-path 0.3s $cubic-title;
  overflow: hidden;
  clip-path: inset(0);
  display: block;

  img {
    transition: transform 0.4s $cubic-title;
  }

  &:hover {
    clip-path: $clip-path-hover;

    img {
      transform: scale(1.03);
      transition: transform 0.4s $cubic-title;
    }
  }
}

.u-clip-hover--scale-child-img--target {

  .u-clip-hover--target {
    transition: clip-path 0.3s $cubic-title;
    overflow: hidden;
    clip-path: inset(0);
    display: block;
  }

  img {
    transition: transform 0.4s $cubic-title;
  }

  &:hover {

    .u-clip-hover--target {
      clip-path: $clip-path-hover;

      img {
        transform: scale(1.03);
        transition: transform 0.4s $cubic-title;
      }
    }
  }

  // Stat Lockup
  .stat-lockup__front {

    @include break($md-break) {
      margin-bottom: 15rem;
    }
  }
}