.u-margin {
  &--sm {
    margin-top: 3rem;
    margin-bottom: 3rem;

    @include break($sm-break) {
      margin-top: 4rem;
      margin-bottom: 4rem;
    }

    &-top {
      margin-top: 3rem;

      @include break($sm-break) {
        margin-top: 4rem;
      }
    }

    &-bottom {
      margin-bottom: 3rem;

      @include break($sm-break) {
        margin-bottom: 4rem;
      }
    }
  }

  &--md {
    margin-top: 3rem;
    margin-bottom: 3rem;

    @include break($sm-break) {
      margin-top: 6rem;
      margin-top: 6rem;
    }

    &-top {
      margin-top: 3rem;

      @include break($sm-break) {
        margin-top: 6rem;
      }
    }

    &-bottom {
      margin-bottom: 3rem;

      @include break($sm-break) {
        margin-bottom: 6rem;
      }
    }
  }

  &--lg {
    margin-top: 4rem;
    margin-bottom: 4rem;

    @include break($sm-break) {
      margin-top: 8rem;
      margin-top: 8rem;
    }

    &-top {
      margin-top: 4rem;

      @include break($sm-break) {
        margin-top: 8rem;
      }
    }

    &-bottom {
      margin-bottom: 4rem;

      @include break($sm-break) {
        margin-bottom: 8rem;
      }
    }
  }

  &--xl {
    margin-top: 4rem;
    margin-bottom: 4rem;

    @include break($sm-break) {
      margin-top: 8rem;
      margin-top: 8rem;
    }

    @include break($sm-md-break) {
      margin-top: 10rem;
      margin-top: 10rem;
    }

    &-top {
      margin-top: 4rem;

      @include break($sm-break) {
        margin-top: 8rem;
      }

      @include break($sm-md-break) {
        margin-top: 10rem;
      }
    }

    &-bottom {
      margin-bottom: 4rem;

      @include break($sm-break) {
        margin-bottom: 8rem;
      }

      @include break($sm-md-break) {
        margin-bottom: 10rem;
      }
    }
  }

  &--none {
    margin-top: 0rem;
    margin-bottom: 0rem;

    @include break($sm-break) {
      margin-top: 0rem;
      margin-bottom: 0rem;
    }

    &-top {
      margin-top: 0rem;

      @include break($sm-break) {
        margin-top: 0rem;
      }
    }

    &-bottom {
      margin-bottom: 0rem;

      @include break($sm-break) {
        margin-bottom: 0rem;
      }
    }
  }
}
