.three-col-callout {
  position: relative;
  margin-top: 5rem;
  margin-bottom: 0;

  .container-fluid {
    position: relative;
    z-index: 1;
    padding-bottom: 4rem;

    @include break($sm-break) {
      padding-bottom: 7rem;
    }
  }

  &__label {
    margin: 0 auto 1.4rem;
    max-width: 30rem;

    @include break($sm-break) {
      max-width: none;
      position: absolute;
      top: 0;
      transform-origin: top left;
      transform: rotate(-90deg) translate(-100%, -24px);
    }

    @include break($md-break) {
      left: 13.5%;
      transform: rotate(-90deg) translate(-100%, 0);
    }
  }

  &__col {
    margin-bottom: 2.4rem;

    @include break($sm-break) {
      margin-bottom: 0;
    }

    &-inner {
      display: flex;
      align-items: flex-start;
      flex-direction: column-reverse;
      text-align: left;
      margin: 0 auto;
      max-width: 30rem;

      @include break($sm-break-max) {
        opacity: 1 !important;
        transform: translate3d(0,0,0) !important;
        animation: none !important;
      }

      @include break($sm-break) {
        max-width: none;
      }
    }
  }

  &__title {
    color: $black;
    transition: 0.35s ease;

    &:hover {
      transform: translate3d(5px,0,0);
    }
  }

  &__subtitle {
    color: $red;
    margin-bottom: 0.6rem;
    line-height: 1.6;
    width: 100%;

    @include break($sm-break) {
      margin-bottom: 1rem;
    }
  }

  &__image {
    margin-bottom: 1.6rem;
    width: 100%;

    @include break($sm-break) {
      margin-bottom: 2.4rem;
    }
  }

  &__color-block {
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    top: 17rem;
  }

  &__footer {

    .u-cta-regular {
      text-align: center;
      display: block;
      margin-top: 1rem;

      @include break($sm-break) {
        margin-top: 3rem;
      }

      @include break($md-break) {
        margin-top: 5rem;
      }
    }
  }
}