.flyout--search {
  padding-top: 5rem;
  background-color: $red;
  color: $white;

  @include break($sm-md-break) {
    padding-top: 0;
  }

  &.modal__open {

    @include break($sm-md-break) {
      display: flex;
      align-items: center;
    }
  }

  &-form {

    &-wrapper {
      position: relative;
    }

    &-inner {
      border-bottom: 1px solid $black;
      position: relative;
      // padding-bottom: 1rem;
      border-color: $white;

      @include break($sm-md-break) {
        padding-bottom: 1rem;
      }
    }
  }

  &-label,
  input,
  &-input {
    font-size: 2.0rem;
    font-family: $sans;
    min-height: 3.0rem;
    margin-bottom: 0;
    color: $white;

    @include break($sm-md-break) {
      font-size: 4.0rem;
      min-height: 6.0rem;
      letter-spacing: 0.05rem;
    }
  }

  &-input::-ms-clear {
    display:none;
  }

  &-label {
    font-weight: 600;
    position: absolute;
    top: 0.6rem;
    left: 0;
    transition: all 0.4s $cubic-ease;
    transform-origin: top left;

    &.active-input {
      transform: scale(0.6);
      top: -1rem;

      @include break($sm-md-break) {
        transform: scale(0.4);
        top: -3rem;
      }
    }
  }

  input,
  &-input {
    background-color: transparent;
    border: 0;
    border-radius: 0;
    width: 100%;
    font-weight: 500;
    position: relative;
    z-index: 2;
    color: $white;
    padding-left: 0;
    padding-right: 0;
    background-image: none !important;

    @include break($sm-md-break) {

    }
  }

  button[type="submit"],
  &-button {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    display: block;
    padding: 0.5rem 0.5rem 0 0;
    background-color: transparent !important;
    z-index: 3;

    @include break($sm-md-break) {
      width: 10rem;
      padding: 1rem 0 1.8rem;
    }

    svg {
      fill: $white;
      height: 2.4rem;
      width: 2.4rem;

      @include break($sm-md-break) {
        height: 4.4rem;
        width: 4.4rem;
      }
    }
  }
  &-directory {
    padding-top: 1.5rem;

    a {
      margin-top: .5rem;
      display: inline-block;
      position: relative;
      color: $white;
      font-weight: 600;

      @include break($xs-sm-break) {
        margin-top: 0;
        padding-left: 1rem;
      }

      &:after,
      &:before {
        content: '';
        display: block;
        height: 1px;
        width: 5px;
        position: absolute;
        right: -1.5rem;
        background-color: $black;
        transition: transform 0.5s $cubic-ease;
        backface-visibility: hidden;
        background-color: $white;
      }
      &:before {
        transform: rotate(50deg);
        top: 9px;
        @include break($sm-break) {
          top: 10px;
        }
      }
      &:after {
        transform: rotate(-50deg);
        top: 12px;
        @include break($sm-break) {
          top: 13px;
        }
      }
      &:hover {

        transform: translate3d(5px,0,0);

        &:before {
          transform: translateX(6px) rotate(50deg);
        }

        &:after {
          transform: translateX(6px) rotate(-50deg);
        }
      }
    }
  }
  &-links {
    margin-top: 5rem;

    &-title {
      margin-bottom: 2.0rem;
      font-size: 1.2rem;
      text-transform: uppercase;
      font-weight: 500;
      letter-spacing: 0.1rem;

      @include break($sm-break) {
        margin-bottom: 3.4rem;
      }
    }

    &-list {
      @include list-reset;
      font-size: 1.2rem;

      @include break($sm-break) {
        font-size: 1.6rem;
        column-count: 2;
        column-gap: 3rem;
      }
      @include break($sm-md-break) {
        column-count: 3
      }

      a {
        padding: 0.3rem 0 0.3rem 0;
        display: inline-block;
        position: relative;
        font-weight: 400;
        color: $white;

        &:after,
        &:before {
          content: '';
          display: block;
          height: 1px;
          width: 5px;
          position: absolute;
          right: -1.5rem;
          background-color: $black;
          transition: transform 0.5s $cubic-ease;
          backface-visibility: hidden;

          // @include break($sm-md-break) {
            background-color: $white;
          // }
        }

        &:before {
          transform: rotate(50deg);
          top: 8px;
          @include break($sm-break) {
            top: 11px;
          }
        }

        &:after {
          transform: rotate(-50deg);
          top: 11px;
          @include break($sm-break) {
            top: 14px;
          }
        }

        &:hover {

          &:before {
            transform: translateX(6px) rotate(50deg);
          }

          &:after {
            transform: translateX(6px) rotate(-50deg);
          }
        }
      }
      // Override the "ban" icon's fill
      svg {
        fill: $white;
      }
    }
  }

  .st-search-container {
    position: absolute;
    top: 100%;
  }
}
