%pagination_content {
  margin: 5rem 0 0;
  text-align: center;
  white-space: nowrap;
  width: 100%;
}

%pagination_num_button {
  display: inline-block;
  color: $black;
  padding: 0.5rem 0.5rem;
  margin: 0;

  &:hover {
    color: $red;
  }
}

%pagination_num {
  padding: 0.5rem;
  margin: 0 0.3rem;

  @include break($xs-break) {
    margin: 0 0.5rem;
  }

  @include break($sm-break) {
    margin: 0 0.8rem;
  }

  @include break($lg-break) {
    margin: 0 1.6rem;
  }
}

%pagination_button {
  padding: 0.5rem 1.2rem;
  background-color: $gray;
  text-decoration: none !important;

  @include break($sm-break) {
    padding: 1.2rem 2rem;
    margin: 0 1.2rem;
  }
}

.c-pagination {

  &__item {
    display: inline-block;
  }

  &__content {
    @extend %pagination_content;
    padding: 0;
  }

  &__number,
  &__button {
    @extend %pagination_num_button;
  }

  &__number {
    @extend %pagination_num;
  }

  &__sep:hover {
    color: $black !important;
  }

  &__button {
    @extend %pagination_button;
  }

  &__active {
    color: $red;
    text-decoration: none;
  }
}
