//top navigation contentual links and region
.site-header__main-nav {
  .contextual-links-region{
    display: none;

    @include break($sm-md-break) {
      display: block;
    }

    a.contextual-links-trigger {
      display: none;
    }

    &:hover{
      a.contextual-links-trigger {
        display: inline-block;
      }
    }
  }
}

.site-footer__links {
  .contextual-links-wrapper {
    li {
      border: none;
      a {
        margin: 0.25em 0;
        padding: 0.25em 1em 0.25em 0.5em;
      }
    }
    a {
      &:hover, &:focus{
        -webkit-transform: none;
        transform: none;
      }
      &.contextual-links-trigger{
        display: none;
        margin: 0;
        padding: 0 2px;
        &.contextual-links-trigger-active {
          display: block;
        }
      }
    }
  }
}

.basic-page__sidebar {
  .c-sub-nav {
    &.contextual-links-region {
      padding-top: 20px;
      margin-top: -20px;
      @include break($sm-md-break) {
        padding-top: 0;
        margin-top: 0;
      }
    }
  }
}
.dept-overview__sidebar {
  .c-sub-nav {
    &.contextual-links-region {
      padding-top: 20px;
      margin-top: -20px;
      @include break($sm-break) {
        padding-top: 0;
        margin-top: 0;
      }
    }
  }
}
.c-sub-nav {
  .contextual-links-wrapper {
    a {
      &:hover, &:focus{
        -webkit-transform: none;
        transform: none;
      }
      &.contextual-links-trigger{
        display: none;
        margin: 0;
        padding: 0 2px;
        &.contextual-links-trigger-active {
          display: block;
        }
      }
    }
  }
}

.basic-content {
  > .contextual-links-region {
    > *:nth-child(2) {
      margin-top: 0 !important;
    }
  }
  .contextual-links li {
    a {
      text-decoration: none;
    }
  }
}
