@keyframes fadesInInPlace {
  from { opacity: 0; }
  to   { opacity: 1; }
}

@keyframes fadesInFromRight {
  from { opacity: 0; transform: translate3d(2rem, 0, 0); }
  to   { opacity: 1; transform: translate3d(0, 0, 0); }
}

@keyframes fadesInFromLeft {
  from { opacity: 0; transform: translate3d(-2rem, 0, 0); }
  to   { opacity: 1; transform: translate3d(0, 0, 0); }
}

@keyframes fadesInFromTop {
  from { opacity: 0; transform: translate3d(0, -100%, 0); }
  to   { opacity: 1; transform: translate3d(0, 0, 0); }
}

@keyframes fadesInFromBelow {
  from { opacity: 0; transform: translate3d(0, 2.5rem, 0); }
  to   { opacity: 1; transform: translate3d(0, 0, 0); }
}

@keyframes fadesInFromBelowBig {
  0% { opacity: 0; transform: translate3d(0, 20rem, 0); }
  50% { opacity: 1;}
  100%   { opacity: 1; transform: translate3d(0, 0, 0); }
}

@keyframes drawsWidth {
  from { width: 0; }
  to   { width: 100%; }
}

@keyframes drawsWidthAgain {
  0%   { transform: translate3d(0, 0, 0); }
  40%  { transform: translate3d(100%, 0, 0); opacity: 1; }
  50%  { transform: translate3d(100%, 0, 0); opacity: 0; }
  51%  { transform: translate3d(-100%, 0, 0); opacity: 0; }
  52%  { transform: translate3d(-100%, 0, 0); opacity: 1; }
  100% { transform: translate3d(0, 0, 0); }
}

@keyframes slideInFromLeft {
  from { transform: translate3d(-100%, 0, 0); }
  to   { transform: translate3d(0, 0, 0); }
}

@keyframes slideInFromBelow {
  from { transform: translate3d(0,15rem,0); }
  to   { transform: translate3d(0, 0, 0); }
}

@keyframes unmasksFromLeft {
  from { clip-path: inset(0 100% 0 0); }
  to   { clip-path: inset(0 0 0 0); }
}

@keyframes calBarHeader {
  from {transform: translate3d(-100%,0,0); opacity: 0;}
  to   {transform: translate3d(0,0,0); opacity: 1;}
}

@keyframes calBarItemDown {
  from {transform: translate3d(0, 15rem,0); opacity: 0;}
  to   {transform: translate3d(0,0,0); opacity: 1;}
}