.dept-header-full {
  color: $white;
  padding-bottom: 23.0rem;
  margin-bottom: -23.0rem;

  &__content {
    padding-bottom: 3.5rem;
    max-width: 30.0rem;

    @include break($sm-break) {
      max-width: 37.5rem;
      margin-left: 4.5rem;
    }

    @include break($sm-md-break) {
      margin-left: 8.5%;
    }

    @include break($md-break) {
      max-width: 47.5rem;
      margin-left: 11.5%;
    }

    @include break($lg-break) {
      margin-left: 10.5%;
    }

    &-wrapper {
      height: 80vh;
      max-height: 45.0rem;
    }
  }

  &__subtitle {
    margin-top: 1.6rem;
    margin-bottom: 1rem;
  }
}
