.lead {
  margin: 5rem auto;

  @include list-basic;

  @include break($md-break) {
    margin: 8rem auto;
  }

  p {
    font-size: 1.8rem;
    font-weight: 400;
    line-height: 1.8;
    font-family: $sans;

    @include break($md-break) {
      font-size: 2.4rem;
    }
  }

  &__cta {
    display: flex;
    margin-top: 2.4rem;
    font-size: 1.4rem;
    font-weight: 400;
    align-items: center;
    transition-duration: 0.5s;
    backface-visibility: hidden;

    @include break($md-break) {
      font-size: 1.6rem;
    }

    span {
      display: inline-block;
      margin-left: 1rem;
      transition: transform 0.5s ease;
    }

    svg {
      display: block;

      @include break($md-break-max) {
        height: 1rem;
        width: 0.7rem;
      }
    }

    &:hover {
      transform: translate3d(0.5rem, 0, 0);

      span {
        transform: translate3d(0.5rem, 0, 0);
      }
    }
  }
}
