.swiftype {
  font-family: $sans !important;

  // Search Results Page
  .st-ui-container-primary_content {
    font-family: $sans;
  }


  &--results.search-results__results {

    .st-ui-injected-search-summary.st-search-summary {
      font-size: 1.4rem;
      text-transform: uppercase;
      margin-bottom: 2rem;

      @include break($sm-md-break) {
        margin-bottom: 4rem;
      }

      strong {
        font-weight: 400;
      }

      .st-ui-search-summary-query {
        font-weight: 600;
      }
    }

    a.st-ui-result {
      padding: 3rem 3rem 3rem 0;
      display:block;
      position:relative;
      cursor:pointer;
      border-bottom:1px solid #E9EDF2;
      text-align:left;
      text-decoration:none;



      .st-ui-type-heading {
        color: $black !important;
        font-size: 2.4rem !important;
        font-weight: 600 !important;
        margin-bottom: 1.5rem !important;
        white-space: nowrap;
        overflow: hidden;
        -ms-text-overflow: ellipsis;
        text-overflow: ellipsis;
      }

      .st-ui-url {
          font-size: 13px;
      }

      .st-ui-type-detail-bold {
        font-size: inherit !important;
        color: $red;
      }

      .st-ui-type-detail {
        margin-top: 12px;
        font-size: 1.8rem !important;
        color: $black !important;
        em {
          padding: 2px;
          font-size: inherit;
          background: none !important;
          font-style: italic;
          box-shadow: none;
          color: $red;
        }
      }
    }

    .st-ui-pagination.st-search-pagination .st-query-present {
      @extend %pagination_content;
      font-family: $sans;
      margin-top: 5rem !important;

      @include break($sm-md-break) {
        margin-top: 7rem !important;
      }
    }

    .st-ui-pagination-link.st-result-pagination-link {
      @extend %pagination_num_button;
      @extend %pagination_button;
      text-transform: capitalize;

      .st-ui-arrow {
        display: none;
      }
    }

    .st-ui-pagination-link.st-ui-pagination-number-link.st-result-pagination-link {
      @extend %pagination_num;
      background-color: transparent;

      &.active {
        color: $red;
        text-decoration: underline;
      }
    }
  }
}

//autocomplete
.swiftype-widget{
  .autocomplete {
    background-color: #fff;
    display: block;
    list-style-type: none;
    margin: 0;
    padding: 0;
    position: absolute;
    text-align: left;
    overflow: scroll;
    max-height: 650px;
    ul {
      background-color: #fff;
      display: block;
      list-style-type: none;
      margin: 0;
      padding: 0;
      li {
        border-bottom: 1px solid $gray;
        cursor: pointer;
        padding: 10px 8px;
        list-style-type: none;
        background-image: none;
        margin: 0;
        &.active{
          background-color: $gray;
        }
        a.st-ui-result {
          padding-left: 2rem;
          display: block;
          .st-ui-type-heading {
            margin-bottom: 0.8rem;
            font-size: 1.6rem;
            font-weight: 600 ;
            font-family: $sans;
            display: block;
            @include break($sm-break) {
              font-size: 1.6rem;
            }
          }
          .st-ui-type-detail {
            font-size: 1.4rem;
            margin-top: 0;
            color: $black;
            display: block;
            em {
              padding: 2px;
              font-size: inherit;
              background: none;
              font-style: italic;
              box-shadow: none;
              color: $red;
            }
          }
        }
      }
    }
  }
}
