.c-image-slider {
  max-width: 80rem;
  margin: 0 auto;

  .slick-track {
    display: flex;
    align-items: center;
  }

  &__title {
    margin-bottom: 0.6rem;
  }

  &__slides {
    margin: 0 auto;
  }

  &__slide {
    display: block !important;
    margin-right: 5rem;

    &.portrait {
      background-color: #FBFBFB;
    }

    img {
      margin: 0 auto;
      height: auto;
      opacity: 0;
      transition: opacity 0.3s ease;
      max-height: 60rem;

      .slick-current & {
        opacity: 1;
      }
    }
  }

  &__meta {
    padding: 2.6rem 0;
    display: flex;
    justify-content: space-between;
    position: relative;
    margin: 0 auto;
    flex-wrap: wrap;

    @include break($sm-break) {
      flex-wrap: nowrap;
    }

    &-inner{
      transition: opacity 0.4s ease, transform 0.4s ease;
      margin-bottom: 0.5rem;
      &.fades-out {
        opacity: 0;
        transform: translate3d(-10px, 0, 0);
      }
    }
    &-text {
      position: relative;
      transition: transform 0.4s ease;

      @include break($sm-break) {
        padding-left: 7rem;
      }
    }

    &-caption {
      position: relative;
      margin-bottom: 0.5rem;
      flex-grow: 0;
      font-size: 1.6rem;
      font-family: $sans;

      &.no-caption {
        display: none;
      }
      @include break($sm-md-break) {
        font-size: 1.8rem;
      }

      &-line {
        display: none;

        @include break($sm-break) {
          display: block;
          width: 5rem;
          height: 0.2rem;
          position: absolute;
          top: 0.9rem;
          left: 0;
        }

        @include break($sm-md-break) {
          top: 1rem;
        }
      }
    }
    &-credit {
      font-size: 1.2rem;
      text-transform: uppercase;
      color: $gray-dark;
      letter-spacing: 0.05rem;
      margin-bottom: 0.5rem;

      &.no-credits {
        display: none;
      }

      &-sep {
        color: $red;
      }
    }
    &-count {
      font-size: 1.3rem;
    }

    &-arrows {
      width: 100%;
      margin-top: 2rem;
      margin-left: -0.6rem;
      padding: 0;

      @include break($sm-break) {
        flex: 0 0 9rem;
        text-align: right;
        margin-top: 0;
        margin-left: 0;
      }

      @include break($sm-md-break) {
        flex: 0 0 20rem;
      }

      li {
        display: inline-block;
        padding: 1px 7px 2px;
        cursor: pointer;
      }
      button {
        padding: 0;
      }
      svg {
        position: relative;

        @include break($sm-md-break-max) {
          width: 2rem;
          height: 2rem;
        }
      }
    }
  }

  .slick-arrow {
    transition: all 0.5s ease;
    &:before {
      content: "";
    }
  }

  &__meta-arrow-prev:hover {
    transform: translate3d(-3px, 0, 0);
  }

  &__meta-arrow-next:hover {
    transform: translate3d(3px, 0, 0);
  }

  .slick-disabled {
    opacity: 0.2;
    cursor: default;
  }
}
