// Un-Animate
.js.site-no-animation, .data-saver {

  * {
    animation: initial !important;
  }
  .video-header {
    @include no-video-animations;
  }
  .u-title-line,
  .u-title-line--inner {
    opacity: 1 !important;
    transition: none !important;
    transform: none !important;
  }


  .u-fades-in {
    opacity: 1 !important;
    z-index: 1;
    &.u-player-control {
      z-index: 15;
    }
  }

  .u-draws-width {
    width: 100%;
  }

  // Hover states
  .c-title-cta__title:hover svg,
  .u-cta:hover,
  // .u-cta:hover span,
  .u-cta-regular:hover,
  // .u-cta-regular:hover span,
  .u-clip-hover--scale-child-img:hover img,
  .u-clip-hover--scale-child-img--target:hover img,
  .entry-list__entry-title a:hover,
  .entry-list__entry-title:hover,
  .lead a:hover,
  .c-double-callout__link-list a:hover,
  .academic-depts__list a:hover,
  .academic-depts__list .academic-depts__other-button:hover .academic-depts__other-button-text,
  .three-col-callout__title:hover {
    transform: none;
  }

  .u-clip-hover:hover,
  .u-clip-hover--child-img:hover img,
  .u-clip-hover--scale-child-img:hover,
  .u-clip-hover--scale-child-img--target:hover .u-clip-hover--target {
    clip-path: inset(0);
  }

  // Double Callout Links
  .double-callout-image__link:hover .double-callout-image__content,
  .double-callout-image__link:hover .double-callout-image__meta {
    left: 3rem;
  }

  .double-callout-image__link:hover .double-callout-image__content {
    top: 6.0rem;
  }

  .double-callout-image__link:hover .double-callout-image__shader {
    opacity: 1;
  }

  // Quick Links
  .quick-links-h__link:hover {
    padding: 2.0rem 1.8rem;

    @include break($md-break) {
      padding: 3.0rem 1.8rem 4.5rem;
    }
  }

  .quick-links-v a {
    padding-left: 1.8rem;
    padding-right: 1.8rem;
    transition: none;

    &:hover {
      transition: none;
    }

  }

  // Video Embed
  .c-video-embed__play:hover .c-video-embed__poster:before {
    background-color: rgba(0, 0, 0, 0.5);
  }


  // Instagram
  .ig-lockup__entry .ig-lockup__color-bkg.u-fades-in {
    z-index: 0;
  }
  .ig-lockup__entry-link:hover img {
    transform: none;
  }

  .ig-lockup__entry-inner,
  .ig-lockup__entry-inner * {
    transition: none;
  }

  // Calendar Bar
  .calendar-bar__list-item,
  .calendar-bar__header {
    opacity: 1;
  }

  // Guides
  .guide-header__lead,
  .guide-header__scroll-hint,
  .scroll-list__item-number,
  .guide-header__share-inner,
  .guide__content,
  .scroll-list__item-content--small p,
  .scroll-list__item--video-embed .scroll-list__item-graphic,
  .scroll-list__item--video-native .scroll-list__item-graphic,
  .scroll-list__item--image .scroll-list__item-graphic,
  .scroll-list__item--image .scroll-list__item-graphic img {
    transform: none !important;
    opacity: 1 !important;
    transition: none !important;
  }

  .scroll-list__item-content--large:before {
    width: 100%;
    transition: none;
  }

  .guide-header__list-count {
    display: none;
  }


  // Nav
  .site-header__main-nav .submenu,
  .site-header__main-nav .submenu.show-menu {
    transition: none;
    transform: translate3d(0,0,0);
  }

  .site-header__main-nav .submenu__links a,
  .site-header__main-nav .submenu__links a.animate-in,
  .site-header__main-nav .submenu__links a span {
    transition: none !important;
    transform: none;
    opacity: 1;
  }

  .site-header__main-nav .submenu__links a:before,
  .site-header__main-nav .submenu__links a:after {
    display: none;
  }

  .site-options {
    transition: none;
  }


  // Flyout
  .flyout {
    transition: none;

    &--more__nav-social svg:hover,
    &--more__nav-pages-link:hover,
    &--more__nav-pages-link:focus {
      transform: none;
      text-decoration: underline;
    }

    &--search-label {
      transition: none;
    }
  }


  // Site Footer
  .site-footer__links a:hover,
  .site-footer__links a:focus {
    transform: none;
  }


  // Calendar Bar
  &.js .calendar-bar {

    &__list, &__header, &__scroll-indicator, &:after {
      transition: none;
    }

    &__header {
      opacity: 1;
    }

    &__list, &__scroll-indicator, &:after {
      transform: translate3d(0,0,0);
    }

    &__list-item {
      opacity: 1;

      &:before {
        height: 6.6rem;
        transition: none;
      }
    }

  }

}
