:root {
  font-size: 62.5%; // Sets standard to 10px from default 16px
}

* {
  box-sizing: border-box;
}


html,
body {
  min-height: 100%;
  font-size: 62.5%;
}

body {
  background: $white;
  font-family: $sans;
  font-kerning: normal;
  width: 100%;
  -webkit-font-smoothing: antialiased;
  @include style-paragraph;
  padding-top: 5.6rem;
  position: relative;

  @include break($sm-md-break) {
    padding-top: 6.3rem;
  }

  @include break($md-break) {
    padding-top: 7.6rem;
  }

  &.menu-modal-open {
    overflow: hidden;
  }
}




