$trans-hover-time-img-reg: 3s;
$trans-hover-time-img-reg-out: 2s;

$trans-hover-time-img-slow: 4s;
$trans-hover-time-img-slow-out: 2s;

$cubic-ease: cubic-bezier(.5,0,.5,1);
$cubic-title: cubic-bezier(.3, .75, .53, .95);

$clip-path-hover: inset(15px 15px 0 15px);
$trans-clip-hover: 0.3s $cubic-title;

$trans-image-in: transform 1.2s $cubic-title;
$trans-image-out: transform 0.4s $cubic-title;
