.full-width-callout {
  color: $white;
  position: relative;
  overflow: hidden;

  &:before {
    content: '';
    display: block;
    @include cover;
    background: $img-bkg-gradient-rev;
    opacity: 0.5;
    z-index: 1;
  }

  .row {
    min-height: 40.0rem;

    @include break($md-break) {
      min-height: 62.0rem;
    }
  }

  &__content {
    position: absolute;
    bottom: 0;
    z-index: 2;
    padding: 4rem 4rem 4rem 0;

    @include break($xs-break) {
      padding: 4rem 0;
    }

    @include break($sm-break) {
      padding: 6rem 3rem 6rem 0;
    }

    @include break($md-break) {
      padding: 9rem 0;
    }

    blockquote {
      quotes: initial;
    }
  }
    // TODO: Clean up __title if it is truly no longer in use.
  &__title, &__body {
    margin-bottom: 3rem;
    font-size: 2.0rem;
    line-height: 1.25;
    font-weight: 600;
    padding-right: 2rem;
    // hyphens: auto;

    @include break($sm-break) {
      margin-bottom: 4.5rem;
      font-size: 2.4rem;
    }

    @include break($md-break) {
      margin-bottom: 5.5rem;
      font-size: 2.8rem;
      max-width: 50rem;
    }
    // TODO: Clean up --quote if it is truly no longer in use.
    &--quote, blockquote {
      position: relative;
      p {
        // prevent line break between end of blockquote and endquote if a p tag makes its way into the blockquote.
        display: inline;
      }
      &:before {
        content: open-quote;
        position: absolute;
        top: 0.1rem;
        left: -1rem;

        @include break($sm-break) {
          left: -1.2rem;
        }

        @include break($md-break) {
          left: -1.6rem;
        }

      }

      &:after {
        content: close-quote;
      }
    }
  }

  &__footer {
    max-width: 17.5rem;

    @include break($sm-break) {
      max-width: 25.0rem;
    }
  }


}
