.basic-page {

  & + .c-pagination {
    margin-top: -5rem;
    margin-bottom: 6rem;
    position: relative;
    z-index: 2;

    @include break($sm-break) {
      margin-top: -13rem;
      margin-bottom: 15rem;
    }
  }

  &__body {
    position: relative;
    padding: 95px 0 6rem;

    @include break($sm-break) {
      padding: 12rem 0 15rem;
    }
    @include break($sm-md-break) {
      padding: 8rem 0 15rem;
    }

    &.basic-page--no-nav {
      padding-top: 3rem ;

      @include break($sm-break) {
        padding-top: 5rem;
      }
      @include break($sm-md-break) {
        padding-top: 8rem;
      }
    }
  }

  // Full Width Basic Page
  &__fw-wrapper {

    &-title {
      margin-bottom: 3rem;

      @include break($sm-break) {
        margin-bottom: 4.5rem;
      }

      &-line {
        width: 4rem;
        height: 0.2rem;
        display: inline-block;
        position: relative;
        top: -0.9rem;

        @include break($sm-break) {
          width: 6rem;
          height: 0.3rem;
        }

        &-inner {
          display: block;
          width: 100%;
          height: inherit;
          background-color: $red;
        }
      }
    }
  }
}

// Basic Content
// General wrapper for all instances of basic content
// End Page, News, etc.
.basic-content {

  > *:first-child {
    margin-top: 0 !important;
  }
  li {
    a {
      text-decoration: underline;

      &.c-news-archive-list__entry {
        text-decoration: none;
      }
    }

  }
  p {
    // font-family: $serif;
    line-height: 1.6;

    + p {
      padding-top: 3rem;
      &::before {
        content: '';
        white-space: normal;
      }
    }

    sup {
      font-size: 1rem;
      font-family: $sans;
      color: $black;
      text-decoration: none;
      display: inline-block;
      position: relative;
      top: -0.6rem;
    }
  }

  h1 {
    margin-bottom: 3rem;
  }

  h2 {
    margin: 3rem 0 2rem;
  }

  h3 {
    margin: 3rem 0 1.6rem;
  }

  h4 {
    font-size: 1.4rem;
    text-transform: uppercase;
    letter-spacing: 0.5px;
    margin: 3rem 0 1.2rem;

    @include break($sm-break) {
      font-size: 1.8rem;
    }
  }

  h5 {
    font-size: 1.4rem;
    text-transform: uppercase;
    letter-spacing: 0.5px;
    border-top: 2px solid $black;
    padding-top: 1rem;
    margin: 5rem 0 3rem;
  }
  h6 {
    margin: 3rem 0 1rem;
  }
  @include list-basic;

  blockquote {
    // padding: 1.1rem 2.2rem;
    margin: 5rem 5rem 4.5rem;
    border-left: none;
    text-align: justify;
  }

  hr {
    clear: both;
  }
  &__lead-image {
    margin: 2rem 0 3rem;

    &-caption {

      p {
        font-family: $sans;
      }

    }

    .u-caption {
      font-family: $sans;
    }

  }

  .u-caption {
    display: block;
    margin-top: 1.8rem;
  }

  &__lead {
    line-height: 1.45;
    font-size: 2rem;

    @include break($sm-md-break) {
      font-size: 2.4rem;
    }
  }

  &__image {
    margin: 3rem auto;
    overflow: hidden;
    display: table;
    table-layout: fixed;
    width: 100%;

    &--v {
      @include break($sm-md-break) {
        display: flex;
        align-items: flex-end;
        flex-wrap: nowrap;
        margin: 4.4rem 0 5rem;
      }
    }

    &--h {
      // FIXME: non-bem caption fix for horizontal layout (do not like)
      figcaption {
        width: 100%;
        margin: 1rem 0 0 0;
        .u-caption {
          margin: 1rem 0 0 0;
        }
      }

      .news-entry-ftd__article & {

        @extend .news-entry-ftd__image-callout;

        @include break($lg-break) {
          display: flex;
          align-items: flex-end;
          flex-wrap: nowrap;
          margin: 4.4rem 0 5rem;
        }

        figcaption {
          width: 100%;
          @include break($lg-break) {
            width: 22rem;
            margin-left: 4rem;
          }
        }
      }
      // End FIXME: non-bem caption fix for horizontal layout (do not like);
    }


    &-wrap {
      width: 100%;
    }



    img {
      margin: 0 auto;
      @include break($sm-break-max) {
        width: 100%;
      }
    }

    p {
      font-family: $sans;
      font-size: 1.4rem;
      line-height: 1.3;

      @include break($sm-md-break) {
        font-size: 1.6rem;
      }
    }

    &-caption {
      margin-top: 1rem;
      display: table-caption;
      caption-side: bottom;

      @include break($sm-md-break) {
        display: block;
        width: 19rem;
        margin-left: 4rem;
        margin-top: 0;
      }
    }

    .u-caption {

      @include break($sm-md-break-max) {
        margin-top: 1rem;
      }
    }
  }

  &__audio,
  &__video,
  &__slider,
  &__slider-gallery,
  &__embed,
  &__accordions,
  &__stat-block,
  &__person,
  &__people-list,
  &__button,
  &__form,
  &__news-feed,
  &__events-feed,
  &__rss-feed,
  &__multi-col-callout,
  &__lead-callout,
  &__image-callout,
  &__full-width-callout,
  &__feature-callout,
  &__callout-on-image,
  &__link-list,
  &__feature-link-list {
    margin: 3rem 0;

    @include break($sm-md-break) {
      margin: 5rem 0;
    }
  }
  &__custom-markup {}
  &__news-feed {
    .full-width-news {
        margin:0;
    }
  }
  &__events-feed {
    .full-width-events {
        margin:0;
    }
  }
  &__rss-feed {
    .full-width-rss {
        margin:0;
    }
  }
  &__lead-callout {
    .lead {
      margin:0;
    }
  }
  &__image-callout {
    .image-callout {
      margin:0;
    }
    .image-callout__block {
      margin-top: 0;
    }
  }
  &__feature-callout,
  &__feature-link-list {
    .feature-callout {
      margin: 0 auto;
    }
  }
  &__callout-on-image {
    .double-callout-image {
      margin: 0;
    }
  }
  &__video {
    .video-only {
      margin: 0;
    }
  }
  &__person {
    margin-bottom: 5rem;

    @include break($sm-md-break) {
      margin-bottom: 7rem;
    }
  }

  &__table {
    margin: 0 0 3rem;

    @include break($sm-break) {
      margin: 3rem 0 5rem;
    }

    table {
      max-width: 100%;
    }
    ul {
      margin: 0;
      padding: 0;
      li {
        padding: 0;
        line-height: 1.6;
        + li {
          margin: 0;
        }
        &:before {
          content: '';
        }
      }
    }
  }

  table p + p {
    padding-top: 0;
  }

  &__slider {

    .c-image-slider__slides {
      background-color: transparent;
    }

    img {
      max-height: 40rem;

      @include break($sm-break) {
        max-height: 60rem;
      }
    }

    .c-image-slider__meta-caption {
      font-family: $sans;
    }
  }

  &__button {

    @include break($sm-break) {
      margin-bottom: 7rem;
    }
  }


  &__cta {
    display: block;
    margin: 3rem 0;
  }

  // CKEditor Classes
  figure {

    &.image {
      clear: both;
      display: table;
      table-layout: fixed;
      width: 100%;
      border: 0;

      figcaption {
        display: table-caption;
        caption-side: bottom;
        @extend .u-caption;
      }
    }

    &.image-style-side {
      margin: 2.4rem auto;

      @include break($xs-break) {
        float: right;
        max-width: 50%;
        margin: 3rem 0 3rem 3rem;
      }
    }
  }
}
