.quick-links-h {
  margin: 3rem 0;

  @include break($sm-md-break) {
    margin: 6rem 0;
  }

  &__wrapper {
    overflow: hidden;
  }

  &__line {
    width: 100%;
    height: 0.2rem;
    background-color: $red;
    animation-duration: 1.5s !important;
    position: relative;
    z-index: 10;

    &--start-right {
      // height: 0.2rem;
      float: right;
      background-color: $red;
    }
  }

  &__nav {


  }

  &__list {
    @include list-reset;

    width: 100%;

    @include break($sm-md-break) {
      display: flex;
      flex-wrap: wrap;
    }

    &-item {
      border-bottom: 1px solid $gray;
      display: flex;

      &.u-in-view {
        animation: fadesInFromLeft 1s ease 0.3s forwards;
        opacity: 0;

        @for $i from 1 through 10 {
          &:nth-of-type(#{$i}) {
            animation-delay: (0.2s * $i);
          }
        }
      }

      @include break($sm-md-break) {
        flex: 1 1 0;
        border-bottom: 0;
        border-right: 1px solid $gray;

        &.u-in-view {
          animation: fadesInFromTop 1s ease 0.3s forwards;

          @for $i from 1 through 10 {
            &:nth-of-type(#{$i}) {
              animation-delay: (0.2s * $i);
            }
          }
        }
      }

      &:last-child {
        border-right: 0;
      }

    }
  }

  &__link {
    color: $black;
    display: flex;
    padding: 2.0rem 1.8rem;
    transition: all 0.5s ease;
    font-size: 1.6rem;
    width: 100%;

    br {
      display: none;

      @include break($md-break) {
        display: block;
      }
    }

    @include break($md-break) {
      padding: 3.0rem 1.8rem 4.5rem;

    }

    @include break($lg-break) {
      font-size: 1.8rem;
    }

    &:hover {
      background-color: $gray;
      padding: 2.0rem 1.8rem 2.0rem 2.5rem;

      @include break($sm-md-break) {
        padding: 2.4rem 1.8rem 1.6rem;
      }

      @include break($md-break) {
        padding: 4.0rem 1.8rem 3.5rem;
        background-color: $gray;
      }
    }
  }

  span {
    display: block;
  }

  .c-arrow-circle {
    margin-right: 1.8rem;
    flex-grow: 0;
    flex-shrink: 0;
    position: relative;
    top: -2px;
  }

  &__text {
    overflow: hidden;

  }
}
