.campus-directory,
.campus-directory section + section {
  margin-top: 3rem;
  margin-bottom: 3rem;

  @include break($sm-break) {
    margin-top: 5rem;
    margin-bottom: 5rem;
  }
  @include break($md-break) {
    margin-top: 8rem;
    margin-bottom: 8rem;
  }

  .search-results__form {
    &-input {
      font-size: 1.6rem;
      margin-bottom: 0.2rem;
      @include break($sm-break) {
        font-size: 1.8rem;
      }
      @include break($sm-md-break) {
        font-size: 2.6rem;
      }
      @include break($md-break) {
        font-size: 3.6rem;
        margin-bottom: 1.2rem;
      }
    }
  }

  .help-block {
    @extend .sr-only;
  }

}
