.advanced-page-content,
.basic-content {

  // Adjust multiple elements .container-fluid padding
  .acalog-course,
  .full-width-news,
  .full-width-events,
  .quick-links-v,
  .quick-links-h,
  .feature-callout,
  .double-callout-image,
  .full-width-callout,
  .lead,
   {
    .container-fluid {
      padding-left: 2.5rem;
      padding-right: 2.5rem;
    }
  }

  // begin audio pane
  .c-audio-player {
    &__footer p {
      font-family: $sans;
      font-size: 1.5rem;
      color: #4a4a4a;
      font-weight: 400;
    }
  }

  // begin custom markup pane
  .c-custom-markup {
    margin: 0 auto;

    .c-custom-markup__content {
      // TODO: Need to look into how to deal with the "> *:first-child" rule in .basic-content. We want everything from that style except this aggressive style. Maybe break the declarations into mixins since we're using them elsewhere anyways?
      @extend .basic-content;
    }
  }
  // begin full-width-news pane
  .full-width-news{
    .entry-list {
      &.entry-list--large {
        @extend .entry-list--small;
      }
      &.entry-list--news {
        @extend .col-md-12;
        padding-left: 0;
        padding-right: 0;
        margin-left: 0;
      }
      &__cta {
        padding-left: 0;
      }
    }
  }
  // begin full-width-events pane
  .full-width-events {
    .entry-list {
      &.entry-list--events {
        @extend .col-md-12;
        padding-left: 0;
        padding-right: 0;
        margin-left: 0;
      }
    }
    .entry-list__entry-wrapper {
      @include break($sm-break) {
        padding-left: 0;
      }
    }
  }
  // begin photo gallery
  .photo-gallery__wrapper{
      @extend .col-sm-12;
      @extend .col-md-12;
      padding-left: 0;
      padding-right: 0;
      margin-left: 0;
    .photo-gallery__title{
      @extend .u-h3;
      margin-bottom: 0.6rem;
    }
  }
  // begin vertical qyuicklinks
  .quick-links-v {
    .quick-links-v__wrapper {
      @extend .col-sm-12;
      @extend .col-md-12;
      padding-left: 0;
      padding-right: 0;
      margin-left: 0;
    }
  }
  .quick-links-h{
    &__wrapper{
      @extend .col-sm-12;
      padding-left: 0;
      padding-right: 0;
      margin-left: 0;
    }
    &__list{
      display: block;
      flex-wrap: unset;
      &-item {
        flex:initial;
        border-bottom: 1px solid $gray;
        border-right: 0;
      }
    }
  }

  //begin feature-callout
  .feature-callout {
    &--reverse {
      .feature-callout__image {
        img {
          float: none;
        }
      }
    }
    .row {
      flex-direction: column-reverse;
    }
    .feature-callout__line {
      @include break($sm-break) {
        display: block;
        height: 2px;
        width: 6rem;
        flex: 0 1 auto;
        padding-bottom: .9rem;
        position: initial;
      }
    }
    .feature-callout__content {
      @include break($sm-break) {
        display: flex;
        flex-wrap: wrap;
        align-items: baseline;
      }
    }
    .feature-callout__content,
    .feature-callout__image,
    .feature-callout__video {
      @extend .col-sm-12;
      @extend .col-md-12;
      padding-left: 0;
      padding-right: 0;
      margin-left: 0;
    }
    .feature-callout__content {
      .feature-callout__title {
        font-size: 2.2rem;
        font-weight: 600;
        line-height: 1.3125;
        padding-left: 2rem;
      }
    }
    .feature-callout__image,
    .feature-callout__video {
      margin-bottom: 2.5rem;
    }
    .feature-callout__image-callout {
      img {
        margin: 0 auto;
      }
    }
    .feature-callout__text p {
      @extend .feature-callout__text;
      font-family: $sans;
      & + p {
        margin: 0;
      }
    }
  }
  // begin image slider
  .c-image-slider {
      max-width: 80rem;
      @include break($sm-break) {
        max-width: 45rem;
      }
      @include break($lg-break) {
        max-width: 63rem;
      }
      .c-image-slider__meta-arrows {
        padding: 0;

        @include break($sm-break) {
          -webkit-box-flex: 0;
          -webkit-flex: 0 0 9rem;
          -ms-flex: 0 0 9rem;
          flex: 0 0 9rem;
          text-align: right;
          margin-top: 0;
          margin-left: 0;
        }
      }
      .c-image-slider__meta-caption {
        p {
          font-size: 1.6rem;
          font-family: $sans;
        }
      }
  }
  // begin double Callout
  .double-callout-image {
    .row {
      width:100%;
    }
    .double-callout-image__col {
      @extend .col-sm-12;
      padding-left: 0;
      padding-right: 0;
      margin-left: 0;
      font-size: 1.6rem;
      font-family: $sans;
      h2 {
        font-size: 2.8rem;
        font-weight: 600;
        line-height: 1.27;
      }
      p {
        font-family: $sans;
      }
      .u-tag-label {
        font-size: 1.2rem;
        line-height: 1.5;
        font-weight: 600;
        letter-spacing: 1.3px;
        text-transform: uppercase;
      }
      .double-callout-image__meta p {
        font-size: 1.4rem;
        line-height: 1.4;
      }
    }
  }
  // begin image-callout
  .image-callout {
    p {
      font-family: $sans;
    }
    .container-fluid {
      padding-left: 0;
      padding-right: 0;
    }
    &__block {
      order: 3;
      padding: 2.5rem;
      z-index: 2;
      width: 75%;
      margin-left: -65%;
      margin-top: 45%;
      @include break($md-break) {
        margin-top: 0;
      }
      a {
        text-decoration: none;
        transition: all .4s ease;
      }
      &--red {
        .u-cta {
          color: $white;
        }
      }
      &--gray {
        .u-cta {
          color: $red;
        }
      }
      &-footer p {
        font-size: 1.3rem;
        // font-family: $serif;
      }
      &-text--quote {
        @include break($md-break) {
          p:before {
            left: -1rem;
          }
        }
      }
    }
    &__image {
      order: 2;
      width: 90%;
      position: relative;
    }
    &.image-callout--reverse {
      .image-callout__block {
          order: 2;
          padding: 2.5rem;
          z-index: 3;
          width: 75%;
          margin-left: 0;
          margin-top: 45%;
          @include break($md-break) {
            margin-top: 0;
          }
      }
      .image-callout__image {
          order: 3;
          margin-left: -65%;
          width: 90%;
          position: relative;
      }
    }
    blockquote {
      margin: 0;
      text-align: inherit;
    }
  }
  // begin lead
  .lead {
    &__wrapper {
      @extend .col-sm-12;
      padding-left: 0;
      padding-right: 0;
      margin-left: 0;

      p {
        font-size: 1.8rem;
        line-height: 1.8;
        font-family: $sans;
      }
      a {
        font-size: 1.4rem;
        svg {
          height: 1rem;
          width: .7rem;
        }
      }
    }
  }
  // begin full-width callout
  .full-width-callout {
    &__body p {
      margin-bottom: 3rem;
      font-size: 2.0rem;
      line-height: 1.25;
      font-weight: 600;
      padding-right: 2rem;
      font-family: $sans;
    }
    blockquote {
      margin: 0;
      text-align: inherit;
    }
  }
  // begin person detail
  .c-person-detail {
    &__role {
      font-size: 1.2rem;
    }
    &__title,&__subtitle {
      font-size: 1.6rem;
    }
    &__meta, &__links {
      @include list-reset;
    }
  }
  // begin stat-block
  .c-stat-block {
    p {
      font-size: 1.6rem;
    }
  }
  // begin multi-column callout
  .three-col-callout {
    .container-fluid {
      max-width: 45rem;
    }
    .u-tag-label {
      max-width: none;
      position: initial;
      transform: initial;
    }
    &__col {
      @extend .col-sm-12;
      padding-left: 0;
      padding-right: 0;
      margin-left: 0;

      &-inner {
        max-width: 30rem;
      }
      p {
        font-family: $sans;
        &.three-col-callout__subtitle, &.u-tiny-label {
          @extend .u-tiny-label;
        }
      }
    }
    ul.slick-dots {
      @include list-reset;
    }
  }
}
