.photo-gallery {

  &__title {
    margin-bottom: 5rem;
  }

  &__group {
    position: relative;

    & + & {
      margin-top: 5rem;

      @include break($sm-break) {
        margin-top: 11rem;
      }
    }
  }

  &__label {
    margin-bottom: 1.2rem;

    @include break($sm-break) {
      position: absolute;
      top: 0;
      left: 0;
      width: auto;
      transform-origin: top left;
      display: block;
      transform: rotate(-90deg) translate3d(-100%,-2.4rem,0);
      margin-bottom: 0;
    }
  }

  &__list {
    @include list-reset;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    &-item {
      width: 100%;
      margin-bottom: 2.4rem;

      @include break($xs-break) {
        width: 49%;
        margin-bottom: 1.5rem;
      }

      @include break($sm-break) {
        width: 32%;
        margin-bottom: 1.5%;
      }
    }
  }

  &__button {
    padding: 0;
    overflow: hidden;
    display: block;

    img {
      width: 100%;
      transition: 0.3s ease-in-out;
    }

    &:hover {

      img {
        transform: scale(1.05);
        transition: 0.8s ease-in-out;
      }
    }
  }
}

.photo-gallery-modal {
  background-color: rgba(255, 255, 255, 0.9);
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 9999;
  display: none;
  opacity: 0;
  transition: opacity 0.6s ease-in-out;
  overflow: auto;

  &.show-modal {
    display: block;
  }

  &.fades-in {
    opacity: 1;
  }

  &__close {
    display: block;
    position: absolute;
    top: 5rem;
    right: 3rem;
    display: block;
    height: 3rem;
    width: 3rem;

    @include break($sm-break) {
      top: 7rem;
      right: 7rem;
    }

    &:before,
    &:after {
      background-color: $black;
      content: '';
      display: block;
      width: 30px;
      height: 2px;
      background-color: $black;
      position: absolute;
      top: 50%;
      left: 50%;
    }

    &:before {
      transform: translate(-50%, -50%) rotate(45deg);
    }

    &:after {
      transform: translate(-50%, -50%) rotate(-45deg);
    }
  }

  &__inner {
    width: 75%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    box-shadow: 0px 0px 2rem 0px rgba(0,0,0,0.5);
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
    max-width: 70rem;
    // max-height: 80vh;

    &.unfix {
      position: relative;
      top: auto;
      left: auto;
      transform: none;
      margin: 30px auto;
      background-color: transparent;
      // box-shadow: none;
    }

    @include break($sm-md-break) {
      max-width: 90rem;
    }

    .fades-in & {
      opacity: 1;
    }
  }

  &__slider {
    position: relative;

    @include break($sm-break) {
      margin-left: -3rem;
      margin-right: 3rem;
      top: -3rem;

      .unfix & {
        margin-left: 0;
        margin-right: 0;
        top: 0;
      }
    }

    &:before {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      bottom: -3px;
      right: 0;
      background-color: $white;

      @include break($sm-break) {
        left: 3rem;
        right: -3rem;
        top: 3rem;
        bottom: -3.1rem;

        .unfix & {
          left: 0;
          right: 0;
          top: 0;
          bottom: -3px;
        }
      }
    }

    img {
      margin: 0;
    }
  }

  &__content {
    background-color: $white;
    padding: 2.4rem;

    @include break($sm-break) {
      padding: 0 5rem 1rem;

      .unfix & {
        padding: 3rem 5rem 1rem;
      }
    }

    &-inner {
      display: block;
      padding: 0;

      @include break($xs-break) {
        display: flex;
        padding-bottom: 2.4rem;
      }
    }
  }

  &__text {
    margin-bottom: 1rem;
    font-size: 1.4rem;

    @include break($sm-break) {
      font-size: 1.6rem;
    }
  }

  &__credit {
    font-size: 1.2rem;
    text-transform: uppercase;
    color: $gray-dark;
    letter-spacing: 0.05rem;

    &.no-credits {
      display: none;
    }

    &-sep {
      color: $red;
    }
  }

  .c-image-slider__meta-caption-line {

    @include break($sm-break-max) {
      display: none;
    }
  }

}
