/**
 * Imports the basic list style with explicit exceptions
 *
 */
@mixin list-basic {
  ul:not(.c-accordion):not(.contextual-links):not(.news-entry__share-list):not(.c-image-slider__meta-arrows):not(.photo-gallery__list):not(.entry-list__list):not(.quick-links-v__list):not(.quick-links-h__list):not(.c-person-detail__meta):not(.c-person-detail__links):not(.acalog-course__semester-listing):not(.acalog-course__nav-pills):not(.acalog-course__listing):not(.acalog-course-field-list):not(.c-news-archive-list__list):not(.c-pagination__content):not(.c-double-callout__link-list):not(.c-social-icons-block__list):not(.u-list-inline) {
    list-style: none;
    @extend .u-list-basic-ul;
  }
  ol:not(.c-accordion):not(.contextual-links):not(.news-entry__share-list):not(.c-image-slider__meta-arrows):not(.photo-gallery__list):not(.entry-list__list):not(.quick-links-v__list):not(.quick-links-h__list):not(.c-person-detail__meta):not(.c-person-detail__links):not(.acalog-course__semester-listing):not(.acalog-course__nav-pills):not(.acalog-course__listing):not(.acalog-course-field-list):not(.c-news-archive-list__list):not(.c-pagination__content):not(.c-double-callout__link-list):not(.c-social-icons-block__list):not(.u-list-inline)  {
    list-style: none;
    @extend .u-list-basic-ol;
  }
}
