@mixin p_list-style() {
  ul,
  ol {
    margin: 2.4rem 0;

    @include break($xs-break) {
      padding: 0 1.5rem;
    }

    @include break($sm-md-break) {
      margin: 3.5rem 0 5rem;
      padding: 0 5rem 0 2.5rem;
    }

    p {
      font-family: $sans;
    }

    li {
      padding-left: 2.5rem;
      position: relative;
    }

    li + li {
      margin-top: 1.6rem;
    }
  }

  ul li {
      
    &:before {
      content: '';
      color: $red;
      position: absolute;
      top: -0.1rem;
      left: 0;
    }
  }

  ol {
    counter-reset: item;
    
    li {

      &:before {
        display: inline-block;
        content: counter(item) ".";
        counter-increment: item;
        position: absolute;
        top: 0.2rem;
        left: -1.1rem;
        color: $red;
        font-weight: 600;
        width: 2.2rem;
        text-align: center;
        font-size: 90%;
      }
    }
  }
}