//set the height of the navigation header
$header-nav-height-sm-md: 63px;
$header-nav-height-md: 76px;

body {
  &.toolbar{
    padding-top: (30px + $header-nav-height-md) !important;
    .site-header{
      z-index: 480;
    }
    .site-header__drawer{
      z-index: 470;
    }
    .site-header{
      top: 30px;
    }
    .flyout {
      z-index: 499;
      top: (30px + $header-nav-height-sm-md);
      @include break($sm-md-break) {
        top: 30px;
      }
    }
    .site-header__drawer, .site-options{
      @include break($sm-md-break) {
        top: (30px + $header-nav-height-sm-md);
      }

      @include break($md-break) {
        top: (30px + $header-nav-height-md);
      }

    }
  }
  &.toolbar-drawer{
    padding-top: (64px + $header-nav-height-md) !important;
    .site-header{
      top: 64px;
    }
    .flyout {
      top: (64px + $header-nav-height-sm-md);
      @include break($sm-md-break) {
        top: 64px;
      }
    }
    .site-header__drawer, .site-options{
      @include break($sm-md-break) {
        top: (64px + $header-nav-height-sm-md);
      }
      @include break($md-break) {
        top: (64px + $header-nav-height-md);
      }
    }
  }
}
#toolbar {
  z-index: 9999;
}
