.c-video-embed {
  position: relative;
  padding-bottom: 56.25%; /* 16:9 */
  padding-top: 25px;
  height: 0;
  background-color: $black-video;
  width: 100%;
  transition: $trans-clip-hover;
  max-width: 100rem;

  iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  &__poster {
    @include cover;
    background-position: center;
    background-size: cover;
    transition: transform 1s ease;

    &:before {
      content: '';
      @include cover;
      background-color: transparentize($black, 0.5);
      transition: background-color 1s ease;
    }
  }

  &__icon {
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transition: transform 0.8s ease;
  }

  &__play {
    @include cover;
    z-index: 2;
    display: block;
    padding: 0;
    width: 100%;
    overflow: hidden;

    .c-play-icon__circle,
    .c-play-icon__triangle {
      transition: fill 0.8s ease;
      fill: transparent;
    }

    &:hover {
      cursor: pointer;

      .c-video-embed__icon {
        // transform: translate3d(-50%, -60%, 0);
        transition: transform 0.75s ease;
      }

      .c-video-embed__poster {
        // transition: transform 2s ease;

        &:before {
          background-color: transparentize($black, 0.8);
          transition: background-color 2s ease;
        }
      }

      .c-play-icon__circle,
      .c-play-icon__triangle {
        transition: fill 0.75s ease 0.1s;
      }

      .c-play-icon__circle {
        fill: $white;
      }

      .c-play-icon__triangle {
        fill: $red;
      }
    }

    &:active,
    &:focus {
      outline: none;
      border: 0;
    }
  }
}