.u-footnotes {
  margin: 7rem 0 5rem;
  border-top: 2px solid $gray;
  padding: 1.6rem 0 0;
  list-style: none;

  li {
    padding-left: 0;

    &:before {
      display: none;
    }
  }

  p {
    font-family: $sans;
    font-size: 1.2rem;
    line-height: 1.3;
    padding-left: 2rem;
    position: relative;

    @include break($sm-break) {
      font-size: 1.6rem;
    }

    sup {
      color: $black;
      text-decoration: none;
      display: inline-block;
      padding-right: 1rem;
      font-size: 1rem;
      position: absolute;
      top: 0.2rem;
      left: 0;
    }

    & + p {
      padding-top: 0;
    }
  }

  p + p {
    margin-top: 2rem;

    &:before {
      content: none;
    }
  }
}