/**
 * Visually hidden mixin
 * Hides element visibily but allows access to screen readers
 * 
 */
@mixin visually-hidden {
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute !important;
  width: 1px;
  word-wrap: normal !important;
}

@mixin visually-unhidden {
  border: 0;
  clip: auto;
  clip-path: none;
  height: inherit;
  margin: inherit;
  overflow: auto;
  padding: inherit;
  position: static;
  width: inherit;
  word-wrap: normal !important;
}
