// Lists
.u-list-inline {
  list-style: none;
  padding: 0;
  margin: 0;

  li {
    display: inline-block;
  }
}

.u-link-list {
  font-size: 1.6rem;
  line-height: 1.625;
  list-style: none;
  margin: 0;
  padding: 0;

  a {
    display: block;

    &:hover {
      // color: black;
      transform: translate3d(0.5rem,0,0);
    }
  }

  a, svg {
    transition: all 0.4s ease;
  }

  &--icons {

    a {
      position: relative;
      padding-left: 3rem;
    }
  }

  &__icon {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    display: inline-block;
    align-content: center;

    svg {
      display: block;
      fill: $red;
      height: 1.8rem;
      width: auto;
    }
  }
}


.u-list-basic-ul,
.u-list-basic-ol {
  margin: 2.4rem 0;

  @include break($xs-break) {
    padding: 0 1.5rem;
  }

  @include break($sm-md-break) {
    margin: 3.5rem 0 5rem;
    padding: 0 5rem 0 2.5rem;
  }

  p {
    font-family: $sans;
  }

  li {
    padding-left: 2.5rem;
    position: relative;
  }

  li + li {
    margin-top: 1.6rem;
  }
}

.u-list-basic-ul {
  counter-reset: item;

  li {

    &:before {
      content: counter(item, disc);
      // content: '';
      color: $red;
      position: absolute;
      top: -0.1rem;
      left: 0;
    }

    ul {
      margin: 1.2rem 0;

      li{
        &:before {
          content: counter(item, circle);
          // content: '';
          color: $red;
          top: .3rem;
          font-size: 1.4rem;
        }
        li:before{
          content: counter(item, square);
        }
      }

    }
  }
}
.u-list-basic-ol {
  counter-reset: item;

  li {

    &:before {
      display: inline-block;
      content: counter(item) ".";
      counter-increment: item;
      position: absolute;
      top: 0.2rem;
      left: -1.1rem;
      color: $red;
      font-weight: 600;
      width: 2.2rem;
      text-align: center;
      font-size: 90%;
    }
    ol {
      margin: 1.2rem 0;
      counter-reset: item;

      li{
        &:before {
          content: counter(item, lower-alpha) ".";
        }
        li:before{
          content: counter(item, lower-roman) ".";
        }
      }
    }
  }
}
