.calendar-bar {
  overflow: hidden;
  position: relative;


  &:before,
  &:after {
    content: '';
    position: absolute;
  }

  &:before {
    height: 0.1rem;
    background-color: $gray-dark;
    top: 0;
    width: 100%;
    left: 0;
    z-index: 0;
  }

  &:after {
    width: 50%;
    top: 0;
    bottom: 0;
    right: 0;
    background-color: $red;
    z-index: 1;
  }

  &__scroll {
    overflow: auto;

    &::-webkit-scrollbar {
      display: none;
    }
  }

  &__inner {
    display: flex;
    z-index: 2;
    -ms-overflow-style: -ms-autohiding-scrollbar;
    position: relative;

    &::-webkit-scrollbar {
      width: 0;
      background: transparent;
    }
  }

  &__header {
    margin-top: 1px;
    padding: 1.6rem 3rem 1.8rem 2.5rem;

    @include break($xs-break) {
      flex: 0 0 22.5rem;
    }

    @include break($xs-break) {
      padding-left: 5rem;
    }

    @include break($sm-md-break) {
      padding-top: 3rem;
      padding-bottom: 3rem;
    }

    &-title.u-paragraph-default {
      font-weight: 600;
      margin-bottom: 1rem;
      br {
        content: "";
      }
    }
  }

  &__scroll-indicator {
    width: 30px;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(to right, transparentize($red, 1) 0%, transparentize($red, 0) 100%);
    z-index: 2;
    display: block;
  }

  &__list {
    @include list-reset;
    display: flex;
    align-items: center;
    background-color: $red;
    flex: 3 0;
    -ms-overflow-y: hidden;
    -ms-overflow-style: -ms-autohiding-scrollbar;
    position: relative;

    &::-webkit-scrollbar {
      display: none;  // Safari and Chrome
    }

    @include break($sm-break) {
      overflow-y: hidden;
    }


    &-item {
      flex: 1 0;
      color: $white;
      position: relative;
      display: inline-block;
      min-width: 20rem;
      @include break($sm-break) {
        min-width: 25rem;
      }
      @include break($md-break) {
        flex: 0 1 30rem;
      }

      &:first-child {
        display: block;
      }

      & + & {

        &:before {

          // @include break($xs-break) {
            content: '';
            display: block;
            width: 1px;
            background-color: $white;
            height: 6.6rem;
            position: absolute;
            top: 50%;
            left: 0;
            transform: translateY(-50%);
          // }
        }
      }

      p {
        font-size: 1.4rem;
      }
    }
  }

  &__link {
    color: $white;
    display: block;
    align-items: center;
    padding: 2rem;
    transition: all 0.3s $cubic-ease;

    @include break($xs-break) {
      display: flex;
    }

    @include break($sm-md-break) {
      padding: 3rem;
    }

    @include break($md-break) {
      padding: 4rem 3rem;
    }

    &:hover {

      @include break($md-break) {
        transform: translate3d(5px, 0,0);
      }
    }
  }

  &__date {
    flex-shrink: 0;
    padding-right: 1.7rem;
    margin-bottom: 0.5rem;

    @include break($sm-break) {
      float: none;

    }

    span {
      display: block;
      font-weight: 600;
    }
  }

  &__month {
    font-size: 1rem;
    letter-spacing: 0.1rem;
    text-transform: uppercase;

    @include break($xs-sm-break) {
      font-size: 1.2rem;
    }
  }

  &__day {
    font-size: 2.4rem;

    @include break($xs-break) {
      font-size: 2.7rem;
    }
  }

  .swat-central__message {
    color: $white;
    padding: 2rem;

    @include break($sm-md-break) {
      padding: 3rem;
    }

    @include break($md-break) {
      padding: 4rem 3rem;
    }
  }
}


// Animate
.js .calendar-bar {

  &__list,
  &__header,
  &__scroll-indicator,
  &:after {
    transition: transform 0.8s $cubic-ease;
  }

  &__header {
    opacity: 0;
    transition-duration: 1s;
  }

  &__list,
  &__scroll-indicator,
  &:after {
    transform: translate3d(0, -100%,0);
    transition-delay: 0.4s;
  }

  &__list-item {
    opacity: 0;

    &:before {
      transition: height 0.8s $cubic-ease;
      height: 0;
    }

    @for $i from 1 through 10 {
      &:nth-of-type(#{$i}) {
        animation-delay: 0.6s + (0.15s * $i) !important;

        &:before {
          transition-delay: 1.2s + (0.15s * $i);
        }
      }
    }
  }

  // When in view, animate
  &.u-in-view {

    .calendar-bar__header {
      animation: calBarHeader 0.5s $cubic-ease forwards;
    }

    .calendar-bar__list,
    .calendar-bar__scroll-indicator,
    &:after {
      transform: translate3d(0,0,0);
    }

    .calendar-bar__list-item {
      animation: calBarItemDown 1s $cubic-ease forwards;

      &:before {
        height: 6.6rem;
      }
    }
  }

}
