table {
  border-collapse: collapse;
  width: 100%;
  text-align: left;
  margin: 0 0 1.5rem;
  max-width: 100%;
  empty-cells: show;
  border: 0;
  padding: 0;

  @include break($xs-break-max) {
    font-size: 1.4rem;
  }

  @include break($sm-break) {
    margin: 3rem 0;
  }

  & + .u-footnotes {
    margin-top: -1.7rem;
    margin-bottom: 1.5rem;

    @include break($sm-break) {
      margin-top: -3rem;
      margin-bottom: 3rem;
    }
  }

  &,
  & p {
    font-family: $sans !important;
  }

  p + p {
    &:before {
      content: none;
    }
  }

  thead {
    border-bottom: 2px solid $red;

    th {
      font-size: 1.2rem;
      font-weight: 600;
      letter-spacing: 0.05rem;
      text-transform: uppercase;
      line-height: 1.58;
      padding: 1.4rem 1.4rem 1.2rem;
      text-align: left;
    }
  }

  sup {
    font-size: 1rem;
    position: relative;
    top: -0.5rem;
  }

  tbody {

    tr {
      border-bottom: 2px solid $gray;
    }

    td,
    th {
      padding: 2rem 1.4rem 2.5rem;
      text-align: left;
    }
  }

  thead.table__sticky-header {
    border-bottom: 0;
    vertical-align: bottom;

    th {
      position: sticky;
      top: -1px;
      padding: 0;
      background-color: $white;
      vertical-align: bottom;

      span {
        padding: 1.4rem 1.4rem 1.2rem;
        border-bottom: 2px solid $red;
        display: block;
      }
    }
  }
}

table.table {

  &--sort {

    .table--sort__sort {
      display: block;
      position: relative;

      &:hover {
        cursor: pointer;
      }

      &:after,
      &:before {
        content: '';
        display: block;
        position: absolute;
        left: 0;
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 0;
        border-color: transparent;
      }

      &:before {
        top: 24px;
        border-width: 5px 5px 0 5px;
        border-color: $red transparent transparent transparent;
      }

      &:after {
        top: 16px;
        border-width: 0 5px 5px 5px;
        border-color: transparent transparent $red transparent;
      }

      &.desc:before,
      &.asc:before {
        display: none;
      }

      &.desc:after {
        top: 19px;
        border-width: 0 5px 5px 5px;
        border-color: transparent transparent $red transparent;
      }

      &.asc:after {
        top: 19px;
        border-width: 5px 5px 0 5px;
        border-color: $red transparent transparent transparent;
      }
    }

    tbody .table--sort__sort:before,
    tbody .table--sort__sort:after {
      display: none;
    }
  }

  &--zebra {

    thead {
      background-color: $red;
      color: $white;

      a {
        color: $white;
      }
    }

    tbody {

      tr {

        &:nth-child(even) {
          background-color: $gray;
        }
      }
    }

    thead.table__sticky-header {

      th {
        padding: 0;
        background-color: $red;
      }
    }

    .table--sort__sort {

      &:before,
      &:after {
        left: 2px;
      }

      &:before,
      &.asc:after {
        border-color: $white transparent transparent transparent;
      }

      &:after,
      &.desc:after {
        border-color: transparent transparent $white transparent;
      }
    }
  }

  &--text {
    table-layout: fixed;

    caption {
      text-align: left;
      padding: 1.4rem 0;

      h2, h3 {
        font-family: $sans;
        font-weight: 600;
        line-height: 1.5;
        margin: 0 !important;
      }
    }

    thead {
      font-family: $sans;
      font-weight: 600;
      line-height: 1.5;
      border-bottom: none;

      th {
        padding: 1.4rem 0;
        text-transform: none;
        font-size: inherit;
        letter-spacing: 0;

        &:first-child {
          width: 40%;

          @include break($sm-break) {
            width: 25%;
          }
        }
      }
    }

    tbody {
      // font-family: $serif;
      line-height: 1.6;

      tr {
        border-bottom: none;
      }

      th,
      td {
        padding: 0;
      }
    }

    & + & {
      margin-top: -2rem;
    }
  }



}






.tablesaw-stack td .tablesaw-cell-label,
.tablesaw-stack th .tablesaw-cell-label {
  display: none;
}

/* Mobile first styles: Begin with the stacked presentation at narrow widths */

/* Support note IE9+: @media only all */


@include break($sm-break-max) {

  .tablesaw-stack {
    clear: both;
    font-size: 1.5rem;

    thead {
      background-color: $red;
      height: 2px;
      display: block;

      th {
        display: none;
      }
    }

    td,
    th,
    &.table--text tbody td {
      text-align: left;
      display: block;
      padding: 1rem 1.5rem;
    }

    tbody tr:nth-child(2n) {
      background-color: $gray;
    }

    td,
    tbody th {

      .tablesaw-cell-label {
        font-family: $sans;
        display: inline-block;
        padding: 0;
        margin-bottom: 0.5rem;
        font-weight: 400;
        width: 100%;
        font-size: 1rem;
        text-transform: uppercase;
        line-height: 1.5;
        letter-spacing: 0.5px;
        font-weight: 600;
      }
    }

    &.table--text tr {

      .tablesaw-cell-label {
        display: none;
      }

      &:first-child .tablesaw-cell-label {
        display: inline-block;
        font-size: 1.2rem;

        &:empty {
          display: none;
        }
      }
    }

    td + td {
      padding-top: 0;
    }

    tr {
      clear: both;
      display: table-row;
    }

    &.table--sort {

      thead th {

      }
    }
  }
}
