.flyout {
  background-color: $red;
  color: $white;
  position: fixed;
  top: 5.6rem;
  left: 0;
  width: 100%;
  height: calc(100% - 56px);
  z-index: 8888;
  padding: 2rem 0;
  display: none;
  overflow-x: hidden;
  overflow-y: auto;
  opacity: 0;
  transition: opacity 0.4s ease-in-out;

  // *:focus {
  //   border: 2px solid #bada55 !important;
  // }

  @include break($sm-md-break) {
    top: 0;
    padding: 6.0rem 0;
    height: 100%;
  }

  &.modal__open {
    display: block;
    z-index: 9999;
  }

  &.modal__animates-in {
    opacity: 1;
  }

  a {
    color: $white;
  }

  // Desktop Close Button
  &__close {
    display: block;
    position: absolute;
    top: -0.1rem;
    right: 100%;
    padding: 1rem 1rem 1.2rem;
    z-index: 1;
    font-size: 1.6rem;

    &:before {
      content: 'Close Modal';
      color: $red;
    }

    @include break($sm-md-break) {
      top: 50px;
      right: 50px;
      padding: 0;
      width: 30px;
      height: 30px;

      &:before,
      &:after {
        content: '';
        display: block;
        width: 30px;
        height: 2px;
        background-color: $black;
        position: absolute;
        top: 50%;
        left: 50%;

        @include break($sm-md-break) {
          background-color: $white;
        }
      }

      &:before {
        transform: translate(-50%, -50%) rotate(45deg);
      }

      &:after {
        transform: translate(-50%, -50%) rotate(-45deg);
      }
    }
  }

  .slick-dots {

    li.slick-active button {
      background-color: $white;
      border-color: $white;
    }

    button {
      border: 1px solid $gray;
    }
  }
}
