.pane-swat-acalog-pane {
  .basic-content & p {
    font-family: $sans;
  }
}
.acalog-course {
  clear: both;
  &__nav-pills {
    @extend .u-list-inline;
    text-align: center;
    margin:auto;
    li {
      margin: 1.4rem 0 0;
      > a {
        @extend button;
        border-radius: 3px;
        background-color: #B43135;
        font-size: 1.8rem;
        color: #fff;
        font-weight: 900;
        padding: 1.6rem 2.8rem;

        &:hover {

        }

      }
    }
  }

  &__semester-listing {
    border-collapse: collapse;
    width: 100%;
    text-align: left;
    margin: 1.5rem 0;
    max-width: 100%;
    border: 0;
    padding: 0;

    @include break($xs-break-max) {
      font-size: 1.4rem;
    }

    @include break($sm-break) {
      margin: 3rem 0;
    }
  }

  &__panel-heading {
    font-size: 1.2rem;
    font-weight: 600;
    letter-spacing: 0.05rem;
    text-transform: uppercase;
    line-height: 1.58;
    padding: 1.4rem 1.4rem 1.2rem;
    text-align: left;
    background: none $red;
    color: $white;
    a {
      color: $white;
    }
    .basic-content & h3 {
      margin: 0;
      font-size: 1.5rem;
    }
  }

  &__link {
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    line-height: 1.35;
    font-weight: 600;
    text-transform: uppercase;
    font-family: "Larsseit";
    color: #B43135;
    text-decoration: none;
    transition: transform 0.5s ease, -webkit-transform 0.5s ease;
    position: relative;
    width: 100%;
    font-weight: 600;

    &:hover {
      transform: translate3d(5px, 0, 0);
    }

    .fa-angle-right {
      position: absolute;
      top: 0;
      bottom: 0;
      right: 0;
      margin: auto;
      text-align: right;
      @include break($sm-break) {
        display: none !important;
      }
    }
  }

  &__listing, &-field-list {
    @include list-reset;
  }

  &__listing {
    font-size: 1.6rem;
    & > li {
      padding: 1.4rem 1.4rem 1.2rem;


      & > .item-list ul li {
        padding: .7rem 0 0;
      }
      &:nth-child(even) {
        background-color: $gray;
      }
    }
  }
}
