.dept-header-single {
  margin: 3rem auto;
  position: relative;
  z-index: 3;

  @include break($md-break) {
    margin-bottom: -4.4rem;
    margin-top: 0;
  }

  .row {
    flex-direction: column-reverse;

    @include break($sm-break) {
      flex-direction: row;
    }
  }

  &__content {
    position: relative;
    z-index: 1;

    p {
      padding-left: 5rem;
      @include break($sm-md-break) {
        padding-left: 9rem;
      }
      @include break($md-break) {
        padding-left: 16%;
      }
      @include break($xl-break) {
        padding-left: 10%;
      }
    }
  }

  &__title {
    position: relative;
    display: inline-block;
    margin-bottom: 1.8rem;
    padding-left: 5rem;

    @include break($md-break) {
      padding-left: 0;
    }
  }

  &__line {
    display: block;
    width: 4rem;
    height: 0.2rem;
    position: absolute;
    top: 1.4rem;
    left: 0;

    @include break($sm-break) {
      width: 20.0rem;
      right: ((20.0rem + 2rem) * -1);
      left: auto;
      top: 2rem;
    }

    @include break($md-break) {
      display: block;
      width: 21.4rem;
      height: 0.3rem;
      top: 2.4rem;
      right: ((21.4rem + 2rem) * -1);
    }

    &-inner {
      display: block;
      height: inherit;
      width: 100%;
      background-color: $red;

      .js & {
        width: 0;
      }

      .js .no-animation & {
        width: 100%;
      }

      &.u-in-view {
        @include u-draws-width(1s, 0.6s);
      }
    }
  }

  &__image {
    margin-bottom: 3rem;
    min-height: 1px;

    @include break($sm-md-break) {
      margin: -1rem 0 0 0;
      width: inherit;
    }

    @include break($lg-break) {
      margin: -2rem 0 -3rem;
    }
  }
}
