// Colors
// ------

$red: #B43135;
// $red: #CF0409;
// $red: #B23339;
$red-swat: #B43135;
$red-light: #FFF0F1;
$white: #fff;
$black: #000;
$black-video: $black;
$gray: #F2F2F2;
$gray-dark: #4A4A4A;
// $guide-gray: #FAFAFA;
$guide-gray: #F5F4F4;

$green-light: #F0FBE7;
$green-dark: #3D7637;

$blue-light: #E0F1F6;
$blue-dark: #016C8C;

$orange-light: #FFF9EB;
$orange-dark: #975D1C;

$blueberry-light:#F4F5FF;
$blueberry-dark: #0F0052;

$turquoise-light: #E8F4F3;
$turquoise-dark: #006B68;

$pink-light: #F6EBEB;
$pink-dark: #884343;

$charcoal-light: #F7F7F7;
$charcoal-dark: #474747;


// Color list for utility classes
// ------------------------------
// see `src/assets/toolkit/styles/partials/_utility-classes.scss
// Array format: 'class-base': $color-var, where `class-base' would output as
// both `.class-base` and `._class-base`
$color-styles: (
  'white': $white,
  'black': $black,
  'red': $red,
  'red-swat': $red-swat,
  'gray': $gray,
);


// Utility Colors
$img-bkg-gradient: linear-gradient(180deg, rgba(255,255,255,0) 0%, rgba(0,0,0,0.2) 100%), linear-gradient(180deg, rgba(255,255,255,0) 0%, rgba(0,0,0,0.2) 100%),linear-gradient(270deg,rgba(255,255,255,.2) 0%, rgba(0,0,0,.5) 60%, rgba(0,0,0,.45) 100%);

$img-bkg-gradient-rev: linear-gradient(to bottom, rgba(255,255,255,0) 20%,rgba(0,0,0,0.75) 80%), linear-gradient(135deg, rgba(255,255,255,0) 20%,rgba(0,0,0,0.75) 80%);
