.gsc-control-cse {
  font-family: $sans !important;
  border: 0 !important;
  padding: 0 !important;

  .gsc-cursor-page:focus {
    background-color: #FFF0F1 !important;
    outline: 2px dashed $red !important;
    border: 0 !important;
    clip-path: none !important;
    transition: none !important;
  }
  a:focus {
    background-color: #FFF0F1 !important;
    outline: 0 !important;
    border: 2px dashed $red !important;
    clip-path: none !important;
    transition: none !important;
  }
}

.gsc-control-cse .gsc-table-result {
  font-family: $sans !important;
  .gs-image {
    display: inline-block !important;
  }
}

.gsc-above-wrapper-area,
.gsc-above-wrapper-area-container tr{
    border: 0 !important;
}

.gsc-above-wrapper-area {
  padding: 0 !important;
  &-container {
    margin: 0;
  }
}

.gsc-resultsHeader {
  display: none !important;
}

.gsc-result {
  &-info-container {
    display:inline-block;
    padding: 0 !important;
    margin-bottom: 2rem !important;
    font-size: 1.4rem !important;
    text-transform: uppercase !important;

    @include break($md-break) {
      margin-bottom: 4rem !important;
    }

  }
  &-info {
    padding: 0 !important;
    margin-top: 0 !important;
    font-size: 100% !important;
    border-radius: 0 !important;
    border: 0 !important;
    color: #000000 !important;
  }

}
.gsc-webResult.gsc-result {
  padding: 3rem 3rem 3rem 0 !important;
  margin-left: 0 !important;
  margin-right: 0 !important;
  border-bottom: 1px solid #E9EDF2 !important;
  position: relative !important;
  &:before {
    right: 2px;
    top: 50%;
    margin-top: -3px;
    width: 6px;
    height: 6px;
    border-style: solid;
    border-width: 1px 1px 0 0;
    border-color: #C5D1DE;
    -webkit-transform: rotate(45deg);
    -moz-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
    display: block;
    position: absolute;
    content: "";
  }
  &:hover {
    box-shadow: none !important;
    &:before {
      border-color: #8B9BAD;
    }
  }
  .gs-webResult,
  .gs-promotion {
    margin-left: 0 !important;
    .gs-title {
      color: #000 !important;
      font-size: 2.4rem !important;
      font-weight: 600 !important;
      margin-bottom: 0 !important;
      white-space: nowrap !important;
      overflow: hidden !important;
      text-overflow: ellipsis !important;
      height: auto !important;
    }
    a.gs-title {
      max-width: 99% !important;
      display: inline-block !important;
      border-bottom: none !important;
      &:focus {
        background-color: #FFF0F1 !important;
        outline: 0 !important;
        border: 2px dashed $red !important;
        clip-path: none !important;
        transition: none !important;
      }
    }
    a.gs-title, a.gs-title > b {
      font-size: 2.4rem!important;
      font-weight: 600!important;
      margin-bottom: 1.5rem!important;
      white-space: nowrap !important;
      overflow: hidden !important;
      text-overflow: ellipsis !important;
      height: auto !important;
    }
    b {
      color: $red !important;
      font-style: italic !important;
      padding-right: .25rem !important;
    }
    div.gs-visibleUrl {
      color: $red !important;
    }
  }
  .gsc-table-result,
  .gsc-thumbnail-inside,
  .gsc-url-top {
    padding-left: 0 !important;
    padding-right: 0 !important;
    margin-bottom: 0 !important;
  }
  table.gsc-table-result {
    margin: 0 !important;

    tbody tr {
      border-bottom: 0 !important;
      td {
        &.gsc-table-cell-thumbnail {
          padding-bottom: 0 !important;
        }
        &.gsc-table-cell-snippet-close {
          display: inline-block !important;
          padding: 0 !important;
          margin-top: 1.8rem !important;
          .gs-snippet {
            // font-family: $serif !important;
            font-size: 1.8rem !important;
            color: $black !important;
          }
        }
      }
    }
  }

  &.gsc-promotion {
    border-bottom:  1px solid $gray !important;
    background: none !important;
    .gs-promotion-table {
      > tbody:first-of-type  > tr > td {
        padding: 0;
      }
      .gs-snippet {
        font-size: 1.8rem !important;
        color: $black !important;
      }
      .gs-promotion-image-cell {
        padding-bottom: 0 !important;
        a,
        img {
          display: inline-block !important;
        }
      }
      .gs-promotion-text-cell .gs-snippet span,
      .gs-promotion-text-cell-with-image {
        display: inline-block;
        padding: 0 !important;
        margin-top: 1.8rem !important;
      }
    }
    table tbody tr {
      border: 0 !important;
    }
  }

}
.gsc-results .gsc-cursor-box {
  margin-left: 0 !important;
}
.gsc-cursor-box {
  margin-top: 7rem!important;
  margin-bottom: 2rem!important;
  padding: 0 !important;
  font-family: $sans !important;
  text-align: center !important;
  width: 100% !important;

  .gsc-cursor-page {
    padding: .5rem !important;
    margin: 0 .6rem !important;
    display: inline-block !important;
    color: #000 !important;
    font-size: 1.6rem !important;
    transition: all .4s ease;
    text-decoration: none !important;
    &:hover {
      color: $red !important;
    }
    @include break($sm-break) {
      margin: 0 1rem !important;
    }
    @include break($md-break) {
      margin: 0 1.8rem !important;
      font-size: 1.8rem !important;

    }
  }
  .gs-title {
    @extend .st-ui-type-heading;
  }
  .gsc-cursor-current-page {
    color: $red !important;
    text-decoration: underline !important;
    font-weight: normal !important;
  }
}
.gsc-tabsArea {
  margin: 1.2rem 0 !important;
  border-bottom: 1px solid $gray !important;
  .gsc-tabhInactive:focus {
    background-color: #FFF0F1 !important;
    outline: 0 !important;
    border: 2px dashed $red !important;
    clip-path: none !important;
    transition: none !important;
  }
}
.gcsc-find-more-on-google{
  display: none !important;
}
