.image-callout {
  margin: 3rem auto;

  @include break($sm-break) {
    margin: 5rem auto;
  }

  @include break($md-break) {
    margin: 8rem auto;
  }

  &__wrapper {
    display: flex;
    position: relative;
    flex-wrap: wrap;
    align-items: flex-start;
    height: 350px;
    @include break($xs-break) {
      height: 425px;
    }
    @include break($xs-sm-break) {
      height: 500px;
    }
     @include break($sm-break) {
      height: 100%;
    }

    .image-callout--normal & {
      @include break($sm-break-max) {
        padding-left: 0;
      }
    }

    .image-callout--reverse & {
      @include break($sm-break-max) {
        padding-right: 0;
      }
    }

    @include break($sm-break) {
      justify-content: center;
    }

    @include break($md-break) {
      align-items: flex-end;
    }
  }

  &__title {
    order: 1;
    width: 100%;
    padding-left: 2.5rem;
    margin-bottom: .2rem;
    &.u-tag-label {
      font-size: 1.4rem;
    }

    @include break($xs-break) {
      padding-left: 5rem;
    }

    @include break($sm-break) {
      position: absolute;
      top: 0;
      left: -2.4rem;
      padding-left: 0;
      display: inline-block;
      width: auto;
      margin-bottom: 0;
      transform: rotate(-90deg) translate(-100%, 0);
      transform-origin: top left;
    }

    .image-callout--reverse & {
      padding-left: 0;

      @include break($sm-break) {
        left: auto;
        top: 0;
        right: -4.3rem;
        transform: rotate(90deg) translate(100%, 100%);
        transform-origin: top right;
      }

      @media screen and (min-width: 1430px) {
        right: -4.5rem;
      }

    }
  }

  &__block {
    order: 3;
    padding: 2.5rem;
    z-index: 3;
    width: 75%;
    margin-left: -65%;
    margin-top: 45%;

    @include break($sm-break) {
      width: 40.70%;
      margin-left: -11%;
      margin-top: 8rem;
      padding: 3rem;
    }

    @include break($md-break) {
      width: 32.70%;
      padding: 4rem 4.5rem;
      margin-left: -4.830917874%;
      margin-top: 0;
    }

    .image-callout--reverse & {
      order: 2;
      margin-left: 0;

      @include break($sm-break) {
        width: 38.81944444%;
      }

      @include break($md-break) {
        width: 28.81944444%;
      }
    }

    &-header {
      margin-bottom: 2rem;

      @include break($sm-break) {
        margin-bottom: 4.4rem;
      }

      .image-callout__block-line {
        margin-top: 1.5rem;
      }
    }

    &-text {
      font-size: 1.4rem;
      font-weight: 600;
      line-height: 1.3;

      @include break($xs-break) {
        font-size: 1.8rem;
      }

      @include break($sm-break) {
        font-size: 2.4rem;
      }

      @include break($md-break) {
        font-size: 3rem;
      }

      &--quote p {
        position: relative;

        &:before {
          content: '\201C';
          position: absolute;
          top: 0;
          left: -0.7rem;

          @include break($sm-break) {
            left: -1rem;
          }

          @include break($md-break) {
            left: -1.4rem;
          }
        }

        &:after {
          content: '\201D';
        }
      }
    }

    &-line {
      display: block;
      width: 3.3rem;
      height: 0.2rem;

      &-inner {
        display: block;
        height: inherit;
        width: 100%;

        .js & {
          width: 0;
        }
      }
    }


    &-footer {
      margin-top: 2rem;
      display: flex;
      flex-direction: column;

      @include break($sm-break) {
        margin-top: 3.4rem;
      }

      .u-cta {
        margin-bottom: 0.5rem;
        font-size: 1rem;

        @include break($xs-break) {
          font-size: 1.3rem;
        }

        span {
          top: 0.1rem;
        }
      }

      .image-callout__block-line {
        margin-bottom: 1.8rem;
        order: 1;
      }

      p {
        font-size: 1.4rem;
        line-height: 1.3;
        // font-family: $serif;
        @include break($xs-break) {
          font-size: 1.5rem;
        }
      }

      &-link {
        order: 2;
      }

      &-content {
        display: block;
        order: 3;
      }
    }


    // Variations
    &-title {
      order: 2;

      &.u-tag-label {
        font-size: 1.4rem;
      }
    }

    &--red {
      background-color: $red;
      color: $white;

      .u-cta {
        color: $white;

        svg g {
          fill: $white;
        }
      }

      .u-tag-label {
        color: $white;
      }

      .image-callout__block-line-inner {
        background-color: $white;
      }
    }

    // Variations
    &--gray {
      background-color: $gray;
      color: $red;

      .u-cta {
        color: $black;

        svg g {
          fill: $black;
        }
      }

      .u-tag-label {
        color: $black;
      }

      .image-callout__block-line-inner {
        background-color: $black;
      }
    }
  }

  &__image {
    order: 2;
    width: 90%;
    position: relative;

    @include break($sm-break) {
      width: 65%;
    }

    @include break($md-break) {
      margin-bottom: 5rem;
      width: 58%;
    }

    .image-callout--reverse & {
      order: 3;
      margin-left: -65%;

      @include break($sm-break) {
        margin-left: -4.830917874%;
        // width: 62.98611111%;
      }

      @include break($md-break) {
        // width: 52.98611111%;
      }
    }
  }
}
