/**
 * Font Face
 * @param {string} $name - Name for font-family
 * @param {string} $path - Relative path to files from output css sheet
 * @param {string} $weight [normal] - `font-weight`
 * @param {string} $style [normal] - 'font-style'
 * @param {string} $exts [eot woff2 woff ttf svg] - List of file extensions to include, sep with a space
 * @link https://gist.github.com/jonathantneal/d0460e5c2d5d7f9bc5e6
 * @example scss - Basic Usage Sass
 *   @include font-face { 'Circular', '../fonts/circular-book' }
 *   @include font-face { 'Circular', '../fonts/circular-bold', 600 }
 *   @include font-face { 'Circular', '../fonts/circular-book-italic', 400, italic }
 *
 */
@mixin font-face($name, $path, $weight: normal, $style: normal, $exts: eot woff2 woff ttf svg) {

  $src: null;

  $extmods: (
    eot: '?',
    svg: '#' + str-replace($name, ' ', '_')
  );

  $formats: (
    otf: 'opentype',
    ttf: 'truetype'
  );

  @each $ext in $exts {
    $extmod: if(map-has-key($extmods, $ext), $ext + map-get($extmods, $ext), $ext);
    $format: if(map-has-key($formats, $ext), map-get($formats, $ext), $ext);
    $src: append($src, url(quote($path + '.' + $extmod)) format(quote($format)), comma);
  }

  @font-face {
    font-family: quote($name);
    font-style: $style;
    font-weight: $weight;
    font-display: optional;
    src: $src;
  }
}

/**
 * Font Sizes
 */
@mixin style-paragraph {
  font-size: 1.6rem;
  font-weight: 400;
  line-height: 1.2;

  @include break($md-break) {
    font-size: 1.8rem;
  }
}

@mixin style-h1 {
  font-size: 2.8rem;
  font-weight: 600;
  line-height: 1.2;

  @include break($sm-break) {
    font-size: 3.6rem;
  }

  @include break($md-break) {
    font-size: 4.8rem;
  }
}

@mixin style-h2 {
  font-size: 2.8rem;
  font-weight: 600;
  line-height: 1.27;

  @include break($sm-break) {
    font-size: 3.0rem;
  }

  @include break($md-break) {
    font-size: 3.6rem;
  }
}

@mixin style-h3 {
  font-size: 2.2rem;
  font-weight: 600;
  line-height: 1.3125;

  @include break($sm-break) {
    font-size: 2.7rem;
  }

  @include break($md-break) {
    font-size: 3.2rem;
  }
}

@mixin style-h4 {
  font-size: 1.6rem;
  font-weight: 600;
  line-height: 1.5;

  @include break($md-break) {
    font-size: 1.8rem;
  }
}

@mixin style-h5 {
  font-size: 1.6rem;
  font-weight: 600;
  line-height: 1.5;
  text-transform: uppercase;
  letter-spacing: 1px;

  @include break($md-break) {
    font-size: 1.8rem;
  }
}

@mixin style-h6 {
  font-size: 1.2rem;
  font-weight: 600;
  letter-spacing: 0.5px;
  text-transform: uppercase;

  @include break($sm-break) {
    font-size: 1.4rem;
  }
}

@mixin style-cta {
  font-size: 1.2rem;
  line-height: 1.35;
  font-weight: 600;
  text-transform: uppercase;
  font-family: $sans;

  @include break($sm-break) {
    font-size: 1.4rem;
  }
}
