.quick-links-v {
  border-top: 0.2rem solid $gray;
  padding: 4.0rem 0 5.0rem;

  @include break($md-break) {
    border-top: 0.4rem solid $gray;
    padding: 6.0rem 0 7.0rem;
  }
  &__line {
    position: absolute;
    top: 1.2rem;
    left: 0;
    width: 3.0rem;
    height: 0.2rem;

    @include break($sm-break) {
      height: 0.3rem;
      width: 6.0rem;
      right: -7.0rem;
      top: 1.6rem;
    }

    @include break($sm-md-break) {
      top: 2rem;
    }

    &-inner {
      height: inherit;
      width: 100%;
      background-color: $red;
      // top: 2rem;
    }
  }

  &__title {
    margin-bottom: 3.0rem;
    position: relative;
    display: inline-block;
    position: relative;
    padding-left: 4rem;

    @include break($sm-break) {
      margin-bottom: 4.3rem;
      padding-left: 8rem;
    }
  }

  &__list {
    ul {
      @include list-reset;
    }
  }

  &__list-item {
    @include list-reset;
    display: block;

    &:last-child a {
      border-bottom: 1px solid $gray;
    }
  }

  &__wrapper.col-start-fw {
    a {
      color: $red;
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      padding: 2.0rem 0.8rem;
      transition: all 0.5s ease;
      border-top: 1px solid $gray;
      flex: 1 0 2rem;

      @include break($md-break) {
        padding: 3.0rem 0.8rem;
      }

      &:hover {
        background-color: $gray;
        padding-left: 1.8rem;
        padding-right: 1.8rem;
        svg {
          margin-left: 0;
        }
      }
      svg {

        transition: all 0.5s ease;
        width: 1.8rem;
        flex-grow: 0;
        flex-shrink: 0;
        margin-left: 2rem;
      }
    }
  }

  span {
    display: block;
  }

  .arrow-circle {
    margin-right: 1.8rem;
    flex-grow: 0;
    flex-shrink: 0;
  }

  &__text {
    overflow: hidden;

  }
}
