.flyout--more {

  @include break($sm-break-max) {
    padding-top: 0;
    padding-bottom: 0;
  }

  ul, li {
    @include list-reset;
  }

  &-content {
    overflow: auto;
    height: 100%;
    padding-bottom: 6rem;
    -webkit-overflow-scrolling: touch;

    .sharples-hours,
    .next-septa-trains,
    .next-trico-shuttles
    {
      letter-spacing: 0.07rem;
    }
  }

  &__left,
  &__right {
    width: 50%;
  }

  &__nav-social {
    &--m {
      margin-top: 1.6rem;
      @include break($sm-md-break) {
        display: none;
      }
    }
    &--d {
      display:none;
      @include break($sm-md-break) {
        margin-bottom: 3.7rem;
        display: inherit;
      }
    }

    a {
      display: block;
    }

    svg {
      // fill: $gray-dark;
      fill: $white;
      transition: 250ms ease-in-out;

      @include break($sm-md-break) {
        // fill: $white;

        &:hover {
          transform: scale(.88);
        }
      }
    }
  }

  &__nav-pages {

    li {
      display: block;
    }

    &-link {
      display: block;
      font-size: 2rem;
      line-height: 1.5;
      // color: $gray-dark !important;
      color: $white !important;

      transition: 250ms ease-in-out;

      @include break($sm-md-break) {
        font-size: 3.0rem;
        margin-bottom: 2.2rem;
        line-height: 0.9;
        font-weight: 600;
        // color: $white !important;
        display: inline-block;

        &:hover {
          transform: translate3d(5px, 0, 0);
          background-color: transparent !important;
        }
      }

      @include break($md-break) {
        font-size: 3.6rem;
      }
    }
  }

  &__button {
    display: inline-block;
    background-color: $white;
    color: $black !important;
    // background-color: $red;
    // color: $white !important;
    font-size: 1.4rem;
    font-weight: 500;
    padding: 1.0rem 3.0rem;
    margin-bottom: 2.3rem;
    transition: 250ms ease-in-out;

    // @include break($sm-md-break) {
    //   background-color: $white;
    //   color: $black !important;
    // }

    span {
      padding-left: 0.2rem;
    }

    svg {
      height: 1rem;
      width: 1rem;
      fill: $black;
      transition: 250ms ease-in-out;

      // @include break($sm-md-break) {
      //   fill: $black;
      // }
    }

    &:hover {

      @include break($sm-md-break) {
        color: $red !important;

        svg {
          fill: $red;
        }
      }
    }
  }

  &__content {
    margin-bottom: 2.4rem;
    font-size: 1.6rem;
    color: $white;

    @include break($sm-md-break) {
      margin-bottom: 4.0rem;
      // color: $white;
    }

    &--times {
      li {
        display: block;
        &:before {
          content: '';
        }
      }
      @include break($sm-md-break) {
         li {
          display: inline-block;
         }
      }
    }

    p:before {
      display: none;
    }

    &-title {
      font-size: 1.2rem;
      text-transform: uppercase;
      margin-bottom: 1.0rem;
      letter-spacing: 0.1rem;

      @include break($sm-md-break) {
        margin-bottom: 2.0rem;
      }
    }

    span {
      display: none;
    }

    @include break($sm-md-break) {
      br {
        display: none;
      }
      span {
        display: inline;
      }
    }
  }

  &__guides {

    &-inner {
      align-self: flex-end;
      border-top: 1px solid $white;
    }

    &-title {
      display: block;
      color: $white !important;
      margin-bottom: 2rem;

      @include break($sm-md-break) {
        border-top: 1px solid $white;
        padding-top: 2rem;
        margin-bottom: 0;
      }
    }
  }

  &__dots {
    text-align: center;
    position: fixed;
    bottom: 0;
    left: 0;
    background-color: $red;
    width: 100%;
    z-index: 9999;
    line-height: 5rem;
  }


}

// MAIN NAVIGATION
.flyout__main-nav {
  width: 100%;
  margin-bottom: 3rem;

  @include break($sm-md-break) {
    display: none;
  }

  a {
    display: block;
    position: relative;
    padding: 1.8rem 0;
    font-size: 2.0rem;
    font-weight: 500;
    margin-right: 4rem;
  }

  ul {
    list-style: none;
    display: block;
    margin: 0;
    padding: 0;

    .submenu {
      display: none;

      &.is-shown {
        display: block;
      }
    }
  }

  > ul {

    > li {
      // border-bottom: 1px solid $gray;
      border-bottom: 1px solid rgba(255, 255, 255, .5);

      position: relative;

      button {
        height: 6.3rem;
        width: 4.0rem;
        position: absolute;
        right: 0;
        top: 0;
        padding: 0;
        text-align: center;
        fill: $white;
        z-index: 999;

        &.open-submenu {
          svg { transform: rotate(180deg); }
        }
      }

      li {

        &:last-child {
          margin-bottom: 2.0rem;
        }

        a {
          font-size: 1.6rem;
          padding: 0.3rem 0 0.3rem 2.0rem;
          font-weight: 400;
          line-height: 1.43;
        }
      }
    }

  }

  ul.submenu {
    height: 0;
    overflow: hidden;
  }
}

.flyout--mobile-carousel {
  margin-right: -2rem;
  margin-left: -2rem;
  width: calc(100% + 4rem);

  @include break($sm-md-break) {
    margin-right: auto;
    margin-left: auto;
    width: 100%;
  }

  &--slide {
    padding-top: 2rem;


    &--nav {
      padding-top: 0.2rem;
    }

    @include break($sm-break) {
      padding-top: 0;
      padding-left: 2.5rem;
      padding-right: 2.5rem;
    }
  }

  > .slick-list > .slick-track > .slick-slide {
    margin-right: 2rem;
    margin-left: 2rem;
  }
}
