.dept-header-no-image {
  color: $black;

  &__content {
    margin-top: 3.5rem;
    padding-bottom: 3.5rem;
    max-width: 30.0rem;

    @include break($sm-break) {
      margin-top: 8rem;
      max-width: 37.5rem;
      margin-left: 4.5rem;
    }

    @include break($sm-md-break) {
      padding-bottom: 0;
      margin-left: 8.5%;
    }

    @include break($md-break) {
      max-width: 47.5rem;
      margin-left: 11.5%;
    }

    @include break($lg-break) {
      margin-left: 10.5%;
    }
  }

  &__subtitle {
    margin-top: 1.6rem;
    margin-bottom: 1rem;
  }
}
