.additional-content {
  .header-layout--full &.page-layout--fw {
      background-color: $white;
      margin-top: 8rem;
      padding-top: 3.5rem;

      > section:first-of-type {
        margin-top: 0;
      }

      @include break($sm-break) {
        margin-top: 0;
      }

      @include break($md-break) {
        padding-top:10rem;
      }
  }
  .header-layout--standard-left &.page-layout--fw,
  .header-layout--standard-right &.page-layout--fw {

      > section:first-of-type {
        margin-top: 0;

        @include break($sm-break) {
          margin-top: 6rem;
        }
        @include break($md-break) {
          margin-top: 11rem;
        }
      }
  }

  // TODO: Refactor Garnet theme so we can get rid of this.
  .u-color-theme--garnet.header-layout--standard-left &.page-layout--fw,
  .u-color-theme--garnet.header-layout--standard-right &.page-layout--fw {
     > section:first-of-type {
      @include break($md-break) {
        margin-top: 8.8rem;
      }
    }
  }

  // begin custom markup pane
  .c-custom-markup {
    // TODO: determine whether we need to extend styles and include mixins for this display or make use of alternate tpl in the theme. Currently extending .container-fluid to allow for basic display.
    @extend .container-fluid;
    .c-custom-markup__content {
    }
  }
}
