/**
 * Text, background, and fill color classes
 * See $color-styles in ./src/toolkit/settings/_settings.colors.scss
 * 
 */
@each $name, $value in $color-styles {
  .#{$name} { color: $value; }
  ._#{$name} { background-color: $value; }
  .fill-#{$name} { fill: $value; }
  .stroke-#{$name} { stroke: $value; }
}