.ig-lockup {
  margin-top: 6.6rem;
  margin-bottom: 6.6rem;

  &__wrapper {
    position: relative;
  }

  &__color-bkg {
    display: none;

    // @supports (display: grid) {

      @include break($sm-break) {
        display: block;
        position: absolute;
        left: 8.333333%;
        right: 8.333333%;
        top: 2.5rem;
        bottom: 2.5rem;
        // height: calc(100% + 2.5rem);
      }
    // }
  }

  &__list {

    @include break($sm-break) {
      position: relative;
      display: flex;
      flex-direction: row;
      flex-flow: wrap;
      align-items: center;
    }
  }

  &__label {
    margin-bottom: 1.2rem;
    
    @include break($xs-break) {
      margin-bottom: 1.4rem;
      padding-bottom: .5rem;
    }
    @include break($sm-break) {
      margin-bottom: 0;
      padding-bottom: 0;
      display: inline-block;
      position: absolute;
      left: calc(8.333333% + 8rem);
    }
  }

  &__social-links {
    margin-top: 3rem;

    @include break($xs-break) {
      clear: both;
    }
    @include break($sm-break) {  
      display: inline-block;
      clear: unset;
      position: absolute;
      bottom: 6rem;
      left: calc(8.333333% + 4rem);
    }
    a svg {
      fill: $red;
    }
  }


  &__entry {
    margin-bottom: 3rem;
    overflow: hidden;

    @include break($xs-break) {
      margin: 0 1.5% 3rem;
      width: 47%;
      float: left;
    }

    @include break($sm-break) {
      float: unset;
      width: 30.3333%;
    }

    &-link {
      position: relative;
      overflow: hidden;
      display: flex;
      flex-direction: column-reverse;
      color: $black;

      @include break($sm-md-break) {
        color: $white;
        margin: 0 1.5%;
      }

      &:hover,
      &:focus {

        @include break($sm-break) {
          color: $red;
        }

        @include break($sm-md-break) {
          color: $white;
        }

        .ig-lockup__entry-inner {
          opacity: 1;

          * {
            transform: translateY(0);
          }
        }

        img {

          @include break($sm-md-break) {
            transform: scale(1.1);
          }
        }
      }

      img {
        transition: transform 0.8s $cubic-ease;
        position: relative;
        z-index: 0;
        backface-visibility: hidden;
        display: block;
        margin-bottom: 1.5rem;
        width: 100%;
        min-height: 1px;

        @include break($sm-md-break) {
          margin-bottom: 0;
        }
      }
    }

    &-inner {
      // display: none;

      @include break($sm-md-break-max) {
        font-size: 1.2rem;
      }

      @include break($sm-md-break) {
        @include cover;
        display: flex;
        align-items: flex-end;
        // color: $white;
        padding: 1.4rem 2rem;
        transition: opacity 0.5s ease-in-out;
        opacity: 0;
        background: transparentize($black, 0.4);
        z-index: 2;
      }

      * {

        @include break($sm-md-break) {
          position: relative;
          z-index: 1;
          transform: translateY(10rem);
          transition: transform 0.5s ease-in-out 0.2s;
          font-size: 1.4rem;
        }

        @include break($xl-break) {
          font-size: 1.6rem;
        }
      }
    }

    &--standard {
      display: none;
      @include break($xs-break) {
        display: block;
      }
      .ie-11 & {
        @include break($xs-break) {
          display: block !important;
          float: left !important;
          width: 47%;
          flex: unset;
        }
      }
    }

    &--lead {
      @include break($sm-break) {
        width: 50%;
        margin: 0 4rem 0 0;
        flex: 1;
        
        .ig-lockup__entry-link {
          margin: 4rem 0 14rem;
        }
      }
    }
    &--right {
      @include break($sm-break) {
        display: flex;
        flex-direction: column;
        flex: 1.5;
      }
      .ie-11 & {
        @include break($xs-break) {
          display: block !important;
        }
      }

      &-top {
        @include break($sm-break) {
          flex-direction: row;
          display: flex;
          margin-bottom: 2rem; 
        }
      }
      &-bottom {
        @include break($sm-break) {
          flex-direction: row;
          display: flex;
          margin-top: 2rem; 
        }
      }
    }
    &--top-left {
      @include break($sm-break) {
        width: 40%;
        margin: 0;
        margin-right: 2rem;
      }
    }

    &--top-right {
      @include break($xs-break) {
        clear: left;
      }
      @include break($sm-break) {
        display: flex;
        clear: unset;
        flex-direction: column;
        width: 33.3333%;
        margin: 0;
        margin-left: 2rem;
        justify-content: flex-end;
      }
    }

    &--bottom-left {
      @include break($sm-break) { 
        display: flex;
        flex-direction: column;
        width: 50%;
        margin: 0;
        margin-right: 2rem;
        justify-content: end;
      }
    }

    &--bottom-right {
      @include break($xs-break) {
        display: none;
      }
      @include break($sm-break) {
        display: block;
        width: 40%;
        margin: 0;
        margin-left: 2rem;
      }
    }
  }

  .slick-slider {
    margin: 0 -2rem;
  }

  .slick-slide {
    margin: 0 2rem;
  }

  // list overrides
  ul {
    &.ig-lockup__list,
    &.slick-dots {
      padding: 0;
    }
  }
  &__slider {
    display: block;
    @include break($xs-break) {
      display: none;
    }
  }
}
