.u-cta {
  @include style-cta;
  color: $red;
  text-decoration: none;
  letter-spacing: 0.12rem;
  transition: transform 0.5s ease;

  &,
  &-regular {
    display: flex;
    align-items: center;

    span {
      display: inline-block;
      margin-left: 0.8rem;
      transition: transform 0.5s ease;
      position: relative;
    }

    svg {
      display: block;
    }

    &:hover {
      transform: translate3d(5px, 0, 0);

      span {
        transform: translate3d(5px, 0, 0);
      }
    }
  }

  &-regular {
    color: $red;
    font-size: 1.4rem;
    line-height: 1.625;
    transition: transform 0.5s ease;

    span {
      margin-left: 0.5rem;
    }

    &--external {

      svg {
        width: 1rem;
        height: 1rem;
        fill: $red;
      }
    }

    &--p {
      font-size: inherit;
    }
  }
}

.u-tel,
.u-nowrap {
  white-space: nowrap;
}
