.entry-list {
  width: 100%;

  &__list {
    @include list-reset;
  }

  &__title {
    border-top: 0.2rem solid $black;
    transition-duration: 0.5s;
    display: flex;
    align-items: center;
    padding: 1rem 0 2.8rem;
    letter-spacing: 0.05rem;

    svg {
      transition: transform 0.5s ease;
    }

    a {
      color: $black;
    }
  }

  &__cta {
    margin-bottom: 2.4rem;
    @include break($sm-break) {
      margin-bottom: 0;
    }
  }

  &__entry-wrapper {

    @include list-reset;

    @include break($sm-break) {
      padding-left: 20%;
      margin-bottom: 2.4rem;

      .entry-list--small & {
        padding-left: 0;
      }
    }
  }

  &__entry + &__entry,
  &__entry-item + &__entry-item {
    padding-top: 2.7rem;
  }

  &__entry {
    clear: left;
    overflow: hidden;
    display: flex;
    flex-direction: column-reverse;
    align-items: flex-start;
    padding-bottom: 2.7rem;

    @include break($md-break) {
      flex-direction: row-reverse;
      align-items: flex-start;
    }

    &-image {
      margin-bottom: 3rem;
      width: 100%;
      flex-shrink: 0;

      @include break($md-break) {
        max-width: 28rem;
        width: 55%;
        margin-bottom: 0;
        margin-right: 4.4rem;
      }
    }

    &-term {
      margin-bottom: 1.0rem;
      display: inline-block;
    }

    &-title {
      line-height: 1.3 !important;
      padding-right: 5px; //Account for width of the hover animation
      color: $black;
      
      @include break($lg-break) {
        font-size: 2.0rem !important;
      }

      a {
        color: $black;
        display: block;

        &:hover {
          color: $red;
          transform: translate3d(5px,0,0);
        }
      }
    }
    h3.entry-list__entry-title {
      margin-top: 0;
    }

    // Event Date
    &-date {
      margin-right: 3.0rem;
      text-align: center;
      margin-top: 0.2rem;

      @include break($sm-break) {
        margin-right: 5.0rem;
      }

      @include break($md-break) {
        margin-right: 8.4rem;
      }

      span {
        display: block;
      }
    }

    &-month {
      font-size: 1.0rem;
      font-weight: 600;
      text-transform: uppercase;
      letter-spacing: 0.5px;
      margin-bottom: 0.2rem;

      @include break($sm-break) {
        font-size: 1.2rem;
      }

      @include break($md-break) {
        font-size: 1.4rem;
      }
    }

    &-day {
      font-size: 2.0rem;
      font-weight: 600;

      @include break($md-break) {
        font-size: 2.4rem;
      }
    }

    &-content {
      overflow: hidden;
    }

    &-meta {
      margin-top: 1.1rem;
      font-size: 1.4rem;
      line-height: 1.4;

      @include break($md-break) {
        font-size: 1.7rem;
      }
    }

  }

  &__content-wrap {
    overflow: hidden;
    display: flex;
    flex-direction: column-reverse;
    width: 100%;
  }

  // News
  &--news .entry-list {

    &__entry-title {
      color: $black;
      transition: 0.5s ease-in-out;

      &:hover {
        color: $red;
        transform: translate3d(5px,0,0);
      }
    }
  }

  // Events
  &--events.entry-list--small .entry-list {

    &__entry {

      @include break($sm-break) {
        flex-direction: column-reverse;
      }

      &-image {
        float: none;
        margin-right: 0;
        max-width: 100%;
        margin-bottom: 2.7rem;
        width: 100%;
      }

      &-content {
        flex: 1 0;
      }
    }

    &__content-wrap {
      flex-direction: row-reverse;
      align-items: flex-start;
    }
  }

  &--large .entry-list {

    &__entry {

      @include break($sm-break) {
        flex-direction: row-reverse;
      }

      &-image {

        @include break($sm-break) {
          width: 50%;
          max-width: 39.0rem;
          margin-right: 4.4rem;
          margin-bottom: 0;
        }

        @include break($md-break) {
          max-width: 41.5rem;
        }
      }
    }

    &__cta {

      @include break($sm-break) {
        padding-left: 20%;
      }
    }
  }

  &--events.entry-list--large .entry-list {

    &__content-wrap {

      @include break($sm-break) {
        display: flex;
        align-items: flex-start;
      }

    }

    &__entry {
      flex-direction: row;
      flex-wrap: wrap;

      @include break($sm-break) {
        flex-wrap: nowrap;
      }

      &-date {
        order: 2;

        @include break($sm-break) {
          order: 1
        }
      }


      &-image {
        order: 1;

        @include break($sm-break) {
          order: 2;
          flex-shrink: 0;
          float: none;
        }
      }

      &-content {
        order: 3;
        flex: 1 0;
      }

    }
  }
  .swat-central__message {
    padding-bottom: 2.7rem;
  }
}


li.entry-list__entry,
li.entry-list__entry-item {
  border-bottom: 1px solid $gray;

  // TODO: Refactor themes, specifically the garnet theme, so that we don't need to do this sort of thing. Themes with non-white backgrounds require $gray-dark borders, default theme requires $gray.
  [class*="u-color-theme--"]:not(.u-color-theme--garnet) &{
    border-color: $gray-dark;
  }

  &:last-child {
    border-bottom: 0;
  }
}

.full-width-events,
.full-width-news {
  margin: 3.0rem auto;

  @include break($sm-break) {
    margin: 5.0rem auto;
  }

  @include break($md-break) {
    margin: 9.0rem auto;
  }
}

.news-entry__related {
  width: 100%;
}
