button::-moz-focus-inner {
  border: 0;
}

.u-player-control {
  z-index: 15;

  &__circle {
    opacity: 0;
    fill: transparent;
    transition: all 0.5s  ease-in-out;
  }

  &__circle-outline {
    // display: none;
    fill: none;
    stroke: $white;
    stroke-width: 2px;
    // stroke-dasharray: 81.681408993335;
    // transition: all 0.5s ease;
  }

  &:hover {

    // .u-player-control__circle-outline {
      // stroke-dasharray: 81.681408993335;
      // stroke-dashoffset: 81.681408993335;
    // }

    .u-player-control__play,
    .u-player-control__pause {
      fill: $red;
    }

    .u-player-control__circle {
      opacity: 1;
      fill: $white;
    }
  }

  &__play,
  &__pause,
  &__no-sound {
    fill: $white;
    transition: fill 0.5s ease-in-out;
  }

  &__play {
    display: none;

    .is-paused & {
      display: block;
    }
  }

  &__pause {
    display: none;

    .is-playing & {
      display: block;
    }
  }
}

.u-btn{
    background-color: $red;
    color: $white;
    display: inline-block;
    font-family: $sans;
    font-size: 1.4rem;
    font-weight: 600;
    padding: 2rem 2.2rem;
    overflow: hidden;

    @include break($md-break) {
      font-size: 1.6rem;
      padding: 2.6rem;
    }

    span {
      transition: 0.3s ease-in-out;
      display: inline-block;
    }

    &-text {

      span {
        margin-left: 5px;
      }
    }

    &:hover {
      color: $white;
      transform: translate3d(0,0,0);

      .u-btn-text {

        &,
        & span {
          transform: translate3d(5px,0,0);
        }
      }
    }

    svg g {
      fill: $white;
    }
  }
