.stat-lockup {
  li {
    list-style: none;
  }
  &__wrapper {
    position: relative;
  }

  &__title {
    padding: .5rem 2.5rem;

    @include break($xs-break) {
      padding: .5rem 5rem;
    }

    @include break($md-break) {
      padding: 0;
      position: absolute;
      top: 0;
      left: 11.5%;
      transform: rotate(270deg) translate(-100%, -2.4rem);
      transform-origin: top left;
      animation-delay: 1.2s !important
    }
  }

  img {
    width: 100%;
  }

  &__back {
    width: 80%;

    @include break($md-break) {
      margin-left: 11.52777778%;
      width: 52.77777778%;
    }
  }

  &__front {
    padding-left: 16%;
    margin-top: 15%;
    position: relative;
    margin-bottom: 3rem;

    @include break($md-break) {
      width: 84%;
      margin-left: 16%;
      margin-top: -12%;
      padding-left: 15%;
      margin-bottom: 0;

      &:after {
        content: '';
        display: block;
        padding-bottom: 5rem;
      }
    }

    @include break($lg-break) {

      &:after {
        // padding-bottom: 18%;
      }
    }
  }

  &__block {
    position: relative;
    z-index: 2;

    @include break($md-break) {
      position: absolute;
    }

    &-one {
      margin: -32% 0 -10% 10%;
      margin-left: 10%;
      width: 80%;
      margin-top: -32%;

      @include break($md-break) {
        top: 0;
        left: 45%;
        margin: 0;
        transform: translate(0, -80%);
      }
    }

    &-two {
      margin-left: -2.5rem;
      margin-left: calc(-20% + 2.5rem);
      width: 85%;
      margin-top: -10%;
      z-index: 3;

      @include break($xs-break) {
        margin-left: -5rem;
        margin-left: calc(-20% + 5rem);
      }

      @include break($sm-break) {
        margin-top: -20%;
      }

      @include break($md-break) {
        left: 0;
        top: 40%;
        margin: 0;
        transform: translate(0,-50%);
      }
    }

    &-three {
      width: 85%;
      padding-top: 1rem;
      margin-top: -rem;
      margin-left: 15%;

      @include break($md-break) {
        padding-top: 0;
        bottom: -3rem;
        left: 45%;
        width: 50%;
        margin: 0;
      }
    }
    blockquote {
      quotes: initial;
      p {
        &:before {
          content: '';
          display: inline-block;
          position: absolute;
          top: 0;
          left: -1rem;

          @include break($sm-break) {
            left: -1.4rem;
          }

          @include break($md-break) {
            left: -1.8rem;
          }
        }

        &:after {
          content: '';
          display: inline-block;
        }
      }
    }
  }
  &__image-two {

    .u-fades-in--from-below--big {

      @include break($sm-break) {
        animation-delay: 0.8s !important;
      }
    }
  }

}
