.c-dept-info {
  font-size: 1.2rem;
  letter-spacing: 0.05rem;
  font-weight: 600;

  @include break($sm-break) {
    font-size: 1.2rem;
    margin-top: 3rem;
  }

  @include break($md-break) {
    max-width: 20.5rem;
  }

  p + p {
    margin-top: 0.5rem;

    &:before {
      content: none;
    }
  }

  &__social {
    @include list-reset;
    margin-top: 2rem;
    flex-wrap:wrap;
    max-width: 20.5rem;
    justify-content: flex-start;

    .c-social-icons-block__icon {
      margin-bottom: .8rem;
      width: 20%;
    }

    .c-social-icons-block__icon + .c-social-icons-block__icon {
      margin-left: 0;
    }

  }
}
