.c-news-archive-list {
  &__list {
    @include list-reset;
  }
  &__entry {
    padding-left: 0;
    display: flex;
    flex-direction: column-reverse;
    padding-top: 2.7rem;
    padding-bottom: 2.7rem;
    border-bottom: 1px solid $gray;

    @include break($xs-sm-break) {
      flex-direction: row-reverse;
      align-items: flex-start;
      justify-content: space-between;
    }

    &-image {
      width: 100%;
      @include break($xs-sm-break) {
        flex: 1 0;
      }
      max-width: 420rem;
      margin-bottom: 3rem;
      min-height: 1px;

      @include break($xs-sm-break) {
        margin-right: 4.4rem;
        width: 55%;
        margin-bottom: 0;
      }

      img {
        margin: 0 auto;
        width: 100%;
      }
    }

    &-content {
      display: flex;
      flex-direction: column-reverse;
      @include break($xs-sm-break) {
        flex: 1 0;
      }
    }

    &-title {
      line-height: 1.3 !important;
      transition: .5s ease-in-out;

      .basic-page &{
        margin:0;
      }

      color: $black;

      &:hover {
        color: $red;
        transform: translate3d(5px,0,0);
      }

      @include break($sm-break) {
        font-size: 2rem !important;
      }
    }

    &-term {
      margin: 0 0 1rem;
    }

    &--full-width {

      .c-news-archive-list__entry-content {
        padding: 50% 3rem 3rem;
        max-height: 25rem;
        height: 25rem;
        position: relative;
        overflow: hidden;

        @include break($sm-break) {
          padding: 50% 4.4rem 4rem;
          max-height: 39rem;
          height: 39rem;
        }

        &-image-wrapper,
        &-image {
          @include cover();
        }

        &-image-wrapper {
          clip-path: inset(0);
          transition: $trans-clip-hover;
        }

        &-image {
          transition: $trans-image-out;

          &:before {
            content: '';
            display: block;
            @include cover();
            background: linear-gradient(180deg,transparent 0,transparent 37%,rgba(0,0,0,.54) 70%,rgba(0,0,0,.54));
          }
        }

        &:hover {
          .c-news-archive-list__entry-content {

            &-image {
              transform: scale(1.05);
              transition: $trans-image-in;
              // transition: 0.8s ease-in-out;
            }
          }
        }
      }

      .c-news-archive-list__entry-term,
      .c-news-archive-list__entry-title {
        color: $white !important;
        position: relative;
        z-index: 1;
      }

      .c-news-archive-list__entry-title {
        font-size: 2.8rem !important;
        font-weight: 600;
        line-height: 1.27;

        @include break($sm-break) {
          font-size: 3.0rem !important;
        }

        @include break($md-break) {
          font-size: 3.6rem !important;
        }
      }

    }
  }
}
