label {
  display: block;
  margin-bottom: 1.1rem;
  font-family: $sans;

  & + .form-radios
  & + .form-checkboxes {
    margin-top: 2rem;
  }
}

select,
textarea,
input {
  width: 100%;
  display: block;
	border: 1px solid #000000;
  border-radius: 4px;
  padding: 1rem;
  font-size: inherit;
  font-family: $sans;
  margin: 0 0 3.0rem;

  @include break($md-break) {
    padding: 1.6rem 2.4rem 1.8rem;
    // font-size: 1.8rem;
  }

  &:focus {
    border-width: 2px;
    outline: none;
  }

  //add the focus outline to radios and checkboxes
  &[type="radio"], &[type="checkbox"] {
    &:focus {
      outline-style: auto;
    }
  }

  &::placeholder {
    color: $gray-dark;
  }
}

select {
  height: 4rem;
  text-indent: 0.2rem;

  @include break($md-break) {
    height: 5.7rem;
    text-indent: 1.6rem;
  }
  option {
    font-size: 1.2rem;
    @include break($md-break) {
      font-size: 1.6rem;
    }
  }
  &[multiple]{
    padding: 1rem 0;
    height: 8rem;
    @include break($sm-break) {
      height: 11rem;
    }
    option {
      padding: .1rem 0 .1rem 1rem;
    }
  }
}

