// USING WEBFORM MODULE MARKUP
.radio,
.checkbox {

  label {
    position: relative;
    padding-left: 3.2rem;
  }

  input {
    position: absolute;
    left: 0;
    top: 2px;
    height: auto;
    width: auto;
    min-height: 0;
  }
}

.form-required {
  color: $red;
}

.form-item {
  margin-bottom: 3.0rem;

  //allows the hiding of form items
  &.hide {
    display: none;
  }

  .form-item {
    margin-bottom: 1rem;
  }
  // override portions of autocomplete from style.css
  #autocomplete {
    li {
      padding: .75rem;
      border-bottom: 1px dotted $gray-dark;
    }
    .autocomplete__list-item.is-selected, .autocomplete__list-item--is-selected,
    li.is_selected,
    .selected {
      background: $red-light;
      color: $black;
    }
  }
}

.form-group, .form-item {
  input + .help-block, input + .description,
  textarea + .help-block, textarea + .description,
  select + .help-block, select + .description,
  .form-textarea-wrapper + .help-block, .form-textarea-wrapper + .description, {
    margin-top: -2.0rem;
  }

  .help-block, .description {
    font-size: 1.6rem;
    color: $gray-dark;
  }

  &.error-processed {
    position: relative;
    padding-right: 3rem;

     &:after {
      position: absolute;
      top: 3.8rem;
      right: -0.75rem;
      content: ' ';
      width:3rem;
      height:3rem;
      background-size: 4rem 4rem;
      background-repeat: no-repeat;
      background-image: url('https://bluecadet.github.io/swarthmore-web/assets/toolkit/images/form-required.svg');
    }

    @include break($sm-break) {
      padding-right: 5rem;
      &:after {
        right: -1rem;
        width:4rem;
        height:4rem;
        background-size: 6rem 6rem;
      }
    }

    @include break($lg-break) {
      &:after {
        top: 4.5rem;
      }
    }

    input,
    textarea,
    select {
      border-color: $red;
      color: $red;
    }

    label {
      color: $red;
    }
  }
}

.form-error,
.basic-content__form .form-error {
  position: relative;
  margin: 1rem 0 2rem;
  padding: 3rem;
  background: $gray;
  color: $red;

  p {
    font-family: $sans;
    font-weight: 600;
    margin-bottom: 2rem;
  }

  ul {
    margin: 0;
    padding: 0;
  }

  li {
    padding-right: 3rem;

    &:before {
      color: $black;
    }
  }
}

.form-error__alert-icon {
  position: absolute;
  display: block;
  height: 2rem;
  width: 2rem;
  top: 3rem;
  right: 3rem;
  z-index: 2;

  @include break($sm-md-break) {
    height: 3rem;
    width: 3rem;
  }

  svg {
    width: 2rem;
    height: 2rem;

    @include break($sm-md-break) {
      width: 3rem;
      height: 3rem;
    }
  }
}

.webform-datepicker {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 3rem;

  select,
  input {
    margin-bottom: 0;
  }

  .form-type-select {
    width: 25%;
    margin-right: 1rem;
    max-width: 13rem;
    text-align: center;

    select {
      text-align: center;
    }
  }

  .form-item {
    margin-bottom: 0;
  }

  .webform-calendar {
    width: 1.8rem;
    height: auto;
    padding: 0;
    border: 0;
    min-height: 0;
  }
}

.webform-component-time {
  .webform-container-inline{
    @extend .webform-datepicker;
    .form-type-select{
      margin-left: 1rem;
      &:first-of-type {
        margin-left: 0;
      }
    }
  }
}

.ui-datepicker-prev, .ui-datepicker-next {
  position: relative;
  span {
    @extend .sr-only;
  }
}

//added arrows to datepicker as icons are not available
.ui-datepicker-prev:before{
  position: absolute;
  top: .7rem;
  left: .5rem;
  cursor: pointer;
  content: "<<";
}
.ui-datepicker-next:before{
  position: absolute;
  top: .7rem;
  right: .5rem;
  cursor: pointer;
  content: ">>";
}

button[type="submit"],
button[type="reset"],
input[type="submit"],
input[type="reset"] {
  width: auto;
	border-radius: 3px;
  font-size: 1.8rem;
  font-weight: 900;
  padding: 1.6rem 2.8rem;
  margin-right: 1rem;
}
button[type="submit"],
input[type="submit"]{
  background-color: $red;
  color: $white;
}
button[type="reset"],
input[type="reset"]{
  background-color: $gray;
  color: $red;
}

fieldset {

  & + & {
    margin-top: 3.4rem;
  }

  legend {
    display: block;
    width: 100%;
    font-weight: 600;
    padding: 1.2rem 0;
    border-bottom: 3px solid $gray;
    margin-bottom: 2.6rem;
  }

  &.collapsible {
    margin-bottom: 3rem;

    legend {
      margin-bottom: 0;
    }
    .fieldset-wrapper {
      background: $gray;
      padding: 3rem;

      .form-item {
          &:first-of-type {
            margin-top: 0;
          }
          &:last-of-type {
            margin-bottom: 0;
          }
      }
    }
  }

}


@function makelongshadow($color, $size) {
  $val: 5px 0 0 $size $color;

  @for $i from 6 through 800 {
    $val: #{$val}, #{$i}px 0 0 $size #{$color};
  }

  @return $val;
}

input[type=range] {
  width: 100%;
  background-color: darken($gray, 10%);
  border: 0;
  border-radius: 0;
  display: flex;
  align-items: center;
  appearance: none;
  background: none;
  cursor: pointer;
  overflow: hidden;


  &:focus {
    outline: none;
  }

  // Webkit
  &::-webkit-slider-thumb {
    -webkit-appearance: none;
    width: 1.4rem;
    height: 1.4rem;
    background: $gray;
    border: 1px solid $gray-dark;
    cursor: pointer;
    border-radius: 50%;
    margin-top: -4px;
    box-shadow: makelongshadow(darken($gray, 10%), -4px);
  }

  &::-webkit-slider-runnable-track {
    cursor: pointer;
    box-shadow: none;
    background: $red;
    border-radius: 0;
    border: 0;
    outline: none;
    content: '';
    pointer-events: none;
    height: 0.6rem;
  }

  &:focus::-webkit-slider-thumb {
    background: lighten($red, 50%);
  }


  // Mozilla
  &::-moz-range-thumb {
    -moz-appearance: none;
    width: 1.4rem;
    height: 1.4rem;
    background: $gray;
    border: 1px solid $gray-dark;
    cursor: pointer;
    border-radius: 50%;
    margin-top: -4px;
    box-shadow: makelongshadow(darken($gray, 10%), -4px);
    box-sizing: border-box;
  }

  &::-moz-range-track {
    cursor: pointer;
    box-shadow: none;
    background: $red;
    border-radius: 0;
    border: 0;
    outline: none;
    content: '';
    pointer-events: none;
    height: 0.6rem;
  }

  &::-moz-focus-outer {
    border: 0;
  }

  // Microsoft
  &::-ms-thumb {
    background: $gray;
    border: 1px solid $gray-dark;
    cursor: pointer;
    border-radius: 50%;
    box-shadow: none;
  }

  &::-ms-track {
    width: 100%;
    cursor: pointer;
    box-shadow: none;
    background-color: darken($gray, 10%);
    border-radius: 0;
    border: 0;
    outline: none;
    color: transparent;
  }

  &::-ms-fill-lower {
    background: $red;
    border-radius: 0;
  }

  &:focus::-ms-fill-lower {
    background: $red;
  }

  &::-ms-fill-upper {
    background: transparent;
    border: 0;
  }

  &::-ms-tooltip {
    display: none;
  }

}
