.c-title-cta {
  font-weight: 600;

  &__line {
    height: 0.2rem;
    display: block;
    background-color: $red;
    animation-delay: 0.8s !important;

    @include break($sm-break) {
      height: 0.3rem;
    }
  }

  &__text,
  &__title {
    color: $white;
    font-size: 1.4rem;
    line-height: 1.3;
    padding: 1.6rem 0 1.9rem;
    transition: 0.35s ease-in-out;

    @include break($sm-break) {
      font-size: 1.5rem;
    }
  }

  &__title {
    color: $white;
    font-size: 1.4rem;
    animation-delay: 1.2s !important;
    display: flex;
    align-items: center;

    @include break($sm-break) {
      font-size: 1.5rem;
    }

    span {
      margin-left: 0.6rem;
      display: inline-block;
      transition: transform 0.3s ease-in-out;
    }

    &:hover {

      span {
        transform: translate3d(0.5rem, 0, 0);
      }
    }
  }
}
