.feature-callout {
  margin: 6.0rem auto;

  .row {
    flex-direction: column-reverse;

    @include break($sm-break) {
      flex-direction: row-reverse;
    }
  }

  &__content {
    display:  flex;
    flex-wrap: wrap;
    align-items: baseline;
    @include break($sm-break) {
      position: relative;
      display: block;
    }
    &-inner {
      margin-top: .6rem;
      @include break($sm-break) {
        flex: 1 1 100%;
      }
    }
    .u-watch {

      @include break($sm-break) {
        animation-delay: 0.4s !important;
      }
    }

    &-mask {
      animation-delay: 0.875s !important;
    }

    &-header {
      display: flex;
      flex: 1 1 75%;
      flex-direction: column-reverse;
    }
  }

  &__line {
    display: block;
    height: 2px;
    width: 6rem;
    flex: 0 1 auto;
    padding-bottom: .9rem;

    @include break($xs-sm-break) {
      // left: -6rem;
    }

    @include break($sm-break) {
      // width: 80%;
      position: absolute;
      top: 50%;
      left: -6rem;
      z-index: 10;
      pointer-events: none;
    }
    @include break($sm-md-break) {
      left: -7rem;
    }
    @include break($md-break) {
      width: 12rem;
      left: -55%;
    }
    @include break($lg-break) {
      // width: 12rem;
      left: -52%;
    }

    &-inner {
      display: block;
      background-color: $red;
      height: 2px;
      width: 100%;
    }
  }

  &__section-label,
  &__title {
    padding-left: 2rem;

    @include break($sm-break) {
      padding-left: 0;
    }
  }

  &__section-label {
    margin-bottom: 0.8rem;

    @include break($sm-break-max) {
      font-size: 1rem !important;
    }

    @include break($sm-break) {
      margin-bottom: 1.5rem;
    }
  }

  &__title {
    // margin-bottom: 0.6rem;
    color: $black;
  }

  &__text {
    font-size: 1.6rem;
    line-height: 1.3;
    color: $black;

    @include list-basic;

    p + p { margin: 1.5rem 0; }

    @include break($sm-break) {
      line-height: 1.5;
    }
  }
  &__text--link-list {
    margin-bottom: 1.4rem;
  }
  p + p:before {
    content: '';
  }

  &__cta {
    margin-top: 1.5rem;
    display: block;

    & + & {
      margin-top: 0;
    }
  }

  &__image {
    min-height: 1px;

    img {
      max-height: 525px;
    }

  }

  &__video,
  &__image {
    margin-bottom: 2.5rem;

    @include break($sm-break) {
      margin-bottom: 0;
    }
  }

  // Reversed Callout
  &--reverse {

    .row {

      @include break($sm-break) {
        flex-direction: row;
      }
    }

    .feature-callout__title,
    .feature-callout__section-label {
      // padding-left: 0;
      // padding-right: 8rem;

      // @include break($sm-break) {
      //   padding-right: 0;
      // }
    }

    .feature-callout__line {
      // right: 0;
      // left: auto;

      @include break($sm-break) {
        right: -6rem;
        left: auto;
      }
      @include break($sm-md-break) {
        right: -7rem;
      }
      @include break($md-break) {
        width: 12rem;
        right: -55%
      }
      @include break($lg-break) {
        // width: 12rem;
        right: -52%;
      }

      &-inner {
        float: right;
      }
    }
    .feature-callout__image {

      img {
        float: right;
      }

    }
  }

  &--center {

    .row {

      @include break($sm-break) {
        flex-direction: row;
      }
    }

    .feature-callout__line {
      display: block;
      height: 2px;
      width: 6rem;


      @include break($sm-break) {
        display: none;
      }
    }
  }
  // Video Callout
  &__video {
    position: relative;
  }
}

