/**
 * No Video Animations Mixin
 * Hides element visibily but allows access to screen readers
 * 
 */

@mixin no-video-animations {
  .video-header__overlay,
  .video-header__title,
  video,
  .video-header__footer,
  .video-header__content,
  .video-header__footer .c-title-cta__arrow {
    opacity: 1 !important;
    transition: none !important;
  }
  .video-header__content {
    padding-bottom: 2rem !important;
    @include break($sm-break) {
      padding-bottom: 6rem !important;
    }
  }
  &.video-header--quote {
    .video-header__content {
      @include break($sm-break) {
        padding-bottom: 3rem !important;
      }
    }
    .video-header__footer {
      &.video-header__attribution {
        opacity: 1 !important;
        transition: 0s !important;
        transform: none !important;
      }
      .c-title-cta {
        &__line {
          background-color: $red;
        }
      }
    }
  }
  .video-header__poster-fallback {
    transform: none !important;
  }
  .video-header__scroll-cta {
    transition: 0s !important;
    transform: none !important;
    @include break($xs-break) {
      position: relative !important;
      visibility: visible !important;
      opacity: 1 !important;
    }
    &-arrow {
      margin: 0 0 0 1rem !important;
    }
    .c-title-cta__btn {
      display: inline-block !important;
      padding: 1.6rem 2rem 1.9rem !important;
      background-color: $red !important;
      &:focus {
        background-color: $red-light !important;
        outline: 2px dashed $red;
      }
    }
  }
  .video-header__inner {
    &:after {
      transform: none !important;
      transition: none !important;
    }
  }
  .video-header__footer {
    width: auto !important;
    transform: none !important;
    &.video-header__attribution {
      opacity: 1 !important;
      transition: none !important;
      transform: translateY(0%) !important;
    }
    .c-title-cta {
      &__arrow {
        transform: none !important;
      }
      &__btn {
        padding: 1.6rem 2rem 1.9rem !important;
        background-color: $red !important;
        &:focus {
          background-color: $red-light !important;
          outline: 2px dashed $red;
        }
      }
      &__line {
        width: 100% !important;
        // background-color: transparent;
      }
      &__title {
        transition: none !important;
      }
    }
  }
}
